import React, { FC, ChangeEvent, Fragment } from "react";
import clsx from "clsx";

import styles from "./style.module.scss";
import Radio, { Props as RadioProps } from "../Radio";

interface Props {
  name: string;
  labels: Array<string | number>;
  values: Array<string | number>;
  topLabel?: string;
  disabled?: boolean;
  onChange: (event: ChangeEvent<HTMLInputElement>) => void;
  value: string | number;
  nestedComponents?: Record<number, JSX.Element>; // { componentPosition: ReactComponent }
  className?: string;
  radioClasses?: RadioProps["classes"];
}

const RadioGroup: FC<Props> = ({
  labels,
  values,
  topLabel,
  disabled,
  onChange,
  value,
  nestedComponents = {},
  name,
  className,
  radioClasses,
}) => {
  const group = values.map((radioValue, index) => {
    return (
      <Fragment key={`${name}_${radioValue}_${index}`}>
        {nestedComponents[index] ? nestedComponents[index] : null}
        <Radio
          name={name}
          value={radioValue}
          onChange={onChange}
          checked={value === radioValue}
          label={labels[index] as string}
          disabled={disabled}
          classes={radioClasses}
        />
      </Fragment>
    );
  });

  const lastNestedComponent = nestedComponents[values.length];
  if (lastNestedComponent) group.push(lastNestedComponent);

  return (
    <div className={clsx(styles.radioGroup, className)}>
      <label className={styles.label}>{topLabel}</label>
      {group}
    </div>
  );
};

export default RadioGroup;
