import React from "react";
import { Link } from "react-router-dom";
import clsx from "clsx";

import { useGetLocale } from "providers";
import { Breadcrumbs } from "components";
import { useLocalStorage, useModalLocation } from "hooks";

import TelegramIcon from "assets/icons/telegram.png";
import TwitterIcon from "assets/icons/twitter.png";
import FaceBookIcon from "assets/icons/facebook.png";

import { localesLanguages } from "config/locales";
import { useLocale } from "providers/Locale";

import ThemeChange from "../ThemeChanger";
import FooterSelect from "../FooterSelect";
import { supportedLanguages } from "..";
import css from "./style.module.scss";

import { IModalParams } from "config/routes";

const linkStyle = { cursor: "pointer" };

const FooterRightBlock = () => {
  const intl = useGetLocale();
  const { pushHistoryTo } = useModalLocation("help");
  const [savedLocale, setSavedLocale] = useLocalStorage("locale", "en");
  const { setLocale } = useLocale();

  const handleChangeLanguage = (value: string | number) => {
    if (typeof value !== "string" || !(value in localesLanguages)) {
      return;
    }

    setSavedLocale(value);
    //@ts-ignore
    setLocale(value);
  };

  const handleChangePath = (name: "page", value: keyof IModalParams) => () => {
    if (name === "page") {
      pushHistoryTo({ search: { name, value } });
    }
  };

  return (
    <div className={css.rightColumn}>
      <div className={clsx(css.flexRow, css.aboutBlock)}>
        <a style={linkStyle} href="https://pasino.com/blog/" target="_blank">
          {intl.formatMessage({ id: "footer.blog" })}
        </a>

        <Link to="/contact-us">
          {intl.formatMessage({ id: "footer.contact" })}
        </Link>

        <a onClick={handleChangePath("page", "help")} style={linkStyle}>
          FAQ
        </a>

        <a
          style={linkStyle}
          href="https://pasinocom.github.io/pasino-verify/index.html"
          target="_blank">
          {intl.formatMessage({
            id: "gameTemplate.statistics.yourBetsTable.betData.verify",
          })}
        </a>
        <Link to="/rules">{intl.formatMessage({ id: "rules.rules" })}</Link>
        <Link to="/dice-api">Dice API</Link>
      </div>
      <div className={css.description}>
        {intl.formatMessage({ id: "footer.description" })}
        <a
          href="https://www.responsiblegambling.org/for-the-public/safer-play/"
          className={css.link}>
          {intl.formatMessage({ id: "footer.url" })}
        </a>
      </div>
      <div className={clsx(css.termsBlock)}>
        <div className={css.termsElements}>
          <div className={clsx(css.flexRow, css.leftColumn)}>
            <ThemeChange className={css.themeChange} height={30} width={30} />

            <FooterSelect
              className={css.select}
              value={savedLocale}
              handleChange={handleChangeLanguage}
              options={supportedLanguages}
            />

            <div className={css.social}>
              <a
                href="https://www.facebook.com/PasinoCom"
                target="_blank"
                rel="noopener noreferrer"
                className={clsx(css.socialIcon)}>
                <img src={FaceBookIcon} alt="Telegram" />
              </a>

              <a
                href="https://twitter.com/PasinoCom"
                target="_blank"
                rel="noopener noreferrer"
                className={clsx(css.socialIcon)}>
                <img src={TwitterIcon} alt="Telegram" />
              </a>

              <a
                href="https://t.me/pasinocom"
                target="_blank"
                rel="noopener noreferrer"
                className={clsx(css.socialIcon)}>
                <img src={TelegramIcon} alt="Telegram" />
              </a>
            </div>
          </div>
        </div>
        <div className={css.termsElements}>
          <Breadcrumbs
            className={css.breadcrumbs}
            elements={[
              <Link className={css.link} to="/terms">
                {intl.formatMessage({ id: "terms" })}
              </Link>,
              <Link className={css.link} to="/privacy">
                {intl.formatMessage({ id: "privacy" })}
              </Link>,
              <a
                className={css.link}
                href="https://www.responsiblegambling.org/for-the-public/safer-play/"
                rel="noreferrer">
                {intl.formatMessage({ id: "gambleAware" })}
              </a>,
            ]}
          />
        </div>
      </div>
    </div>
  );
};

const memoFroze = () => true;

export default React.memo(FooterRightBlock, memoFroze);
