import React, { useRef } from "react";

import { useMessages } from "components/Chat/chatContext";
import { chatWrapperId } from "components/Chat/chatContext/config";

import { Gif, Rain, Text } from "../renderMessages";
import { Message } from "../Messages";
import css from "./style.module.scss";

export const chatComponents = {
  gif: Gif,
  rain: Rain,
  text: Text,
};

export const MessageList = () => {
  const { messages, setIsAutoScrollMode } = useMessages();
  const wrapper = useRef(null);

  return (
    <div className={css.root} id={chatWrapperId} ref={wrapper}>
      <div
        className={css.scrollWrapper}
        onScroll={({ target }) => {
          const node = target as HTMLDivElement;
          const { scrollHeight, scrollTop, offsetHeight } = node;
          const isAutoScrollPosition = scrollHeight - offsetHeight * 1.5;
          const isAuto = isAutoScrollPosition - scrollTop < 0;

          setIsAutoScrollMode(isAuto);
        }}>
        {messages.map(
          (
            {
              profile_picture_url = "",
              level,
              type,
              user_name,
              isHightlight,
              is_moderator,
              streamer,
              ...props
            },
            index,
          ) => {
            const Component = chatComponents[type];

            return (
              <React.Fragment
                key={`messageVirtualWrapper_#${props.message_id}`}>
                <Message
                  index={index}
                  avatar={profile_picture_url || ""}
                  rating={level}
                  type={type}
                  isHightlight={isHightlight}
                  id={props?.message_id}
                  is_moderator={!!is_moderator}
                  is_streamer={!!streamer}
                  user={user_name}
                  //@ts-ignore -- because rain_by only in rain message type
                  rainBy={props?.rain_by}>
                  <Component {...(props as any)} />
                </Message>
              </React.Fragment>
            );
          },
        )}
      </div>
    </div>
  );
};
