import React, { useEffect, useState } from "react";
import { createPortal } from "react-dom";
import clsx from "clsx";

import { useChat } from "providers";
import { useEventListener } from "hooks";

import { useMessages } from "components/Chat/chatContext";

import { chatContentId, translateChatWhenScroll } from "./utils";
import { Messages, Write } from "../../components";
import { Header } from "./components";
import css from "./style.module.scss";
import { createTimeouts } from "utils";

const hideAnimationTime = parseInt(css.hideAnimationTime);
const rootDom = document.getElementById("pop-up");

const { clearTimeouts, pushTimeout } = createTimeouts();
export const Desktop = () => {
  useEventListener("scroll", translateChatWhenScroll);
  const { setDevice, chatFunctions } = useMessages();
  const { isHide } = useChat();
  const [disabled, setDisabled] = useState(isHide);

  useEffect(() => {
    setDevice("desktop");
  }, []);

  useEffect(() => {
    if (isHide)
      pushTimeout(() => {
        setDisabled(true);
      }, hideAnimationTime);
    else {
      setDisabled(false);
      chatFunctions.current.scrollToBottom();
    }

    return () => {
      clearTimeouts();
    };
  }, [isHide]);

  return createPortal(
    <div className={clsx(css.root, isHide && css.hide)}>
      <Header />
      <div
        id={chatContentId}
        className={clsx(css.content, disabled && css.disabled)}>
        <Messages />
        <Write />
      </div>
    </div>,
    rootDom as HTMLDivElement,
  );
};
