import React, { MutableRefObject, useEffect, useRef } from "react";
import { IAudiosRef, IAudioState } from "../types";

export interface ISoundEffectsRefData {
  [key: string]: HTMLAudioElement;
}

interface IUseBackgroundSound {
  audiosRef: MutableRefObject<IAudiosRef>;
  soundSystemState: IAudioState;
}
export const useSoundEffects = ({
  audiosRef,
  soundSystemState,
}: IUseBackgroundSound) => {
  useEffect(() => {
    for (let effect in audiosRef.current.game) {
      if (audiosRef.current.game[effect].audio) {
        audiosRef.current.game[effect].audio!.volume = soundSystemState.volume;
      }
    }
  }, [soundSystemState.volume]);
  useEffect(() => {
    for (let effect in audiosRef.current.game) {
      if (audiosRef.current.game[effect].audio) {
        if (!soundSystemState.isGameEffectsPlay) {
          if (audiosRef.current.game[effect].audio!.currentTime > 0) {
            // audiosRef.current.game[effect].audio!.pause();
            // audiosRef.current.game[effect].audio!.currentTime = 0;
            audiosRef.current.game[effect].audio!.volume = 0;
          }
        } else {
          audiosRef.current.game[effect].audio!.volume =
            soundSystemState.volume; //?
        }
      }
    }
  }, [soundSystemState.isGameEffectsPlay]);
};
