import { useRef, useEffect } from "react";

type HandlerCallBack = (...args: any[]) => void;

export function useInterval(handler: HandlerCallBack, timeout: number) {
  const savedHandler = useRef<HandlerCallBack>();

  useEffect(() => {
    savedHandler.current = handler;
  }, [handler]);

  useEffect(() => {
    const tick = () => {
      if (savedHandler.current) {
        savedHandler.current();
      }
    };

    if (timeout !== null) {
      const id = setInterval(tick, timeout);
      return () => clearInterval(id);
    }
  }, [timeout]);
}
