import React, {
  DetailedHTMLProps,
  FC,
  HTMLAttributes,
  useEffect,
  useRef,
} from "react";
import { getPercentFromValue } from "utils";
import { AnimatedValues } from "./type";

interface Props {
  from: number;
  to: number;
  format?: (value: number) => string;
  duration?: number;

  span?: DetailedHTMLProps<HTMLAttributes<HTMLSpanElement>, HTMLSpanElement>;
}

export const AnimateNumbers: FC<Props> = ({
  from,
  to,
  span,
  duration = 1000,
  format,
}) => {
  const rootSpan = useRef<HTMLSpanElement>(null);

  const animatedValues = useRef<AnimatedValues>({
    intermediateValue: Number(from),
    animationFrames: [],
    firstTime: 0,
  });

  const animate = (now = 0) => {
    if (!rootSpan.current) return;
    const { current: animation } = animatedValues;
    const finishValue = Number(to);

    if (now && !animation.firstTime) animation.firstTime = now;

    const timePassed = now - animation.firstTime;
    const timePassedPercent = getPercentFromValue(timePassed, duration);
    animation.intermediateValue = Math.max(
      from,
      finishValue * (timePassedPercent / 100),
    );

    rootSpan.current.innerText = format
      ? format(animation.intermediateValue)
      : animation.intermediateValue.toFixed(2);

    if (animation.intermediateValue < finishValue) {
      const id = requestAnimationFrame(animate);
      animation.animationFrames.push(id);
    } else {
      rootSpan.current.innerText = format
        ? format(finishValue)
        : finishValue.toFixed(2);
    }
  };

  useEffect(() => {
    if (from === to && rootSpan.current) {
      rootSpan.current.innerText = format ? format(from) : from.toFixed(2);
      return;
    }

    animatedValues.current.intermediateValue = Number(from);
    animatedValues.current.firstTime = 0;

    animate();

    return () => {
      animatedValues.current.animationFrames.forEach((id) =>
        cancelAnimationFrame(id),
      );
      animatedValues.current.animationFrames.length = 0;
    };
  }, [to, rootSpan.current]);

  return <span {...span} ref={rootSpan} />;
};
