import React from "react";

import { Select, useMessages } from "components";
import css from "./style.module.scss";

export const LanguageSelector = () => {
  const { selectedChannel, setSelectedChannel, channels } = useMessages();

  return (
    <Select
      value={selectedChannel}
      isCoinSelect={false}
      options={channels.map(({ flag, name, id }) => ({
        value: name,
        icon: flag,
        label: name,
        name,
      }))}
      onChange={(value) => setSelectedChannel(value as string)}
      openFromUpwards={false}
      showLabel={false}
      className={css.select}
      iconClass={css.selectIcon}
      labelClass={css.selectLabel}
      itemClass={css.selectItem}
      classes={{ expandIcon: css.dropDownIcon }}
    />
  );
};
