import { HTMLFullScreenNode } from "../types";

const requestFullscreenFunctions = [
  "requestFullscreen",
  "mozRequestFullScreen",
  "webkitRequestFullScreen",
  "msRequestFullscreen",
];

export const enableFullscreen = async (
  node?: HTMLFullScreenNode | null,
): Promise<void | never> => {
  if (!node) {
    return;
  }

  const activeFullscreenFunctions = requestFullscreenFunctions.find((value) => {
    const request = value as keyof HTMLFullScreenNode;
    if (node[request]) {
      return request;
    }
  });

  if (activeFullscreenFunctions) {
    //@ts-ignore bacause [typeof activeFullscreenFunctions === string] not [keyof HTMLFullScreenNode]
    return await node[activeFullscreenFunctions]();
  }

  throw {
    message: "the device does not support fullScreen",
  };
};
