import React, {
  useRef,
  forwardRef,
  ChangeEvent,
  useEffect,
  useImperativeHandle,
} from "react";
import clsx from "clsx";

import { useGetLocale } from "providers";
import { RadioGroup, Checkbox } from "components";

import { AutoDecisions, InputProps, Inputs, RefInput } from "./type";
import css from "./style.module.scss";

interface Props {
  title: string;
  value: AutoDecisions;
  onChange: (value: AutoDecisions) => void;

  InputComponent?: RefInput;

  classes?: {
    input?: string;
    inputWrapper?: string;
  };
  withoutMultiplier?: boolean;
}

const DefaultInput = forwardRef<HTMLInputElement, InputProps>((props, ref) => (
  <input {...props} ref={ref} />
));

export const AutoDecision = forwardRef<Inputs, Props>(
  (
    {
      title,
      value,
      onChange,
      classes,
      InputComponent = DefaultInput,
      withoutMultiplier,
    },
    ref,
  ) => {
    const intl = useGetLocale();

    const inputs = useRef<Inputs>({
      cashoutInput: null,
      radioInput: null,
    });

    const radioOnChange = ({ target }: ChangeEvent<HTMLInputElement>) => {
      onChange({
        ...value,
        variant: Number(target.value),
      });
    };

    const handleCheck = (ev: any, is: boolean) => {
      onChange({
        ...value,
        isCashout: is,
      });
    };

    useEffect(() => {
      const { cashoutInput, radioInput } = inputs.current;
      if (cashoutInput) cashoutInput.value = `${value.cashout}`;
      if (radioInput) radioInput.value = `${value.betPercent}`;
    }, [value]);

    useImperativeHandle(ref, () => inputs.current);

    return (
      <div className={css.box}>
        <h1 className={css.title}>{title}</h1>
        <div>
          <fieldset className={css.formBox}>
            <RadioGroup
              onChange={radioOnChange}
              name={`${title}_limbo_radiogroup`.replace(" ", "_")}
              value={value.variant}
              values={[0, 1, 2]}
              labels={[
                intl.formatMessage({ id: "plinko.autoBet.changeBetBy" }),
                intl.formatMessage({
                  id: "plinko.autoBet.resetToBaseBet",
                }),
                intl.formatMessage({ id: "plinko.autoBet.stopBetting" }),
              ]}
              nestedComponents={{
                1: (
                  <div
                    className={clsx(css.inputWrapper, classes?.inputWrapper)}>
                    <InputComponent
                      ref={(node) => (inputs.current.radioInput = node)}
                      className={clsx(css.input, classes?.input)}
                      type="number"
                      onBlur={({ target }) =>
                        onChange({
                          ...value,
                          betPercent: Number(target.value),
                        })
                      }
                    />
                  </div>
                ),
              }}
              radioClasses={{
                label: css.label,
                round: css.radioRound,
                radio: css.radio,
              }}
            />
            {!withoutMultiplier && (
              <div>
                <Checkbox
                  classes={{ label: css.label }}
                  onChange={handleCheck}
                  className={css.checkbox}
                  checked={value.isCashout}
                  name="changeCashout"
                  label={intl.formatMessage({
                    id: "limboInterface.bet.auto.change",
                  })}
                />
                <div className={css.inputWrapper}>
                  <InputComponent
                    ref={(node) => (inputs.current.cashoutInput = node)}
                    className={clsx(css.input, classes?.input)}
                    type="number"
                    onBlur={({ target }) =>
                      onChange({
                        ...value,
                        cashout: Number(target.value),
                      })
                    }
                  />
                </div>
              </div>
            )}
          </fieldset>
        </div>
      </div>
    );
  },
);
