import { CoinData, CoinsPriceData } from "hooks";
import { getData } from "./getData";

export const getAllCoins = async (middleware: (coins: CoinData[]) => void) => {
  const coinsData = await getData<CoinsPriceData>("/coin/get-all");
  if (coinsData?.coins) {
    middleware(coinsData.coins);
  }
};
