import React, { FC, useEffect, useRef, useState } from "react";
import useLocalStorage from "@rehooks/local-storage";

import {
  between,
  chatSocket,
  createHash,
  detectRobots,
  fetchBase,
  isDevelop,
  repeat,
  track,
} from "utils";
import { useDetectRobots, useVisibility } from "hooks";
import { useChat, useGetLocale, useUser } from "providers";

import { User } from "providers/User/Context";
import {
  Channel,
  ChannelsList,
  Device,
  SocketToken,
  UserParams,
} from "./types";
import { Message, RainMessage } from "../types";
import { ChatContextType, Context } from "./Context";
import { getSystemMessage } from "./getSystemMessage";
import { chatWrapperId, localStorageChatName } from "./config";
import { chatPipeFunctions, chatSocketListener } from "./listener";

const adminOptionPattern = /^(\/ban|\/unban|\/remove)\s(\w*)\s?([0-9]{1,})?$/;
const maxMessages = 74;

const getActiveUser = () => {
  let activeUser: User | null = null;

  try {
    const user = localStorage.getItem("user");
    if (user) {
      const parsedUser = JSON.parse(user || "");
      if (parsedUser) {
        activeUser = parsedUser;
      }
    }
  } catch (error) {
    console.log("error", error);
    track()?.errors(error, {
      custom: {
        localStorageUserValue: localStorage.getItem("user") || "null",
      },
    });
  }

  return activeUser;
};

const getIsHightlight = (values: string | string[]): boolean => {
  const activeUser = getActiveUser();
  if (!activeUser) {
    return false;
  }

  if (typeof values === "string") {
    if (values?.search(activeUser.user_name) >= 0) {
      return true;
    }
  } else if (Array.isArray(values)) {
    return !!values.find((user) => user === activeUser.user_name);
  }

  return false;
};

const setDefaultMessages = (): Message[] => {
  const overscanCount = 5,
    messageHeight = 70;

  return (
    JSON.parse(localStorage.getItem(localStorageChatName) || "null") ||
    repeat(
      overscanCount + Math.ceil(window.innerHeight / messageHeight),
      (i) =>
        ({
          type: "text",
          isHightlight: false,
          is_moderator: 0,
          streamer: 0,
          level: 0,
          message: " ",
          message_id: `id_${i}`,
          user_name: "user",
        } as Message),
    )
  );
};

const MessagesProvider: FC = ({ children }) => {
  const { isHide } = useChat();
  const [selectedChannel, setSelectedChannel] = useLocalStorage("channel");
  const [userParams, setUserParams] = useLocalStorage<UserParams>("userParams");
  const intl = useGetLocale();
  const { user } = useUser();
  const { isHidden } = useVisibility();
  const isRobot = useDetectRobots();

  const isChatHidden = useRef(isHide);
  const isInitiatedComplete = useRef(false);

  const chatFunctionsRef = useRef<ChatContextType["chatFunctions"]["current"]>({
    setSystemMessage: () => {},
    scrollToBottom: () => {},
  });

  const [channels, setChannels] = useState<Channel[]>([
    { flag: "", id: "0", name: "English" },
  ]);
  const [messages, setMessages] = useState<Message[]>(() => {
    setTimeout(() => {
      chatFunctionsRef.current.scrollToBottom();
    }, 0);
    return setDefaultMessages();
  });
  const [device, setDevice] = useState<Device>("desktop");
  const [isAutoScrollMode, setIsAutoScrollMode] = useState(true);
  const autoScrollRef = useRef(isAutoScrollMode);
  const audioRef = useRef<HTMLAudioElement | null>(null);

  const createSetIsRainHidden = (messageId: string) => () => {
    setMessages((list) => {
      const id = list.findIndex((msg) => msg?.message_id === messageId);

      if (id !== -1) {
        const message = list[id] as RainMessage;
        message.isHidden = false;
      }

      return [...list];
    });
  };

  const addMessage = (message: Message) => {
    if (message.type === "rain") {
      message.isHidden = true;
      message.setIsHidden = createSetIsRainHidden(message.message_id);

      message.isHightlight = getIsHightlight([
        ...message.winners,
        message.rain_by,
      ]);
    } else if (message.type === "text") {
      message.isHightlight = getIsHightlight(message.message);
    }

    if (message.isHightlight && isChatHidden.current && audioRef.current) {
      if (!(audioRef.current.duration > 0 && !audioRef.current.paused)) {
        const playAudio = async () => {
          try {
            await audioRef.current?.play();
          } catch (error) {
            console.error("audio play error:", error);
          }
        };

        playAudio();
      }
    }

    setMessages((list) => {
      const index = list.findIndex(
        (msg) => message?.message_id === msg?.message_id,
      );

      if (index >= 0) {
        list[index] = message;
      } else {
        list.push(message);
      }

      const listAmount = list.length;

      if (listAmount > maxMessages) {
        list.splice(0, listAmount - maxMessages);
      }

      return [...list];
    });
  };

  const removeMessage = (id: string) => {
    setMessages((list) => list.filter(({ message_id }) => message_id !== id));
  };

  const createMessage = (value: string, type: "text" | "gif") => {
    try {
      const sendValue = {
        type: type === "text" ? "send_message" : "send_gif",
        message: value,
        message_id: createHash(between(6, 8)),
      };

      let user = {
        user_name: "unknown",
        avatar: "",
      };

      try {
        const newUser = JSON.parse(localStorage.getItem("user") || "");
        user = newUser || user;
      } catch (error) {
        if (isDevelop(true)) {
          console.error(
            " >>> Error in get user from localStorage: ",
            localStorage.getItem("user"),
            "\n",
            error,
          );
        }

        track()?.errors(error, {
          custom: {
            " >>> Error in get user from localStorage":
              localStorage.getItem("user") || "null",
          },
        });
      }

      const messageValues = {
        is_moderator: userParams?.chat_moderator || 0,
        level: userParams?.level || 0,
        streamer: userParams?.streamer || 0,
        message_id: sendValue.message_id,
        user_name: user?.user_name || "",
        profile_picture_url: user?.avatar || "",
      };

      if (type === "text") {
        if (userParams?.chat_moderator) {
          const matchedCommand = value.match(adminOptionPattern);

          if (matchedCommand) {
            getSystemMessage(matchedCommand);
            if (matchedCommand[1] === "/remove") {
              removeMessage(matchedCommand[2]);
            }

            return;
          }
        }

        addMessage({
          type: "text",
          message: sendValue.message,
          ...(messageValues as any),
        });
      } else {
        addMessage({
          type: "gif",
          gif_url: value,
          ...(messageValues as any),
        });
      }

      chatSocket.send(sendValue);

      setTimeout(() => {
        chatFunctionsRef.current.scrollToBottom();
      }, 0);
    } catch (error) {
      console.error(`Chat Error: `, error);
      track()?.errors(error);
    }
  };

  useEffect(() => {
    isChatHidden.current = isHide || isHidden;
  }, [isHide]);

  useEffect(() => {
    setTimeout(() => {
      chatFunctionsRef.current.scrollToBottom();
    });
  }, [device]);

  useEffect(() => {
    if (!user?.user_id && localStorage.getItem("token")) {
      return;
    }

    chatPipeFunctions.chatMessageListener = (data) => {
      switch (data.action) {
        case "last_messages":
          const messages = data.messages.map((message, index) => {
            if (message.type === "rain") {
              message.isHidden =
                typeof message.isHidden === "undefined" ? true : false;

              message.setIsHidden = createSetIsRainHidden(message.message_id);
              message.isHightlight = getIsHightlight([
                ...message.winners,
                message.rain_by,
              ]);
            } else if (message.type === "text") {
              message.isHightlight = getIsHightlight(message.message);
            }

            return {
              ...message,
            };
          });

          setMessages((old) => {
            const resize = () => {
              chatFunctionsRef.current.scrollToBottom();
            };

            try {
              const firstIndex = old.findIndex(
                (msg) => msg?.message_id === messages[0]?.message_id,
              );

              if (
                firstIndex === -1 ||
                old.length - firstIndex !== messages.length
              ) {
                resize();
              } else {
                const isFill = messages.find((msg, i) => {
                  const oldMsg: Message | undefined = old[i + firstIndex];
                  const isRightId = msg.message_id === oldMsg.message_id;
                  const isOpenedRain =
                    oldMsg.type === "rain" && !oldMsg.isHidden;

                  if (isOpenedRain && isRightId) {
                    //@ts-ignore -- because if true -- mgs it is RainMessage
                    msg.isHidden = oldMsg.isHidden;
                  }

                  return !isRightId;
                });

                if (!isFill) {
                  const oldMessages = [...old];
                  oldMessages.splice(firstIndex, messages.length);
                  messages.unshift(...oldMessages);
                } else {
                  resize();
                }
              }
            } catch (error) {
              resize();
              track()?.errors(error);
            }

            return messages;
          });
          chatFunctionsRef.current.scrollToBottom();
          localStorage.setItem(
            localStorageChatName,
            JSON.stringify(data.messages),
          );
          break;

        case "set_user_parameters":
          setUserParams({
            chat_moderator: data.chat_moderator,
            level: data.level,
            streamer: data.streamer,
          });
          break;

        case "message_received":
          addMessage(data as unknown as Message);
          break;

        case "server_message":
          if (data.remove_message) {
            removeMessage(data.message_id);
          }

          addMessage({
            type: "text",

            is_moderator: 1,
            streamer: 0,
            level: 70,

            user_name: intl.formatMessage({ id: "chat.serverMessage" }),
            message: data.message,
            message_id: createHash(between(6, 8)),
            isHightlight: false,
            profile_picture_url: data.profile_picture_url || "",
          });
          break;

        case "remove_message":
          removeMessage(data.message_id);
          break;

        default:
          break;
      }
    };

    const initial = async () => {
      const { data } = await fetchBase<SocketToken>(
        "/account/get-socket-token",
      );

      const socketToken = data?.socket_token || "";

      const initValues = [
        {
          channel: selectedChannel,
          type: "get_older_messages",
        },
        {
          type: "initialization",
          socket_token: socketToken,
          channel: selectedChannel,
        },
      ];

      initValues.forEach((value) => {
        chatSocket.send(value);
      });

      isInitiatedComplete.current = true;
    };

    chatSocket.open(initial);
    chatSocket.listener();
    chatSocket.dispatch(chatSocketListener);

    return () => {
      chatSocket.close("change user", true);
    };
  }, [user?.user_id]);

  useEffect(() => {
    if (isInitiatedComplete.current) {
      const changeChannel = [
        { type: "change_channel", channel: selectedChannel },
        {
          channel: selectedChannel,
          type: "get_older_messages",
        },
      ];

      changeChannel.forEach((value) => chatSocket.send(value));
    }
  }, [selectedChannel]);

  useEffect(() => {
    chatFunctionsRef.current.scrollToBottom = () => {
      const wrapper = document.getElementById(
        chatWrapperId,
      ) as HTMLDivElement | null;
      const scrollWrapper = wrapper?.firstChild as HTMLDivElement | null;

      if (!scrollWrapper) return;
      const { scrollHeight } = scrollWrapper;
      scrollWrapper.scrollTo({
        top: scrollHeight,
        behavior: "auto",
      });
    };
    if (!audioRef.current) {
      try {
        const audioElement = new Audio("assets/general/chat_message.mp3"); //Error on midori
        audioRef.current = audioElement;
      } catch (error) {
        console.error("create audio error");
        track()?.errors(error);
      }
    }

    const getChannels = async () => {
      const { data } = await fetchBase<ChannelsList>("chat/get-channels");
      if (data?.channels) {
        setChannels(data.channels);
      }
    };

    getChannels();
  }, []);

  useEffect(() => {
    autoScrollRef.current = isAutoScrollMode;
  }, [isAutoScrollMode]);

  useEffect(() => {
    if (isAutoScrollMode) {
      chatFunctionsRef.current.scrollToBottom();
    }
  }, [messages]);

  useEffect(() => {
    if (!isHidden) {
      chatFunctionsRef.current.scrollToBottom();
    }
  }, [isHidden]);

  return (
    <Context.Provider
      value={{
        selectedChannel: selectedChannel || "English",
        setSelectedChannel,
        createMessage,
        channels,
        messages,
        userParams,
        device,
        setDevice,
        isAutoScrollMode,
        setIsAutoScrollMode,
        chatFunctions: chatFunctionsRef,
        audio: audioRef.current,
      }}>
      {!isRobot ? children : null}
    </Context.Provider>
  );
};

export default MessagesProvider;
