import React, { useState, useRef, useEffect } from "react";
import { useLocation, useHistory } from "react-router";
import SwipeableViews from "react-swipeable-views";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { useVisibility } from "hooks";
import { useWhiteTheme } from "providers";

import Header, { HeaderButtonOption } from "./Header";
import { History, Ranking, YourBets } from "./Tabs";
import AllBets from "./Tabs/AllBets";
import WageringRace from "./Tabs/WageringRace";
import AffiliateBattle from "./Tabs/AffiliateBattle";
import css from "./style.module.scss";

export interface StatisticsProps {
  withFairness?: boolean;
  smallButton?: boolean;
}

const Statistics = ({ withFairness, smallButton }: StatisticsProps) => {
  const intl = useGetLocale();
  const location = useLocation();
  const history = useHistory();
  const { isHidden } = useVisibility();
  const { activeEvent } = useWhiteTheme();

  const [selectedIndex, setSelectedIndex] = useState(0);
  const scrollRef = useRef<any>(null);

  const tabOptions: HeaderButtonOption[] = [
    {
      key: "ranking",
      label: intl.formatMessage({ id: "gameTemplate.statistics.ranking" }),
    },
    {
      key: "history",
      label: intl.formatMessage({ id: "gameTemplate.statistics.chain" }),
    },
    {
      key: "allBets",
      label: intl.formatMessage({ id: "gameTemplate.statistics.allBets" }),
    },
    {
      key: "yourBets",
      label: intl.formatMessage({ id: "gameTemplate.statistics.yourBets" }),
    },
    {
      key: "wageringRace",
      label: intl.formatMessage({ id: "gameTemplate.statistics.wageringRace" }),
    },
    {
      key: "affiliateBattle",
      label: intl.formatMessage({
        id: "gameTemplate.statistics.affiliateBattle",
      }),
    },
  ];

  useEffect(() => {
    const params = new URLSearchParams(location.search);
    if (params.get("link") === "wagering") {
      setSelectedIndex(4);
      window.scrollTo({
        top: scrollRef.current.getBoundingClientRect().top - 40,
        behavior: "smooth",
      });
      const delay = setTimeout(() => {
        params.delete("link");
        history.replace({ ...location, search: params.toString() });
      }, 0);
      return () => clearTimeout(delay);
    }
  });

  return (
    <div ref={scrollRef} className={clsx(activeEvent && css[activeEvent])}>
      <Header
        options={tabOptions}
        selectedIndex={selectedIndex}
        onChange={setSelectedIndex}
        withFairness={withFairness}
        smallButton={smallButton}
      />

      <div className={css.statisticsBlock}>
        {!isHidden && <Tabs selectedIndex={selectedIndex} />}
      </div>
    </div>
  );
};

export default Statistics;

interface TabProps {
  selectedIndex: number;
}

const Tabs = React.memo(({ selectedIndex }: TabProps) => (
  <SwipeableViews disabled disableLazyLoading={false} index={selectedIndex}>
    <Ranking open={selectedIndex === 0} />
    <History open={selectedIndex === 1} />
    <AllBets open={selectedIndex === 2} />
    <YourBets open={selectedIndex === 3} />
    <WageringRace open={selectedIndex === 4} />
    <AffiliateBattle open={selectedIndex === 5} />
  </SwipeableViews>
));
