import React, { FC, useMemo } from "react";
import clsx from "clsx";
import { Link } from "react-router-dom";

import {
  Button,
  ButtonProps,
  LinkWithSearch,
  LinkWithSearchProps,
} from "components";

import css from "./style.module.scss";

type ExtendLinkAndButtonProps = Omit<
  LinkWithSearchProps,
  "search" | "classes"
> &
  Omit<ButtonProps, "classes">;

interface Props extends ExtendLinkAndButtonProps {
  search?: LinkWithSearchProps["search"];
  classes?: {
    selected?: string;
  };
  selected?: boolean;
  asButton?: boolean;
}

const Item: FC<any> = (props) => {
  return (
    <Button
      {...props}
      className={clsx(
        props.className,
        props.selected && props.classes?.selected,
      )}
      waveColor={props.waveColor || "black"}
    />
  );
};

export const MenuItem = React.forwardRef<HTMLElement, Props>(
  (
    { to = "#", search, className, asButton: isButton, children, ...props },
    ref,
  ) => {
    const ForwardedLink = useMemo(
      () =>
        React.forwardRef<HTMLAnchorElement | null, LinkWithSearchProps>(
          ({ ...props }, ref) => <LinkWithSearch {...props} ref={ref} />,
        ),
      [],
    );

    const ItemLink = React.forwardRef<HTMLAnchorElement | null>(
      (props, ref) => {
        return <Link {...props} ref={ref} to={to as any} />;
      },
    );

    if (search) {
      return (
        <Item
          component={isButton ? undefined : ForwardedLink}
          to={to}
          search={search}
          {...props}
          ref={ref}
          className={clsx(className, css.root)}>
          {children}
        </Item>
      );
    }

    return (
      <Item
        ref={ref}
        className={clsx(className, css.root)}
        {...props}
        component={isButton ? undefined : ItemLink}>
        {children}
      </Item>
    );
  },
);

export default MenuItem;
