import React, { FC } from "react";
import clsx from "clsx";

import { ReactComponent as RefreshIcon } from "assets/diamonds/refresh-icon.svg";
import { ReactComponent as LoopIcon } from "assets/diamonds/loop-icon.svg";
import { GetCoinImg, Button, InputAdornment } from "components";
import InputBase, {
  BaseInputProps as InputProps,
} from "components/Input/InputBase";
import { useSassImport } from "hooks";

export interface ButtonHandlers {
  min: () => void;
  max: () => void;
  half: () => void;
  double: () => void;
}

export interface Props extends InputProps {
  topLabel?: string;
  topValue?: string;
  coin?: string;
  coinIsLeftward?: boolean;
  withRefreshIcon?: boolean;
  withInfinityIcon?: boolean;
  buttonHandlers?: ButtonHandlers;
  onRefreshActionHandler?: () => void;
  warningModal?: JSX.Element;
  textAdornment?: string;
  showUSD?: boolean;
  buttonClass?: string;
  refreshIconClass?: string;
}

const getButtons = (buttonHandlers: ButtonHandlers, btnClass?: string) => {
  return (
    <>
      <Button className={btnClass} onClick={buttonHandlers?.min}>
        min
      </Button>
      <Button className={btnClass} onClick={buttonHandlers?.half}>
        1/2
      </Button>
      <Button className={btnClass} onClick={buttonHandlers?.double}>
        2x
      </Button>
      <Button className={btnClass} onClick={buttonHandlers?.max}>
        max
      </Button>
    </>
  );
};

const Input: FC<Props> = ({
  topLabel,
  topValue,
  coin,
  coinIsLeftward,
  withRefreshIcon,
  withInfinityIcon,
  textAdornment,
  value,
  showUSD,
  warningModal,
  className,
  buttonHandlers,
  classes,
  buttonClass,
  refreshIconClass,
  onRefreshActionHandler,
  ...props
}) => {
  const styles = useSassImport(() => import("./style.module.scss"));

  const adornment = (
    coin
      ? {
          [coinIsLeftward ? "startAdornment" : "endAdornment"]: (
            <InputAdornment position={coinIsLeftward ? "start" : "end"}>
              <GetCoinImg
                className={styles.coin}
                coin={coin}
                draggable={false}
              />
            </InputAdornment>
          ),
        }
      : (withRefreshIcon || withInfinityIcon) && !props.disabled
      ? {
          endAdornment: (
            <InputAdornment position="end">
              {withRefreshIcon ? (
                <RefreshIcon
                  onClick={onRefreshActionHandler}
                  className={clsx(styles.refreshIcon, refreshIconClass)}
                />
              ) : (
                <LoopIcon />
              )}
            </InputAdornment>
          ),
        }
      : null
  ) as {
    startAdornment: JSX.Element;
  };

  const buttons =
    buttonHandlers && !props.disabled
      ? {
          endAdornment: (
            <InputAdornment
              position="end"
              className={clsx(styles.buttons, styles.desktopButtons)}>
              {getButtons(buttonHandlers, buttonClass)}
            </InputAdornment>
          ),
        }
      : null;

  const text = textAdornment
    ? {
        startAdornment: (
          <InputAdornment className={styles.textAdornment} position="start">
            {textAdornment}
          </InputAdornment>
        ),
      }
    : null;

  return (
    <div className={clsx(styles.root, className)}>
      <div className={styles.topLabel}>
        {topLabel ? <label>{topLabel}</label> : null}
        {topValue ? <span>{topValue}</span> : null}
      </div>
      {warningModal || null}
      <InputBase
        classes={{
          root: clsx(styles.input, classes?.root),
          disabled: styles.inputDisabled,
        }}
        value={`${showUSD ? "$" : ""}${value}`}
        {...adornment}
        {...buttons}
        {...text}
        {...props}
      />
      {buttonHandlers && !props.disabled ? (
        <div className={clsx(styles.mobileButtons, styles.buttons)}>
          {getButtons(buttonHandlers, buttonClass)}
        </div>
      ) : null}
    </div>
  );
};

export default Input;
