import React, { FC, KeyboardEvent, MouseEvent } from "react";
import { createPortal } from "react-dom";
import clsx from "clsx";

import { useEventListener } from "hooks";
import { createGuard } from "utils";

import { PopUpRootProps } from "../types";
import css from "./style.module.scss";

const keyEventGuard = createGuard<KeyboardEvent<HTMLDivElement>>("key");

const rootDom = document.getElementById("pop-up");

export const PopUpRoot: FC<PopUpRootProps> = ({
  children,
  showAnimation,
  background = "transparent",
  curtainOnClick,
  curtainOnMove,
  className,
  classes,
}) => {
  const stopPropagation = (event: MouseEvent<HTMLDivElement>) => {
    event.stopPropagation();
  };

  useEventListener("keydown", (event) => {
    event.stopPropagation();

    if (keyEventGuard(event)) {
      if (event?.key === "Escape") {
        curtainOnClick && curtainOnClick({}, "escapeKeyDown");
      }
    }
  });

  if (rootDom) {
    return createPortal(
      <div
        className={clsx(
          css.root,
          className,
          showAnimation && (showAnimation === "hide" ? css.close : css.open),
          classes?.root,
        )}
        onClick={stopPropagation}>
        <div
          onPointerMove={curtainOnMove}
          onClick={() => {
            curtainOnClick && curtainOnClick({}, "backdropClick");
          }}
          className={clsx(
            css.curtain,
            background === "transparent"
              ? css.curtain__transparent
              : css.curtain__dark,
          )}
        />
        <div className={clsx(css.paper, classes?.paper)}>{children}</div>
      </div>,
      rootDom,
    );
  }

  console.error("can't find #pop-up element");
  return null;
};

export default PopUpRoot;
