import React, { FC } from "react";
import clsx from "clsx";

import { createFC, TableTFootProps } from "components";

export const TFoot: FC<TableTFootProps> = ({ values, components, classes }) => {
  const Cell = createFC(components?.cell);
  const Row = createFC(components?.row);
  const Footer = createFC(components?.foot);

  return (
    <tfoot className={clsx(classes?.tfoot)}>
      <Footer>
        <tr className={clsx(classes?.tr)}>
          <Row>
            {values.map((value) => (
              <td className={clsx(classes?.td)} key={value}>
                <Cell>{value}</Cell>
              </td>
            ))}
          </Row>
        </tr>
      </Footer>
    </tfoot>
  );
};
