import React, { FC, useEffect, useRef, useState } from "react";
import debounce from "lodash.debounce";

import { createListeners, isElementOnDisplay, track } from "utils";
import { useWindowSize } from "providers";
import { useDetectRobots } from "hooks";

interface Props {
  sizes:
    | {
        width: number | string;
        height?: number | string;
      }
    | {
        height: number | string;
        width?: number | string;
      };

  manualSet?: boolean;

  fallBack?: () => JSX.Element;
  debug?: boolean;
  isHide?: boolean;
  shouldHideIfOutOfFrame?: boolean;
}

const { addListeners, removeListeners } = createListeners([
  "scroll",
  "resize",
  "visibilitychange",
  "orientationchange",
]);

export const LazyHiderComponent: FC<Props> = ({
  children,
  sizes,
  manualSet,
  fallBack: FallBack,
  debug,
  isHide = false,
  shouldHideIfOutOfFrame,
}) => {
  const [isVisible, setIsVisible] = useState(false);
  const wrapperRef = useRef<HTMLDivElement | null>(null);
  const windowSizes = useWindowSize();
  const isRobot = useDetectRobots();

  const componentLoader = debounce(() => {
    try {
      if (!wrapperRef.current) return;

      if (debug) {
        debugger;
      }

      if (shouldHideIfOutOfFrame) {
        isElementOnDisplay(
          wrapperRef.current,
          () => setIsVisible(true),
          () => setIsVisible(false),
        );
      } else {
        if (!isVisible) {
          isElementOnDisplay(wrapperRef.current, () => {
            setIsVisible(true);
            removeListeners(componentLoader);
          });
        } else if (isVisible) {
          removeListeners(componentLoader);
        }
      }
    } catch (error) {
      setIsVisible(true);
      track()?.errors(error, {
        place: {
          fileName: "LazyHider/componentLoader",
        },
      });
    }
  }, 100);

  useEffect(() => {
    if (isVisible && !shouldHideIfOutOfFrame) {
      removeListeners(componentLoader);
      wrapperRef.current = null;
    }
  }, [isVisible]);

  useEffect(() => {
    return () => {
      removeListeners(componentLoader);
    };
  }, []);

  useEffect(() => {
    if (manualSet !== undefined && !isVisible) {
      setIsVisible(manualSet);
    }
  }, [manualSet]);

  return (
    <>
      {!shouldHideIfOutOfFrame && isVisible && !isRobot && !isHide ? (
        children
      ) : shouldHideIfOutOfFrame && isVisible ? (
        <div
          ref={(node) => {
            wrapperRef.current = node;
            if (manualSet === undefined) {
              componentLoader();
              addListeners(componentLoader);
            }
          }}>
          {children}
        </div>
      ) : (
        <div
          style={{
            width: sizes?.width,
            height: sizes?.height,
            maxWidth: windowSizes.width,
            maxHeight: windowSizes.height,
          }}
          ref={(node) => {
            wrapperRef.current = node;
            if (manualSet === undefined) {
              componentLoader();
              addListeners(componentLoader);
            }
          }}>
          {FallBack && <FallBack />}
        </div>
      )}
    </>
  );
};
