import React, { useState, FunctionComponent, useEffect } from "react";

import { useFetch, useLocalStorage } from "hooks";
import { generateSeed, track } from "utils";
import { notifyBarManager } from "components";

import Context, { ServerSeed, ClientSeed } from "./Context";

interface FetchData {
  success: boolean;
  message: string;

  seed: ServerSeed;
}
export type FairnessGameName =
  | "plinko"
  | "dice"
  | "wheel"
  | "limbo"
  | "videopoker"
  | "roulette"
  | "diamonds"
  | "keno"
  | "blackjack"
  | "mines"
  | "towers"
  | "fortune";

export const fairnessGameNames: { [key in FairnessGameName]: string } = {
  plinko: "plinko",
  dice: "hash-dice",
  wheel: "wheel",
  limbo: "limbo",
  videopoker: "video-poker",
  roulette: "roulette",
  diamonds: "diamonds",
  keno: "keno",
  blackjack: "blackjack",
  mines: "mines",
  towers: "towers",
  fortune: "fortune-quest",
};

interface Props {
  gameName: FairnessGameName;
  persistKey?: string;
}

const getInitClientSeed = (persistKey: string) => {
  const persistValue = localStorage.getItem(persistKey);
  const defaultSeed = {
    seed: generateSeed(64),
    isSetManually: false,
  };

  if (!persistValue) {
    return defaultSeed;
  }

  try {
    return JSON.parse(persistValue) as ClientSeed;
  } catch (error) {
    const message = `
${error}
Seed > Provider > initClientSeed
persistValue: ${JSON.stringify(persistValue)}`;

    track()?.errors(message);
    return defaultSeed;
  }
};

const clientSeed: Record<string, ClientSeed> = {};

const Provider: FunctionComponent<Props> = ({
  gameName,
  persistKey = "clientSeed",
  children,
}) => {
  const [token] = useLocalStorage("token");
  const [serverSeed, setServerSeed] = useState<ServerSeed | null>(null);

  useEffect(() => {
    clientSeed[persistKey] = getInitClientSeed(persistKey);
  }, [persistKey]);

  const setClientSeed = (value: ClientSeed) => {
    const { isSetManually, seed } = value;
    clientSeed[persistKey].seed = seed;
    clientSeed[persistKey].isSetManually = isSetManually;

    localStorage.setItem(persistKey, JSON.stringify(value));
  };

  const [fetchGet, fetchGetResult] = useFetch<FetchData>(
    `${gameName}/get-seed`,
  );
  const [fetchRotate, fetchRotateResult] = useFetch<FetchData>(
    `${gameName}/rotate-seed`,
  );

  const getServerSeed = () => {
    if (!token) {
      return;
    }
    fetchGet({ variables: { token } });
  };

  useEffect(() => {
    getServerSeed();
  }, [token]);

  useEffect(() => {
    if (!fetchGetResult?.data?.seed) {
      return;
    }
    setServerSeed(fetchGetResult?.data?.seed);
  }, [fetchGetResult?.data?.seed]);

  useEffect(() => {
    if (!fetchRotateResult?.data?.seed) {
      return;
    }

    setServerSeed(fetchRotateResult.data.seed);
  }, [fetchRotateResult?.data?.seed]);

  const rotateServerSeed = async () => {
    const { data } = await fetchRotate();
    if (!data?.success && data?.message) {
      notifyBarManager.error({
        message: data.message,
        timeOut: 4000,
      });
    }
  };

  const rotateClientSeed = () => {
    const seed = {
      seed: generateSeed(64),
      isSetManually: false,
    };
    setClientSeed(seed);

    return seed;
  };

  const updateNonce = () => {
    setServerSeed((seed) => {
      if (!seed) {
        return seed;
      }

      const updatedNonce = Number(seed.games_played) + 1;

      return {
        ...seed,
        games_played: updatedNonce ? `${updatedNonce}` : seed.games_played,
      };
    });
  };

  return (
    <Context.Provider
      value={{
        serverSeed,
        getServerSeed,
        rotateServerSeed,

        clientSeed: clientSeed[persistKey],
        setClientSeed,
        rotateClientSeed,
        updateNonce,
      }}>
      {children}
    </Context.Provider>
  );
};

export default Provider;
