import React, { FC, CSSProperties, useCallback } from "react";
import { smileToImg } from "utils/smileToImg";

import { SmileCode } from "..";
import css from "./style.module.scss";
interface ImgSmileList {
  [key: string]: React.MemoExoticComponent<() => JSX.Element>;
}

export const imgList: ImgSmileList = {};

interface Props {
  element: SmileCode;
  style: CSSProperties;
  smileEnter: (v: string) => void;
}

const Component: FC<Props> = ({ element, style, smileEnter }) => {
  const Smile = useCallback(
    () => (
      <div
        style={style}
        className={css.cell}
        onClick={() => {
          smileEnter(element.smile);
        }}
        dangerouslySetInnerHTML={
          element ? { __html: smileToImg(element.base) } : undefined
        }
      />
    ),
    [element],
  );

  return <Smile />;
};

export default Component;
