import React, { FC, memo } from "react";
import clsx from "clsx";

import { GetCoinImg, Button, LinkWithSearch } from "components";
import css from "./style.module.scss";
import { getSearch } from "../../Messages/utils";

export const hiddenMessagesAmount = 7;

interface Props {
  winners: string[];
  amount: string;
  coin: string;
  rain_by: string;
  message_id: string;

  isHidden: boolean;
  setIsHidden: () => void;
}

const Component: FC<Props> = ({
  amount,
  coin,
  rain_by,
  message_id,
  winners,

  isHidden,
  setIsHidden,
}) => {
  const getRenderMessage = () => {
    let resultWinners = [...winners];

    if (isHidden) {
      resultWinners.length = Math.min(
        resultWinners.length,
        hiddenMessagesAmount,
      );
    }

    return resultWinners;
  };

  const renderMessage = getRenderMessage();

  return (
    <>
      <div className={clsx(css.rainWrapper, css.text)}>
        <span className={css.rainHeader}>
          <p className={css.rainUserBlock}>
            <span className={css.rainUser}>
              <LinkWithSearch
                {...getSearch(rain_by)}
                className={clsx(css.text)}>
                @{rain_by}
              </LinkWithSearch>
            </span>
            is making it rain!
          </p>

          <span className={css.rainCoin}>
            <span className={css.rainAmount}>{amount}</span>
            <GetCoinImg coin={coin} className={css.rainCoinImg} />
            each!
          </span>

          <span>Users who have received the water are as follows:</span>
        </span>

        {renderMessage.map((user) => (
          <span
            className={css.rainUserBlock}
            key={`${user}_${winners.length}_${message_id}`}>
            <span className={css.rainUser}>
              <LinkWithSearch {...getSearch(user)} className={clsx(css.text)}>
                @{user}
              </LinkWithSearch>
            </span>
          </span>
        ))}

        {isHidden && winners.length > hiddenMessagesAmount && (
          <Button className={css.seeAllButton} onClick={setIsHidden}>
            {`See all winners (${winners.length})`}
          </Button>
        )}

        <span className={css.rainBottom}>Congratulations!</span>
      </div>
    </>
  );
};

export const Rain = memo(
  Component,
  (p, n) =>
    p.message_id === n.message_id &&
    p.isHidden === n.isHidden &&
    p.setIsHidden === n.setIsHidden &&
    p.winners.length === n.winners.length,
);
