import React, { FC, memo } from "react";
import matchAll from "match-all";
import clsx from "clsx";

import { LinkWithSearch, LinkWithSearchProps, Typography } from "components";
import css from "./style.module.scss";
import { reduceSmileString } from "utils";

const userReg = /(@\w*)/g;

interface Props {
  message: string;
}

const getSearch = (user_name: string): LinkWithSearchProps => ({
  search: { name: "page", value: "profile" },
  searchParams: {
    user: user_name,
  },
});

const Component: FC<Props> = ({ message }) => {
  const createMessage = () => {
    const findedNames: string[] = matchAll(message, userReg).toArray();

    const splitMessage = message.split(userReg);
    const resultMessage = splitMessage.map((string) => {
      const isName = findedNames.indexOf(string) >= 0;

      if (isName) {
        return (
          <LinkWithSearch
            {...getSearch(string.substring(1))}
            className={clsx(css.link)}>
            <Typography
              variant="inherit"
              className={clsx(css.text, css.authorText)}>
              {string}
            </Typography>
          </LinkWithSearch>
        );
      }

      return (
        <span
          dangerouslySetInnerHTML={{
            __html: reduceSmileString(string),
          }}
        />
      );
    });

    return resultMessage;
  };

  return <span className={css.text}>{createMessage()}</span>;
};

export const Text = memo(Component, (p, n) => p.message === n.message);
