import React, { FunctionComponent, useReducer } from "react";

import Context from "./Context";
import reducer from "./reducer";

const BalanceAnimationProvider: FunctionComponent = ({ children }) => {
  const [balanceUpdate, balanceDispatch] = useReducer(reducer, []);

  return (
    <Context.Provider
      value={{
        balanceUpdate,
        balanceDispatch,
      }}>
      {children}
    </Context.Provider>
  );
};

export default BalanceAnimationProvider;
