import React, { MouseEventHandler, FC, memo } from "react";
import clsx from "clsx";

import { LinkWithSearch, Rating, Typography, useMessages } from "components";

import Fire_URL from "assets/chat/fire.png";
import TV_URL from "assets/chat/tv.png";
import css from "./style.module.scss";
import { getSearch } from "../../utils";

interface Props {
  user: string;
  is_moderator: boolean;
  is_streamer: boolean;
  rating: number;
}

const Content: FC<Props> = ({ user, rating, is_moderator, is_streamer }) => {
  const { chatFunctions, userParams } = useMessages();

  const rightClickForBan: MouseEventHandler<HTMLDivElement> = (event) => {
    event.preventDefault();
    if (userParams?.chat_moderator) {
      chatFunctions.current.setSystemMessage(user, "ban");
    } else {
      chatFunctions.current.setSystemMessage(user, "contact");
    }
  };

  let preEmoji = "";
  if (is_moderator) preEmoji = Fire_URL;
  else if (is_streamer) preEmoji = TV_URL;
  return (
    <div className={css.nameBlock} onContextMenu={rightClickForBan}>
      <LinkWithSearch {...getSearch(user)}>
        <Typography
          variant="inherit"
          className={clsx(
            css.userName,
            is_streamer ? css.streamer : is_moderator ? css.moderator : "",
          )}>
          {preEmoji && (
            <img
              src={preEmoji}
              alt={is_moderator ? "🔥" : "📺"}
              className={css.preEmoji}
            />
          )}
          {user}
        </Typography>
      </LinkWithSearch>
      <Rating value={rating} />
    </div>
  );
};

export const User = memo(Content, (prev, next) => prev.user === next.user);
