import React, { useState, useEffect, FunctionComponent, useMemo } from "react";

import locales, {
  defaultLocale,
  languageImport,
  localesLanguages,
  localesRTLLanguages,
} from "config/locales";
import { track } from "utils";

import Context from "./Context";

interface Props {
  persistKey?: string;
}

export const contextValues = {
  locale: "en",
};

const checkUserLanguage = () => {
  const langPriority = window.navigator.languages;
  const langDefault = window.navigator.language;

  const langCut = (lang: string) => lang.toLocaleLowerCase().substr(0, 2);

  let useLang = langPriority.find((lang) => langCut(lang) in localesLanguages);

  if (!useLang) {
    useLang =
      langCut(langDefault) in localesLanguages
        ? (langCut(langDefault) as localesLanguages)
        : defaultLocale;
  }

  return langCut(useLang);
};

const Provider: FunctionComponent<Props> = ({
  children,
  persistKey = "locale",
}) => {
  const persistLocale = localStorage.getItem(persistKey) || checkUserLanguage();

  const persistValue =
    (persistLocale &&
      persistLocale in localesLanguages &&
      (persistLocale as localesLanguages)) ||
    defaultLocale;

  const [locale, setLocale] = useState(persistValue);
  const [messages, setMessages] = useState(
    locales[locale] ? locales[locale] : locales[defaultLocale],
  );

  useEffect(() => {
    contextValues.locale = locale;
    try {
      localStorage.setItem(persistKey, locale);
      if (!locales[locale]) {
        languageImport(locale, setMessages);
      } else {
        setMessages(locales[locale]);
      }
    } catch (error) {
      const message = `
          ${error}
          Locale > Provider > setItem
          persistKey: ${JSON.stringify(persistKey)}
          locale: ${JSON.stringify(locale)}`;

      try {
        track()?.errors(message);
      } catch {
        console.log("🚀 trackError", error, locale, persistKey);
      }
    }
  }, [locale, persistKey]);

  const rtl = useMemo(() => {
    const isRTL = localesRTLLanguages[locale];

    if (isRTL) {
      document.body.dir = "rtl";
    } else {
      document.body.dir = "ltl";
    }

    return isRTL;
  }, [locale]);

  return (
    <Context.Provider
      value={{ locale, setLocale, rtl, messages: messages || {} }}>
      {children}
    </Context.Provider>
  );
};

export default Provider;
