import React, {
  Children,
  cloneElement,
  forwardRef,
  useEffect,
  useImperativeHandle,
  useRef,
  useState,
} from "react";
import { useHistory } from "react-router-dom";
import clsx from "clsx";

import {
  FullScreenProps,
  FullScreenReactElement,
  FullScreenRefValues,
  HTMLFullScreenNode,
} from "./types";
import { disableFullscreen, enableFullscreen } from "./utils";
import css from "./style.module.scss";
import { track } from "utils";

export * from "./types";

const fullScreenSearchName = "fullScreen";

export const FullScreen = forwardRef<FullScreenRefValues, FullScreenProps>(
  ({ children, warnAboutIsFullScreen }, ref) => {
    const history = useHistory();

    const [isEnable, setIsEnable] = useState(false);
    const [customFullScreen, setCustomFullScreen] = useState(false);

    const childElementRef = useRef<HTMLFullScreenNode | null>(null);
    const activeFullScreenElementRef = useRef<Element | null>(null);

    const element = Children.map(
      children as FullScreenReactElement,
      (child) => {
        return cloneElement(child, {
          ...child.props,
          ref: (node: any) => {
            childElementRef.current = node;

            //@ts-ignore
            const ref: any = child.ref;

            if (typeof ref === "function") {
              ref(node);
            } else if (ref) {
              ref.current = node;
            }

            return node;
          },
          className: clsx(
            customFullScreen && css.customFullScreen,
            child.props?.className,
          ),
        });
      },
    );

    const handleCustomFullScreen = (value: boolean) => {
      const params = new URLSearchParams(history.location.search);

      if (value) {
        params.set(fullScreenSearchName, "enable");
      } else {
        params.delete(fullScreenSearchName);
      }
      history.push({ ...history.location, search: params.toString() });
    };

    const enterFullscreen = async () => {
      try {
        return await enableFullscreen(childElementRef.current);
      } catch (error) {
        console.error("enterFullscreen error: ", error);
        handleCustomFullScreen(true);
        track()?.errors(error, {
          custom: {
            place: "enterFullscreen error",
          },
        });
      }
    };

    const exitFullscreen = async () => {
      try {
        await disableFullscreen();
      } catch (error) {
        console.error("exitFullscreen error: ", error);
        handleCustomFullScreen(false);
        track()?.errors(error, {
          custom: {
            place: "exitFullscreen error",
          },
        });
      }
    };

    useEffect(() => {
      const fullscreenOnChange = () => {
        const fullscreenElement = document.fullscreenElement;
        activeFullScreenElementRef.current = fullscreenElement;
        setIsEnable(!!fullscreenElement);
      };

      document.addEventListener("fullscreenchange", fullscreenOnChange);

      return () => {
        document.removeEventListener("fullscreenchange", fullscreenOnChange);
      };
    }, [childElementRef]);

    useEffect(() => {
      warnAboutIsFullScreen && warnAboutIsFullScreen(isEnable);
    }, [isEnable]);

    useEffect(() => {
      const params = new URLSearchParams(history.location.search);

      const isFullScreen = !!params.get(fullScreenSearchName);
      setCustomFullScreen(isFullScreen);

      const disableFromCustomFullScreen = (event: KeyboardEvent) => {
        if (event.code === "Escape") {
          handleCustomFullScreen(false);
        }
      };

      const removeListeners = () =>
        window.removeEventListener("keyup", disableFromCustomFullScreen);

      if (isFullScreen) {
        window.addEventListener("keyup", disableFromCustomFullScreen);
      } else {
        removeListeners();
      }

      return () => {
        removeListeners();
      };
    }, [history.location.search]);

    useImperativeHandle(ref, () => ({
      enterFullscreen,
      exitFullscreen,
      fullScreenElement: activeFullScreenElementRef.current,
      currentElement: childElementRef.current,
    }));

    return <>{element}</>;
  },
);
