import React, { Dispatch, SetStateAction } from "react";
import { NotificationAudio, NotificationData } from "./types";

type ChatContextType = {
  notification: NotificationData;
  setNotification: Dispatch<SetStateAction<NotificationData>>;
  audio: {
    current: NotificationAudio | null;
  };
  playAudio: (whichSound?: keyof NotificationAudio) => void;

  missed: number;
  setMissed: (missed: number) => void;

  missedChallenges: boolean;
  setMissedChallenges: (isMissed: boolean) => void;
};

export const defaultValue: ChatContextType = {
  notification: {
    isOpen: false,
    notifications: [],
    rewards: [],
    challenges: [],
  },
  setNotification: () => {},
  audio: {
    current: null,
  },
  playAudio: () => {},
  missed: 0,
  setMissed: () => {},

  missedChallenges: false,
  setMissedChallenges: () => {},
};

export const Context = React.createContext<ChatContextType>(defaultValue);

export default Context;
