import React, { useCallback } from "react";
import { Tabs, Tab } from "components";
import css from "./style.module.scss";

interface Props {
  value: boolean;
  onChange: (val: boolean) => void;
}

const SwitchBar = ({ value, onChange }: Props) => {
  const handleChange = useCallback((ev: any, value: number | string) => {
    onChange(value === 1);
  }, []);
  return (
    <div className={css.root}>
      <Tabs
        value={value ? 1 : 0}
        onChange={handleChange}
        highlighterClass={css.indicator}
        className={css.wrapper}>
        <Tab
          label="Emoji"
          classes={{
            root: css.tab,
            label: css.tabText,
          }}
        />
        <Tab
          label="GIF"
          classes={{
            root: css.tab,
            label: css.tabText,
          }}
        />
      </Tabs>
    </div>
  );
};

export default SwitchBar;
