import React, { FC } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { Button, Transition } from "components";
import { useSassImport } from "hooks";

interface Props {
  isOpen: boolean;
  onClose: () => void;
  callBack: () => void;
  className?: string;
}

export const Warning: FC<Props> = ({
  isOpen,
  onClose,
  callBack,
  className,
}) => {
  const intl = useGetLocale();
  const css = useSassImport(() => import("./style.module.scss"));

  const handleYes = () => {
    callBack();
    onClose();
  };

  return (
    <div
      className={clsx(css.box, {
        [css.disableWarning]: !isOpen,
      })}>
      <div
        className={clsx(css.disableWrapper, !isOpen && css.wrapperNone)}
        onClick={onClose}></div>
      <Transition
        in={isOpen}
        className={clsx(css.root, className)}
        animation="grow">
        <h1 className={css.title}>
          {intl.formatMessage({ id: "crashInterface.bet.menu.warning" })}
        </h1>
        <div className={css.buttonBox}>
          <Button onClick={handleYes}>Yes</Button>
          <Button onClick={onClose}>No</Button>
        </div>
      </Transition>
    </div>
  );
};

export default Warning;
