import React, { useState, useEffect } from "react";

import { clearTimeout, setTimeout } from "timers";
import { Icon, InputAdornment } from "components";
import { useGetLocale } from "providers";
import { debounce } from "utils/lodash";

import { useHotKeys } from "components/HotKeys/Provider";
import InputBase from "components/Input/InputBase";
import image from "assets/logos/powered-tenor.png";

import * as TenorApi from "./tenorFetch";
import css from "./style.module.scss";
import { track } from "utils";

interface Props {
  onClose: () => void;
  sendGif: (value: string) => void;
}

const TenorComponent = ({ onClose, sendGif }: Props) => {
  const intl = useGetLocale();

  const [gifArr, setGifArr] = useState<any[]>([]);
  const [search, setSearch] = useState({
    key: "",
    next: null,
  });
  const { setIsHotKeysPaused } = useHotKeys();

  useEffect(() => {
    if (!gifArr.length) {
      getGifs(null, null, true);
    }
  }, []);

  useEffect(() => {
    const delayedDebounceFn = setTimeout(() => {
      getGifs(search.key, null, true);
    }, 1000);

    return () => clearTimeout(delayedDebounceFn);
  }, [search.key]);

  const getGifs = debounce(
    (searchKey: string | null, next: string | null, clear: boolean) => {
      TenorApi.searchGif(searchKey, next, intl.locale)
        .then(({ next, results }) => {
          setSearch({
            ...search,
            next: next,
          });
          if (clear) {
            setGifArr(results);
          } else {
            setGifArr([...gifArr, ...results]);
          }
        })
        .catch((error) => {
          track()?.errors(error, {
            custom: {
              function: "getGifs",
            },
          });
        });
    },
    1000,
    {
      leading: true,
      trailing: false,
    },
  );

  const handleScroll = (event: any) => {
    const { scrollHeight, scrollTop, clientHeight } = event.target;
    const scrollPos = scrollHeight - scrollTop - clientHeight;

    if (scrollPos > 1) {
      return;
    }
    if (search.next && search.next === "0") {
      return;
    }
    getGifs(search.key, search.next, false);
  };

  const handleClick = (image: any) => {
    sendGif(image.url);
    onClose();
  };

  const handleInputBlur = () => {
    setIsHotKeysPaused(false);
  };

  const handleInputFocus = () => {
    setIsHotKeysPaused(true);
  };

  const handleSearchChange = (value: string) => {
    setSearch({
      key: value,
      next: search.next,
    });
  };

  return (
    <div className={css.root}>
      <InputBase
        autoComplete="off"
        placeholder="Search GIF"
        onFocus={handleInputFocus}
        onBlur={handleInputBlur}
        value={search.key}
        className={css.input}
        onChange={(e) => handleSearchChange(e.target.value)}
        startAdornment={
          <InputAdornment position="start" className={css.formInputAdornment}>
            <Icon variant={"search"} className={css.formInputAdornmentIcon} />
          </InputAdornment>
        }
      />
      <div className={css.gifContainer} onScroll={handleScroll}>
        {gifArr.length ? (
          gifArr.map((item) => (
            <div
              key={item.id}
              onClick={() => handleClick(item.media[0].tinygif)}>
              <img
                className={css.image}
                src={item.media[0].tinygif.url}
                alt={"test"}
              />
            </div>
          ))
        ) : (
          <span>No results</span>
        )}
      </div>
      <img className={css.tenor} src={image} />
    </div>
  );
};

export default TenorComponent;
