export type LiveStatsElement = number;
export type LiveStatsList = LiveStatsElement[];

export enum LiveStatsEvents {
  GET_DATA = "GET_DATA",
  GET_DOTS = "GET_DOTS",
}

export interface LiveData {
  profit: number;
  amount: number;
  coin: string;
}

export interface LiveStatistics {
  wins: number;
  loses: number;
}

export interface LiveStatsDataResult {
  generalLiveData: LiveData;
  statistics: LiveStatistics;
}
