import {
  ChatPipeTypes,
  MessageListener,
  SlotListener,
  SocketListener,
  StatisticAllBetsTypes,
  StatisticMyBetsTypes,
} from "./types";

export const chatPipeFunctions: ChatPipeTypes = {
  chatMessageListener: () => null,

  statisticAllBetsListener: () => null,
  homePageAllBetsListener: () => null,
  statisticMyBetsListener: () => null,

  slotPageListener: () => null,
};

export const chatSocketListener = (
  data: SocketListener, // data -- incomeValues
) => {
  switch (data.action) {
    case "last_messages":
      chatPipeFunctions.chatMessageListener({
        action: "last_messages",
        messages: data.messages.map((message: any) => {
          if (!message.type) message.type = "text";

          return message;
        }),
      });
      break;
    case "set_user_parameters":
      chatPipeFunctions.chatMessageListener(data as MessageListener);
      chatPipeFunctions.statisticAllBetsListener(data as StatisticAllBetsTypes);
      chatPipeFunctions.homePageAllBetsListener(data as StatisticAllBetsTypes);
      chatPipeFunctions.statisticMyBetsListener(data as StatisticMyBetsTypes);

      break;

    case "message_received":
    case "gif_received":
    case "rain_received":
      const type =
        data.action === "rain_received"
          ? "rain"
          : data.action === "gif_received"
          ? "gif"
          : "text";

      chatPipeFunctions.chatMessageListener({
        action: "message_received",
        is_moderator: data.is_moderator,
        is_streamer: data.streamer,
        level: data.level,
        message_id: data.message_id,
        profile_picture_url: data.profile_picture_url || "",
        user_name: data.user_name,

        type: type,

        message: data.message,

        gif_url: data.gif_url,

        rain_by: data.rain_by,
        winners: data.winners,
        amount: data.amount,
        coin: data.coin,
        rain: data.rain,
      });
      break;

    case "server_message":
      chatPipeFunctions.chatMessageListener({
        action: "server_message",
        remove_message: data.remove_message,
        message_id: data.message_id,
        message: data.message,
        profile_picture_url: data.profile_picture_url || "",
      });
      break;

    case "remove_message":
      chatPipeFunctions.chatMessageListener({
        action: "remove_message",
        message_id: data.message_id,
      });
      break;

    case "all_bets":
      chatPipeFunctions.statisticAllBetsListener(data as StatisticAllBetsTypes);
      chatPipeFunctions.homePageAllBetsListener(data as StatisticAllBetsTypes);

      break;

    case "new_bet_received":
      if (!data.game) {
        data.game = "─";
      }
      chatPipeFunctions.statisticAllBetsListener(data as StatisticAllBetsTypes);
      chatPipeFunctions.homePageAllBetsListener(data as StatisticAllBetsTypes);
      chatPipeFunctions.statisticMyBetsListener(data as StatisticAllBetsTypes);

      if (data.game === "SLOTS") {
        chatPipeFunctions.slotPageListener(data as unknown as SlotListener);
      }
      break;

    case "my_bets":
      chatPipeFunctions.statisticMyBetsListener(data as StatisticMyBetsTypes);
      break;

    default:
      break;
  }
};
