import React, { CSSProperties, FC, MouseEventHandler } from "react";
import clsx from "clsx";

import { Message as MessageType } from "components/Chat/types";
import { useMessages } from "components/Chat/chatContext";
import css from "./style.module.scss";

import { MessageAvatar, User } from "./components";

interface Props {
  user: string;
  avatar: string;
  rating: number;
  type: MessageType["type"];

  is_moderator: boolean;
  is_streamer: boolean;

  style?: CSSProperties;
  index?: string | number;

  isHightlight: boolean;
  id: string;

  rainBy?: string;
}

export const Message: FC<Props> = ({
  avatar,
  rating,
  user,
  children,
  type,
  isHightlight,
  is_moderator,
  is_streamer,

  style,
  index,
  id,

  rainBy,
}) => {
  const { device, chatFunctions, userParams } = useMessages();

  const handleUserContact = () => {
    const contact = rainBy || user;
    chatFunctions.current.setSystemMessage(contact, "contact");
  };

  const rightClickForDelete: MouseEventHandler<HTMLDivElement> = (event) => {
    event.preventDefault();

    if (userParams?.chat_moderator) {
      chatFunctions.current.setSystemMessage(id, "remove");
    } else {
      handleUserContact();
    }
  };

  return (
    <div
      className={css.messageWrap}
      style={style}
      data-message-index={`${index}`}>
      <MessageAvatar
        {...{
          avatar,
          rating,
          user,
        }}
      />

      <div
        className={clsx(
          css.content,
          device === "desktop" ? css.desktop : css.mobile,
        )}>
        <User
          {...{
            user,
            rating,
            is_moderator,
            is_streamer,
          }}
        />

        <div
          onDoubleClick={handleUserContact}
          onContextMenu={rightClickForDelete}
          className={clsx(
            css.messageBlock,
            type === "gif" && css.imageBlock,
            isHightlight && css.contactMessage,
          )}>
          {children}
        </div>
      </div>
    </div>
  );
};
