import React, { useState } from "react";
import clsx from "clsx";

import {
  FilledInput,
  FilledInputProps,
  Icon,
  IconProps,
  InputAdornment,
} from "components";

import css from "./style.module.scss";

export interface InputProps extends FilledInputProps {
  label?: string;
  helperText?: string;
}

const FormInput = React.forwardRef(
  (
    {
      id,
      type,
      value,
      onChange,
      className,
      label,
      helperText,
      error,
      ...props
    }: InputProps,
    ref,
  ) => {
    const [showPassword, setShowPassword] = useState(false);

    const handleClickShowPassword: IconProps["onClick"] = (event) => {
      event.preventDefault();

      setShowPassword(!showPassword);
    };

    const handleMouseDownPassword: IconProps["onMouseDown"] = (event) => {
      event.preventDefault();

      if (event.nativeEvent.button === 0) {
        setShowPassword(!showPassword);
      }
    };

    const handleChange: FilledInputProps["onChange"] = (event: any) => {
      if (onChange) {
        onChange(event);
      }
    };

    return (
      <>
        <FilledInput
          {...props}
          label={label}
          classes={{
            ...props.classes,
            root: clsx(css.root, className),
            label: css.label,
          }}
          inputClasses={{
            root: clsx(
              css.inputField,
              error && css.error,
              type === "password" && css.passwordFormInputField,
            ),
          }}
          value={value}
          onChange={handleChange}
          //@ts-ignore
          ref={ref}
          id={id}
          type={type === "password" && showPassword ? "text" : type}
          endAdornment={
            type === "password" ? (
              <InputAdornment position="end" className={css.formInputAdornment}>
                <Icon
                  aria-label="toggle password visibility"
                  className={css.formInputAdornmentIcon}
                  onClick={handleClickShowPassword}
                  onMouseDown={handleMouseDownPassword}
                  fontSize={28}
                  variant={showPassword ? "visibilityOff" : "visibility"}
                />
              </InputAdornment>
            ) : (
              props.endAdornment
            )
          }
        />
        {/* {helperText && <FormHelperText error>{helperText}</FormHelperText>} */}
      </>
    );
  },
);

export default FormInput;
