export const cutSmallNumber = (number: number | string, length: number) => {
  let multiplier = Number("1".padEnd(length, "0"));
  let calcValue = Number(number) * multiplier;

  if (calcValue < 1) {
    multiplier *= 10;
    calcValue = Math.round(Number(number) * multiplier);
  }

  return Math.floor(calcValue) / multiplier;
};
