import { useEffect, useState } from "react";
import { track } from "utils";

type CSSType = {
  [key: string]: string;
};
export const useSassImport = (
  imp: () => Promise<typeof import("*.module.scss")>,
) => {
  const [css, setCss] = useState<CSSType>({});

  useEffect(() => {
    const getCss = async () => {
      try {
        const result = await imp();

        setCss(result.default);
      } catch (error) {
        console.error("wrong sass import: ", error);
        track()?.errors(error);
      }
    };

    getCss();
  }, []);

  return css;
};
