import React, { MutableRefObject, useEffect } from "react";
import { track } from "utils";
import { soundSystemConfig } from "../config";
import { permittedSoundGamePaths } from "../provider";
import { IAudiosRef, IAudioState } from "../types";

interface IUseBackgroundSound {
  audiosRef: MutableRefObject<IAudiosRef>;
  pathname: string;
  soundSystemState: IAudioState;
}
export const useBackgroundSound = ({
  audiosRef,
  pathname,
  soundSystemState,
}: IUseBackgroundSound) => {
  const isCanPlay =
    permittedSoundGamePaths.includes(pathname) &&
    soundSystemState.isInteractivityWas;

  useEffect(() => {
    if (
      !audiosRef.current ||
      audiosRef.current.background.audio === null ||
      (isCanPlay && soundSystemConfig.games[pathname].background.length === 0)
    )
      return;

    const listener = () => {
      if (
        audiosRef.current.background.audio === null ||
        !audiosRef.current.background.src
      )
        return;
      let nextIndex =
        audiosRef.current.background!.index ===
        soundSystemConfig.games[pathname].background.length - 1
          ? 0
          : audiosRef.current.background!.index + 1;

      const src = soundSystemConfig.games[pathname].background[nextIndex];
      audiosRef.current.background.audio.pause();
      audiosRef.current.background.audio.src = src;
      audiosRef.current.background.audio.load();

      const promise = audiosRef.current.background!.audio.play();
      promise
        .then(() => {
          audiosRef.current.background!.index = nextIndex;
        })
        .catch((err) => {
          console.log(err);
          track()?.errors(err, {
            custom: {
              place: "audiosRef.current.background",
            },
          });
        });
    };
    if (!audiosRef.current.background.src) {
      audiosRef.current.background.audio.removeEventListener("ended", listener);
      return;
    }
    audiosRef.current.background!.audio.addEventListener("ended", listener);
    return () => {
      audiosRef.current.background.audio &&
        audiosRef.current.background.audio.removeEventListener(
          "ended",
          listener,
        );
    };
  }, [pathname]);

  useEffect(() => {
    const { background } = audiosRef.current;
    if (!background.src || !isCanPlay) return;

    if (!soundSystemState.isBackgroundPlay) {
      if (background.audio !== null) {
        background.audio.pause();
        background.audio.currentTime = 0;
        return;
      }
    } else {
      if (background.audio === null) {
        background.audio = new Audio(background.src);
        background.audio.volume = soundSystemState.volume / 2.5;
      }
      background.audio.src = background.src;
      background.audio.load();

      const soundPromise = background.audio ? background.audio.play() : null;
      if (soundPromise) {
        soundPromise
          .then(() => {
            if (background.audio)
              background.audio.onended = () => {
                background.audio?.play();
              };
          })
          .catch((err) => {
            console.log(err);
            track()?.errors(err, {
              custom: {
                place: "soundPromise *",
              },
            });
          });
      }
    }
  }, [soundSystemState.isBackgroundPlay, isCanPlay, pathname]);

  useEffect(() => {
    if (!audiosRef.current || !audiosRef.current.background.audio) return;
    audiosRef.current.background!.audio.volume = soundSystemState.volume / 2.5;
  }, [soundSystemState.volume]);

  return;
};
