import { useRef } from "react";
import { useResizeObserver } from "hooks/useResizeObserver";

interface ScrollOptionsTypes {
  left: number;
  behavior: ScrollBehavior | undefined;
}
const scrollOptions = (step: number): ScrollOptionsTypes => ({
  left: step,
  behavior: "smooth",
});

export const useScroll = (
  observerContainer: HTMLDivElement | null,
  elementsAmounth: number,
  defaultCardWidth: number,
  rows: number,
  cardsAmount: number | null,
  currentIndex: number | null,
  setActualIndex: React.Dispatch<React.SetStateAction<number>>,
  isGrow: boolean,
) => {
  const step = useRef<number>(0);
  const visibleEls = useRef<number | undefined>(0);
  const elementWidth = useRef<number>(0);
  const dataLength = useRef<number>(0);
  const sliderWrapper = useRef<HTMLDivElement | null>(null);

  if (currentIndex === 0) step.current = 0;

  useResizeObserver(
    ([{ target }]) => {
      if (!target || !elementsAmounth) {
        return;
      }

      sliderWrapper.current = target.firstChild as HTMLDivElement;

      sliderWrapper.current.style.width = "";
      const { offsetWidth, children } = sliderWrapper.current as HTMLDivElement;

      const defaultWrapperWidth = defaultCardWidth * dataLength.current;

      const newSliderSize =
        offsetWidth >= defaultWrapperWidth
          ? defaultWrapperWidth
          : defaultCardWidth * Math.floor(offsetWidth / defaultCardWidth);

      if (!isGrow) {
        sliderWrapper.current.style.width = `${newSliderSize}px`;
      }

      dataLength.current = elementsAmounth;
      setActualIndex(0);
      step.current = 0;
      sliderWrapper.current.scrollTo(scrollOptions(step.current));

      visibleEls.current = Math.floor(offsetWidth / defaultCardWidth);

      elementWidth.current = !isGrow
        ? defaultCardWidth
        : offsetWidth / (cardsAmount ?? visibleEls.current);

      sliderWrapper.current.addEventListener("touchend", () => {
        if (!sliderWrapper?.current) return;
        const scrollStep = Math.round(
          sliderWrapper.current.scrollLeft / elementWidth.current,
        );
        step.current = scrollStep * elementWidth.current;
        setActualIndex(scrollStep);
        sliderWrapper.current.scrollTo(scrollOptions(step.current));
      });

      const elements = [...children] as HTMLAnchorElement[];
      const firstImage = elements[0];
      if (!firstImage) return;

      elements.forEach((element) => {
        element.style.width = `${elementWidth.current - 5}px`;
        element.style.margin = "2.5px";
      });
    },
    observerContainer,
    [elementsAmounth, observerContainer, cardsAmount],
  );

  const scroll = (type: "increment" | "decrement") => {
    const isIncrement = type === "increment";

    if (visibleEls.current) {
      const finalStep =
        elementWidth.current *
        (dataLength.current / rows - (cardsAmount ?? visibleEls.current));
      const isOverScroll = isIncrement
        ? step.current > finalStep - 1
        : step.current < elementWidth.current;
      if (isOverScroll) {
        const scrollStep = isIncrement ? 0 : finalStep;
        sliderWrapper?.current?.scrollTo(scrollOptions(scrollStep));
        step.current = scrollStep;
        return;
      }
      step.current = isIncrement
        ? step.current + elementWidth.current
        : step.current - elementWidth.current;
    }

    sliderWrapper?.current?.scrollTo(scrollOptions(step.current));
  };
  const setIndex = () => {
    setActualIndex(step.current / elementWidth.current);
  };
  const increment = () => {
    scroll("increment");
    setIndex();
  };
  const decrement = () => {
    scroll("decrement");
    setIndex();
  };

  if (currentIndex) step.current = currentIndex * elementWidth.current;
  sliderWrapper?.current?.scrollTo(scrollOptions(step.current));

  return { increment, decrement };
};
