import React, { useRef, DOMAttributes } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { ReactComponent as CachedIcon } from "assets/icons/cached.svg";
import { ReactComponent as FileCopyIcon } from "assets/icons/file-copy.svg";
import { notifyBarManager, InputAdornment } from "components";
import InputBase, {
  BaseInputProps as InputBaseProps,
} from "components/Input/InputBase";
import classes from "./style.module.scss";
import { track } from "utils";

export interface CopyInputProps extends InputBaseProps {
  label?: string;
  handleRotate?: () => void;
  withoutCopy?: boolean;
  InputAdornment?: JSX.Element;
}

const CopyInput = React.forwardRef(
  (
    {
      id,
      label,
      className,
      handleRotate,
      withoutCopy,
      InputAdornment: AdornmentComponent,
      ...props
    }: CopyInputProps,
    ref,
  ) => {
    const inputRef = useRef<HTMLInputElement>(null);
    const intl = useGetLocale();

    const handleCopy: DOMAttributes<HTMLButtonElement>["onClick"] = (event) => {
      try {
        if (inputRef.current) {
          inputRef.current.select();
          document.execCommand("copy");
          notifyBarManager.success({
            timeOut: 2000,
            message: intl.formatMessage({ id: "copied" }),
          });

          event.currentTarget.focus();
        }
      } catch (error) {
        notifyBarManager.error({
          timeOut: 2000,
          message: intl.formatMessage({ id: "unrecognizedError" }),
        });
        track()?.errors(error, {
          custom: {
            place: "handleCopy",
          },
        });
      }
    };

    return (
      <div className={classes.formInput}>
        {label && (
          <label htmlFor={id} className={classes.formInputLabel}>
            {label}
          </label>
        )}
        <InputBase
          {...props}
          ref={inputRef}
          className={clsx(className, classes.inputRoot)}
          classes={{ root: classes.textOverflow, input: classes.input }}
          endAdornment={
            <InputAdornment
              position="end"
              className={classes.formInputAdornment}>
              {handleRotate && (
                <button
                  className={clsx(
                    classes.inputRotateButton,
                    classes.iconButton,
                  )}
                  onClick={handleRotate}>
                  <CachedIcon />
                </button>
              )}
              {document.queryCommandSupported("copy") && !withoutCopy && (
                <button
                  className={clsx(classes.inputCopyButton, classes.iconButton)}
                  onClick={handleCopy}>
                  <FileCopyIcon />
                </button>
              )}
              {AdornmentComponent || null}
            </InputAdornment>
          }
        />
      </div>
    );
  },
);

export default CopyInput;
