import React, { FC, Suspense } from "react";

import LazyComponentLoader from "config/LazyComponentLoader";
import ProgressFallback from "components/fallbacks/Progress";

import { Dialog } from "containers/PopUps";
import { modalsLoaderColor } from "sass/styleVariables";

import css from "./style.module.scss";

const Content = React.lazy(() =>
  LazyComponentLoader(import("./components/Content")),
);

interface Props {
  onClose: () => void;
  isOpen: boolean;
}

const SoundModals: FC<Props> = ({ onClose, isOpen }) => {
  if (!isOpen) {
    return null;
  }

  return (
    <Dialog
      onClose={onClose}
      isOpen={isOpen}
      paper={{
        className: css.root,
      }}>
      <Suspense
        fallback={
          <ProgressFallback
            color={modalsLoaderColor}
            circularWidth={8}
            size={90}
          />
        }>
        <Content onClose={onClose} />
      </Suspense>
    </Dialog>
  );
};

export default SoundModals;
