import React, { FC, useCallback, useMemo } from "react";
import clsx from "clsx";

import { Switch } from "components";

import css from "./style.module.scss";
import { SwitchProps } from "components";

interface Props {
  switchHandleChange: (checked: boolean) => void;
  isChecked: boolean;
  text: {
    on: string;
    off: string;
    color?: string;
  };

  className?: string;

  classes?: {
    root?: string;
    text?: string;
    switch?: SwitchProps["classes"];
  };

  toggleSwitchPosition?: string;
}

const SwitchBox: FC<Props> = ({
  switchHandleChange,
  isChecked,
  text,
  className,
  classes,
  toggleSwitchPosition,
}) => {
  const handleOnDisable = () => {
    switchHandleChange(false);
  };
  const handleOnEnable = () => {
    switchHandleChange(true);
  };

  const textStyle = useMemo(
    () => ({
      color: text.color,
    }),
    [text.color],
  );

  return (
    <div className={clsx(css.switcher, classes?.root)}>
      <p
        style={textStyle}
        className={clsx(css.text, classes?.text, {
          [css.disableText]: isChecked,
        })}
        onClick={handleOnDisable}>
        {text.off}
      </p>
      <Switch
        checked={isChecked}
        onChange={(event) => switchHandleChange(event.target.checked)}
        className={className}
        togglePosition={toggleSwitchPosition}
        classes={{
          ...classes?.switch,
          track: clsx(css.switchTrack, classes?.switch?.track),
          root: clsx(css.switch, classes?.switch?.root),
          toggle: clsx(css.switchToggle, classes?.switch?.toggle),
        }}
      />
      <p
        style={textStyle}
        className={clsx(css.text, classes?.text, {
          [css.disableText]: !isChecked,
        })}
        onClick={handleOnEnable}>
        {text.on}
      </p>
    </div>
  );
};

export default SwitchBox;
