import React, { FunctionComponent } from "react";
import clsx from "clsx";

import { useWindowSize } from "providers";

import { Dialog } from "containers/PopUps";
import { OnClosePopUpCallBack } from "containers/PopUps/types";
import { useModalLocation } from "hooks";

import css from "./style.module.scss";

export interface ModalProps {
  className?: string;
  open: boolean;
  width?: number | string;
  height?: number | string;
  maxHeight?: number | string;
  onClose?: OnClosePopUpCallBack;
}

const Modal: FunctionComponent<ModalProps> = ({
  children,
  className,
  height,
  maxHeight,
  width,
  open,
  onClose,
}) => {
  const { width: screenWidth } = useWindowSize();
  const isSm = screenWidth < parseInt(css.mobileScreen);
  const { close } = useModalLocation();

  const closeModal = (e?: Event) => {
    if (e) {
      e.stopPropagation();
    }

    close();
  };

  const handleClose: OnClosePopUpCallBack = (...args) => {
    if (onClose) {
      onClose(...args);
    }

    closeModal();
  };

  return (
    <Dialog
      isOpen={open}
      paper={{
        className: clsx(css.paper, className),
        style: {
          height: height || "auto",
          maxHeight: maxHeight || height || "calc(100% - 64px)",
          width: width || 500,
          maxWidth: width || 500,
        },
      }}
      onClose={handleClose}
      withCloseIcon
      fullScreen={isSm}>
      <>{children}</>
    </Dialog>
  );
};

export default Modal;
