export const getAvatar = (userName?: string, avatar?: string | null) => {
  if (avatar) {
    return avatar;
  }

  if (userName) {
    userName = userName.toLowerCase();
    const chars = "abcdefghijklmnopqrstuvwxyz0123456789";
    const fixId = (id: number, circle = 0) => {
      if (id < 3 || id > 100) {
        let newIdValue = ((userName?.length || 3) + (id + 1)) % 101;
        if (circle > 5) {
          return 13;
        }

        id = fixId(newIdValue, circle + 1);
      }

      return id;
    };

    let imageId =
      (userName || "null").split("").reduce((acm, letter) => {
        const value = chars.indexOf(letter);
        return acm + value;
      }, 0) % 101;

    return `https://cdn.pasino.com/avatars/${fixId(imageId)}.jpg`;
  }

  return "";
};
