import React, {
  FC,
  DetailedHTMLProps,
  HTMLAttributes,
  forwardRef,
} from "react";
import clsx from "clsx";

import styles from "./style.module.scss";

interface Props
  extends DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement> {
  position: "start" | "end";
  className?: string;
}

const InputAdornment = forwardRef<HTMLDivElement, Props>(
  ({ children, position, ...props }, ref) => {
    return (
      <div
        ref={ref}
        {...props}
        className={clsx(
          styles.adornmentRoot,
          {
            [styles.start]: position === "start",
          },
          {
            [styles.end]: position === "end",
          },
          props.className,
        )}>
        {children}
      </div>
    );
  },
);

export default InputAdornment;
