import React, {
  forwardRef,
  useEffect,
  useImperativeHandle,
  useRef,
  useState,
} from "react";

import { GetCoinImg, Input, InputAdornment } from "components";
import { useGetLocale } from "providers";
import { validateNumbers } from "utils";
import { useCoins } from "hooks";

import { ReactComponent as AllInclusiveIcon } from "assets/icons/all-inclusive.svg";
import css from "./style.module.scss";
import { Inputs } from "./types";
import clsx from "clsx";

export * from "./types";

interface Values {
  numberOfBets?: number;
  stopOnLoss?: number;
  stopOnProfit?: number;
}

interface Props {
  value?: Values;
  setValue?: (value: Values) => void;

  disable?: boolean;
}

export const AutoLimits = forwardRef<Inputs, Props>(
  ({ setValue = () => {}, value, disable }, ref) => {
    const intl = useGetLocale();
    const { showInUSD, selectedCoin } = useCoins();
    const [isInfinity, setIsInfinity] = useState(true);

    const inputs = useRef<Inputs>({
      numberOfBets: null,
      stopOnLoss: null,
      stopOnProfit: null,
    });

    const startAdornment = (
      <InputAdornment position="start">
        <GetCoinImg className={css.coinImg} />
        {showInUSD && selectedCoin?.coin !== "GEM" ? (
          <span className={css.usdAdornment}>$</span>
        ) : null}
      </InputAdornment>
    );

    useImperativeHandle(ref, () => inputs.current);

    useEffect(() => {
      if (!value) return;

      Object.entries(inputs.current).forEach((entries) => {
        const [key, element] = entries as [
          keyof Values,
          HTMLInputElement | null,
        ];

        const selectedValue: number | undefined = value[key];
        if (selectedValue !== undefined && element) {
          element.value = `${selectedValue}`;
        }

        if (key === "numberOfBets") {
          if (!element?.value) setIsInfinity(true);
          else setIsInfinity(false);
        }
      });
    }, [value]);

    return (
      <div className={clsx(css.root, disable && css.disable)}>
        <Input
          label={intl.formatMessage({ id: "dice.auto.roll.numberOfBets" })}
          classes={{
            input: css.input,
            root: css.inputRoot,
          }}
          labelClassName={css.label}
          endAdornment={
            isInfinity ? (
              <InputAdornment position="end">
                <AllInclusiveIcon className={css.infinityImg} />
              </InputAdornment>
            ) : null
          }
          type="number"
          min={0}
          ref={(node) => (inputs.current.numberOfBets = node)}
          onBlur={({ target }) => {
            const numberOfBets = Number(target.value);

            if (!numberOfBets) {
              setIsInfinity(true);
            } else {
              setIsInfinity(false);
            }

            setValue({
              ...value,
              numberOfBets,
            });
          }}
        />
        <Input
          classes={{
            input: css.input,
            root: css.inputRoot,
          }}
          labelClassName={css.label}
          label={intl.formatMessage({
            id: "crashInterface.autoBet.stopOnProfit",
          })}
          startAdornment={startAdornment}
          onChange={({ target }) => {
            target.value = validateNumbers(target.value);
          }}
          ref={(node) => (inputs.current.stopOnProfit = node)}
          onBlur={({ target }) => {
            setValue({
              ...value,
              stopOnProfit: Number(target.value),
            });
          }}
        />
        <Input
          classes={{
            input: css.input,
            root: css.inputRoot,
          }}
          labelClassName={css.label}
          label={intl.formatMessage({
            id: "crashInterface.autoBet.stopOnLoss",
          })}
          startAdornment={startAdornment}
          onChange={({ target }) => {
            target.value = validateNumbers(target.value);
          }}
          ref={(node) => (inputs.current.stopOnLoss = node)}
          onBlur={({ target }) => {
            setValue({
              ...value,
              stopOnLoss: Number(target.value),
            });
          }}
        />
      </div>
    );
  },
);
