import React, { FC } from "react";
import clsx from "clsx";

import styles from "./style.module.scss";
import SelectItemBase, {
  Props as SelectItemBaseProps,
} from "../SelectItemBase";

interface Props extends SelectItemBaseProps {
  className?: string;
  label: string;
  key?: any;
}

const SelectItem: FC<Props> = ({ value, className, label, key, ...props }) => {
  return (
    <SelectItemBase
      value={value}
      className={clsx(styles.root, className)}
      {...props}>
      <span key={key}>{label}</span>
    </SelectItemBase>
  );
};

export default SelectItem;
