import { useGetLocale } from "providers/Locale/hook";
import React, { FC } from "react";

interface Propps {
  id: string;
}

export const FormatMessage: FC<Propps> = ({ id }) => {
  const intl = useGetLocale();
  return (
    <>
      {intl.formatMessage({
        id,
      })}
    </>
  );
};
