import React, { useEffect, useRef, useState } from "react";

import { Icon, ClickAwayListener } from "components";
import { setCursor, stringInsert } from "utils";

import { useMessages, SystemTypes } from "components/Chat/chatContext";
import {
  Input,
  MediaModal,
  ImperativeValues,
  ScrollToBottom,
} from "./components";
import { getStringFromDivChildren } from "./utils";
import css from "./style.module.scss";
import { useModalLocation } from "hooks";

export const Write = () => {
  const { createMessage, chatFunctions } = useMessages();
  const [isOpenMedia, setIsOpenMedia] = useState(false);
  const [inputString, setInputString] = useState("");
  const { pushHistoryTo } = useModalLocation("rain");
  const cursorPositionRef = useRef<number | null>(null);
  const inputRoot = useRef<HTMLDivElement>(null);
  const imperativeInput = useRef<ImperativeValues | null>(null);

  const sendMessage = (value = inputString) => {
    if (!value) return;

    createMessage(value, "text");
    setInputString("");
  };

  const handleRain = () => {
    pushHistoryTo({});
  };

  useEffect(() => {
    chatFunctions.current.setSystemMessage = (
      value: string,
      type: SystemTypes,
    ) => {
      let text = "";
      switch (type) {
        case "ban":
          text = `/ban ${value}`;
          break;

        case "remove":
          text = `/remove ${value}`;
          break;

        case "contact":
          text = `@${value}, `;
          break;

        case "clear":
          text = "";
          return;

        default:
          break;
      }
      setInputString(text);

      setTimeout(() => {
        const { renderTextDiv } = imperativeInput.current || {};
        if (renderTextDiv?.current) {
          renderTextDiv.current.focus();
          setCursor(
            renderTextDiv.current,
            renderTextDiv.current.childNodes.length,
          );
        }
      }, 0);
    };
  }, []);

  return (
    <ClickAwayListener onClickAway={() => setIsOpenMedia(false)}>
      <div className={css.root}>
        <ScrollToBottom />

        <MediaModal
          isOpen={isOpenMedia}
          anchorEl={inputRoot.current}
          onClose={() => {
            cursorPositionRef.current = null;
            setIsOpenMedia(false);
          }}
          sendGif={(gif) => createMessage(gif, "gif")}
          smileEnter={(emoji) => {
            setTimeout(() => {
              setInputString((oldString) => {
                const oldValues = {
                  string: oldString,
                  cursor: 0,
                };
                if (imperativeInput.current?.renderTextDiv.current) {
                  const { cursorPosition, string } = getStringFromDivChildren(
                    imperativeInput.current.renderTextDiv.current,
                    true,
                  );
                  oldValues.string = string;

                  if (cursorPositionRef.current === null) {
                    cursorPositionRef.current = cursorPosition;
                    oldValues.cursor = cursorPosition;
                  } else {
                    oldValues.cursor = cursorPositionRef.current;
                  }
                }

                cursorPositionRef.current = oldValues.cursor + emoji.length;

                return stringInsert(oldValues.string, emoji, oldValues.cursor);
              });
            }, 10);
          }}
        />
        <div className={css.inputWrapper} ref={inputRoot}>
          <Icon
            aria-label="rain"
            className={css.rainIcon}
            variant="chatRain"
            fontSize={20}
            onClick={handleRain}
          />

          <Input
            value={inputString}
            clearCursorPosition={() => {
              cursorPositionRef.current = null;
            }}
            sendMessage={sendMessage}
            setOpenMedia={setIsOpenMedia}
            ref={imperativeInput}
          />
        </div>
      </div>
    </ClickAwayListener>
  );
};
