const tenorURL = "https://api.pasino.com/gif";

const searchGif = async (
  searchKey: string | null,
  next: string | null,
  locale: string,
) => {
  let endPoint = "trending";

  if (searchKey) {
    endPoint = `search/${searchKey}`;
  }
  if (next) {
    endPoint += `/${next}`;
  }
  const result = await callTenor(endPoint);
  return result;
};

const callTenor = async (point: string) => {
  const url = `${tenorURL}/${point}`;
  const res = await fetch(url);
  const data = await res.json();
  return data;
};

export { searchGif };
