import React, { useState } from "react";
import clsx from "clsx";

import { Icon, IconProps, InputAdornment } from "components";

import InputBase, { BaseInputProps as InputBaseProps } from "./InputBase";
import { InputProps } from "./type";
import styles from "./style.module.scss";

const FormInput = React.forwardRef<HTMLInputElement, InputProps>(
  (
    {
      id,
      label,
      labelClassName,
      type,
      value,
      onChange,
      helperText,
      error,
      className,
      rootClass,
      ...props
    },
    ref,
  ) => {
    const [showPassword, setShowPassword] = useState(false);

    const handleClickShowPassword: IconProps["onClick"] = (event) => {
      setShowPassword(!showPassword);
    };

    const handleMouseDownPassword: IconProps["onMouseDown"] = (event) => {
      if (event.nativeEvent.button === 0) {
        event.preventDefault();
        setShowPassword(!showPassword);
      }
    };

    const handleChange: InputBaseProps["onChange"] = (event) => {
      if (onChange) {
        onChange(event);
      }
    };

    return (
      <div className={clsx(styles.root, rootClass)}>
        {label && (
          <label className={clsx(styles.label, labelClassName)}>{label}</label>
        )}
        <InputBase
          ref={ref as any}
          id={id}
          error={error}
          value={value}
          onChange={handleChange}
          className={clsx(
            className,
            styles.formInputField,
            type === "password" && styles.passwordFormInputField,
          )}
          type={type === "password" && showPassword ? "text" : type}
          endAdornment={
            type === "password" ? (
              <InputAdornment
                position="end"
                className={styles.formInputAdornment}>
                <Icon
                  aria-label="toggle password visibility"
                  className={styles.formInputAdornmentIcon}
                  onClick={handleClickShowPassword}
                  onMouseDown={handleMouseDownPassword}
                  fontSize={28}
                  variant={showPassword ? "visibilityOff" : "visibility"}
                />
              </InputAdornment>
            ) : null
          }
          {...props}
        />
      </div>
    );
  },
);

export default FormInput;
