import { CoinData, CoinPriceList } from "hooks";

export const getCoinPriceFromData = (coins: CoinData[]): CoinPriceList => {
  return coins?.reduce((acm, { name, symbol, price }) => {
    return {
      ...acm,
      [symbol]: {
        name: name,
        price: price,
      },
    };
  }, {});
};
