import React, { FC } from "react";
import clsx from "clsx";

import { createFC, TableTHeadProps } from "components";

import css from "./style.module.scss";

export const THead: FC<TableTHeadProps> = ({
  values = [],
  components,
  classes,
}) => {
  const Cell = createFC(components?.cell);
  const Row = createFC(components?.row);
  const Head = createFC(components?.head);

  return (
    <thead className={clsx(css.head, classes?.thead)}>
      <Head>
        <tr className={clsx(css.tr, classes?.tr)}>
          <Row>
            {values.map((value, i) => (
              <th className={clsx(css.th, classes?.th)} key={value}>
                <Cell> {value}</Cell>
              </th>
            ))}
          </Row>
        </tr>
      </Head>
    </thead>
  );
};
