import React, { useState } from "react";
import SwipeableViews from "react-swipeable-views";

import { Popper } from "components";
import { isKeyboardEvent } from "utils";
import { useEventListener } from "hooks";

import { SwitchBar, Tenor, Emoji } from "./components";
import css from "./style.module.scss";

interface Props {
  anchorEl: HTMLDivElement | null;
  sendGif: (value: string) => void;
  smileEnter: (value: string) => void;
  isOpen: boolean;
  onClose: () => void;
}

export const MediaModal = ({
  anchorEl,
  isOpen,
  onClose,
  sendGif,
  smileEnter,
}: Props) => {
  const [isGifOpen, setIsGifOpen] = useState(false);

  const onChangeSwitch = (val: boolean) => {
    setIsGifOpen(val);
  };

  useEventListener("keydown", (event) => {
    if (!isKeyboardEvent(event)) return;

    if (event.code === "Escape" || event.key === "Escape") {
      event.preventDefault();
      onClose();
    }
  });

  if (!isOpen) {
    return null;
  }

  return (
    <Popper
      fixed
      recalculate
      isOpen={isOpen}
      scrollOverWindow
      anchor={anchorEl}
      className={css.root}
      backgroundColor="#000000">
      <SwipeableViews
        className={css.content}
        disabled
        disableLazyLoading={false}
        index={isGifOpen ? 1 : 0}>
        <Emoji smileEnter={smileEnter} />
        <Tenor sendGif={sendGif} onClose={onClose} />
      </SwipeableViews>
      <SwitchBar value={isGifOpen} onChange={onChangeSwitch} />
    </Popper>
  );
};
