import React, { useEffect, useState } from "react";
import clsx from "clsx";

import { CoinName, coinsManager, ManagerListenerValues } from "hooks";
import css from "./style.module.scss";
import { track } from "utils";

interface Props {
  className?: string;
  coin?: CoinName;
  style?: React.CSSProperties | {};
  draggable?: boolean;
  staticCoin?: boolean;
  onLoad?: () => void;
}
const noStyles = {};

interface RenderedCoin {
  url: string;
  name: string;
}

export const generateCoinUrl = (coin: string) =>
  `https://cdn.pasino.com/coins/${coin.toLocaleLowerCase()}.png`;
export const GetCoinImg = ({
  className,
  coin,
  style,
  draggable = false,
  staticCoin,
  onLoad,
}: Props) => {
  const [renderedCoin, setRenderedCoin] = useState<RenderedCoin>({
    url:
      (coin && generateCoinUrl(coin)) ||
      coinsManager.selectedCoin?.image ||
      generateCoinUrl("btc"),
    name: coin || coinsManager.selectedCoin?.coin || "btc",
  });

  useEffect(() => {
    const setSelectedCoinAsRenderedCoin = ({
      selectedCoin,
    }: ManagerListenerValues) => {
      if (selectedCoin) {
        setRenderedCoin({
          name: selectedCoin.coin,
          url: selectedCoin.image,
        });
      } else {
        setRenderedCoin({ name: "btc", url: generateCoinUrl("btc") });
      }
    };

    let removeListener = () => {};

    try {
      if (!coin) {
        removeListener = coinsManager.listener(setSelectedCoinAsRenderedCoin);
      } else {
        setRenderedCoin({ name: coin, url: generateCoinUrl(coin) });
      }
    } catch (err) {
      console.error("show coin error");
      track()?.errors(err, {
        custom: {
          place: "show coin error",
        },
      });
    }

    return () => {
      coinsManager.closeListener(setSelectedCoinAsRenderedCoin);
      removeListener();
    };
  }, [coin]);

  return (
    <img
      draggable={draggable}
      onLoad={onLoad}
      src={renderedCoin.url}
      alt={renderedCoin.name}
      className={clsx(css.btcIcon, staticCoin && css.static, {
        [className || ""]: !!className,
      })}
      style={style || noStyles}
    />
  );
};
