import React, { FC } from "react";
import clsx from "clsx";
import { Button, ButtonProps } from "components";

import css from "./style.module.scss";

export const PageButton: FC<ButtonProps> = ({
  children,
  className,
  ...props
}) => {
  return (
    <Button className={clsx(css.root, className)} {...props}>
      {children}
    </Button>
  );
};
