import React, {
  FC,
  Fragment,
  ReactElement,
  RefCallback,
  MutableRefObject,
  useEffect,
  useRef,
  cloneElement,
} from "react";

const eventTypeMapping = {
  click: "onClick",
  mousedown: "onMouseDown",
  mouseup: "onMouseUp",
  touchstart: "onTouchStart",
  touchend: "onTouchEnd",
};

interface Props {
  children: ReactElement;
  clickEvent?: keyof typeof eventTypeMapping;
  onClickAway: (event: Event) => void;
}

const ClickAwayListener: FC<Props> = ({
  children,
  clickEvent = "click",
  onClickAway,
}) => {
  const elementRef = useRef<HTMLElement | null>(null);
  const bubbledEventTarget = useRef<EventTarget | null>(null);
  const isMounted = useRef(false);
  const isSyntheticEvent = useRef(false);

  useEffect(() => {
    isMounted.current = true;

    return () => {
      isMounted.current = false;
    };
  }, []);

  useEffect(() => {
    const clickHandler = (event: MouseEvent | TouchEvent): void => {
      if (!isMounted.current) return;

      const isInsideReactTree = isSyntheticEvent.current;
      isSyntheticEvent.current = false;

      const insideDom =
        (elementRef.current &&
          elementRef.current.contains(event.target as Node)) ||
        bubbledEventTarget.current === event.target;

      if (insideDom || isInsideReactTree) return;

      onClickAway(event);
    };

    document.addEventListener(clickEvent, clickHandler);
    return () => document.removeEventListener(clickEvent, clickHandler);
  }, [clickEvent, onClickAway]);

  const handleBubbledEvents = (eventName: string) => {
    return (event: Event) => {
      isSyntheticEvent.current = true;

      bubbledEventTarget.current = event.target;
      const eventHandler = children?.props[eventName];
      if (eventHandler) eventHandler(event);
    };
  };

  const handleChildRef = (childRef: HTMLElement) => {
    elementRef.current = childRef;

    const { ref } = children as typeof children & {
      ref: RefCallback<HTMLElement> | MutableRefObject<HTMLElement>;
    };

    if (typeof ref === "function") {
      ref(childRef);
    } else if (ref) {
      ref.current = childRef;
    }
  };

  const eventName = eventTypeMapping[clickEvent];

  return (
    <Fragment>
      {cloneElement(children, {
        ref: handleChildRef,
        [eventName]: handleBubbledEvents(eventName),
      })}
    </Fragment>
  );
};

export default ClickAwayListener;
