import React from "react";
import clsx from "clsx";
import { ReactComponent as SunIcon } from "assets/icons/brightness7.svg";
import { ReactComponent as MoonIcon } from "assets/icons/brightness4.svg";

import { useWhiteTheme } from "providers/WhiteTheme";

import css from "./style.module.scss";
interface Props {
  width?: number;
  height?: number;
  className?: string;
}

const ThemeChange = ({ height = 40, width = 40, className }: Props) => {
  const { isWhiteThemeActive, setIsWhiteThemeActive } = useWhiteTheme();

  const handleChangeTheme = () => {
    setIsWhiteThemeActive(!isWhiteThemeActive);
  };

  const styles = {
    height,
    width,
  };

  return (
    <button
      className={clsx(css.root, className)}
      onClick={handleChangeTheme}
      style={styles}>
      <span className={clsx(css.item, { [css.disable]: isWhiteThemeActive })}>
        <MoonIcon className={css.icon} style={styles} />
      </span>

      <span className={clsx(css.item, { [css.disable]: !isWhiteThemeActive })}>
        <SunIcon className={css.icon} style={styles} />
      </span>
    </button>
  );
};

export default ThemeChange;
