import React, {
  CSSProperties,
  ImgHTMLAttributes,
  useRef,
  useState,
} from "react";
import clsx from "clsx";

import classes from "./style.module.scss";
import { getAvatar } from "./utils";
export { getAvatar } from "./utils"; //export

export interface AvatarProps {
  userName?: string;
  style?: CSSProperties;
  className?: string;
  avatar?: string | null;
  component?: (props: ImgHTMLAttributes<HTMLImageElement>) => JSX.Element;
}

const Avatar = ({
  userName,
  style,
  className,
  avatar,
  component: Component,
}: AvatarProps) => {
  const [loaded, setLoaded] = useState(false);
  const rendered = useRef(false);

  const avatarUrl = getAvatar(userName, avatar);

  const onLoaded = () => {
    setLoaded(true);
    rendered.current = true;
  };

  const imgProps = {
    style,
    alt: "avatar",
    src: avatarUrl,
    onLoad: onLoaded,
    className: clsx(classes.image, {
      [classes.opacity]: !(!loaded && !rendered.current),
    }),
  };

  return (
    <div className={clsx(classes.wrapper, className)}>
      {avatarUrl &&
        (Component ? <Component {...imgProps} /> : <img {...imgProps} />)}
    </div>
  );
};

export default Avatar;
