import { useLocalStorage } from "hooks";
import React, { useState, FunctionComponent } from "react";

import Context from "./Context";

const HotKeysProvider: FunctionComponent = ({ children }) => {
  const [isEnable] = useLocalStorage("isHotkeysEnabled");

  const [isHotkeyActive, setIsHotkeyActive] = useState(
    isEnable ? JSON.parse(isEnable) : false,
  );

  const [isHotKeysPaused, setIsHotKeysPaused] = useState(false);

  return (
    <Context.Provider
      value={{
        isHotkeyActive,
        isHotKeysPaused,
        setIsHotkeyActive,
        setIsHotKeysPaused,
      }}>
      {children}
    </Context.Provider>
  );
};

export default HotKeysProvider;
