import React from "react";
import { Link, LinkProps, useLocation } from "react-router-dom";
import { LocationDescriptorObject, History } from "history";
import clsx from "clsx";

import { getModalPath } from "hooks";
import { ParamKeys } from "config/routes";

import classes from "./style.module.scss";

export interface SearchValue<T> {
  name: string;
  value: T;
}

export interface LinkWithSearchProps extends Omit<LinkProps, "to"> {
  to?: LocationDescriptorObject<any>;
  search: SearchValue<ParamKeys>;
  searchParams?: { [key: string]: string };
}

const LinkWithSearch = React.forwardRef(
  (
    {
      children,
      search,
      searchParams,
      to,
      className,
      ...props
    }: LinkWithSearchProps,
    ref,
  ) => {
    const location: any = useLocation();
    const pathname = getModalPath({ search, searchParams });
    let background =
      location.state && location.state.background
        ? location.state.background
        : location;
    if (background.pathname.split("/").includes("page")) {
      background = { hash: "", pathname: "/", search: "", state: undefined };
    }
    const isReplace =
      search.name === "page" || to?.pathname?.split("/").includes("page");

    const linkTo =
      to && !search
        ? { state: { background }, ...to }
        : { pathname, state: { background } };

    return (
      <Link
        {...props}
        className={clsx(classes.link, className)}
        ref={ref as any}
        to={linkTo}
        replace={isReplace}>
        {children}
      </Link>
    );
  },
);

export * from "./utils";
export default LinkWithSearch;
