import { chatSocket } from "utils";

interface Send {
  type: string;
  user?: string;
  time?: string;
  message_id?: string;
}

export const getSystemMessage = (matchData: string[]) => {
  const [, method, data, time] = matchData;

  const send: Send = {
    type: "",
  };

  if (method === "/ban" || method === "/unban") {
    send.type = method === "/ban" ? "ban_user" : "unban_user";
    send.user = data;
    if (time) {
      send.time = time;
    }
  } else if (method === "/remove") {
    send.type = "remove_message";
    send.message_id = data;
  }

  chatSocket.send(send);
};
