import React, { Component, ErrorInfo, Suspense } from "react";
import { BrowserRouter } from "react-router-dom";
import "url-search-params-polyfill";

import { track } from "utils";

import FallbackPage from "components/fallbacks/Home";
import LazyComponentLoader from "config/LazyComponentLoader";

const AppComponent = React.lazy(() =>
  LazyComponentLoader(import("./AppComponent")),
);

class AppWrapper extends Component {
  state = { hasError: false };

  static getDerivedStateFromError() {
    return { hasError: true };
  }

  componentDidCatch(error: Error, info: ErrorInfo) {
    const description = `
${error.stack}
App Crash
Component Stack: ${info.componentStack}
localStorage: ${JSON.stringify(localStorage)}`;

    track()?.errors(description);
  }

  render() {
    return (
      <BrowserRouter>
        <Suspense fallback={<FallbackPage />}>
          <AppComponent hasError={this.state.hasError} />
        </Suspense>
      </BrowserRouter>
    );
  }
}

export default AppWrapper;
