import React, { FC } from "react";

import { ReactComponent as SpeakerIcon } from "assets/icons/speaker.svg";
import { ReactComponent as GraphIcon } from "assets/icons/graphStat.svg";
import { ReactComponent as LightningIcon } from "assets/icons/lightning.svg";
import { ReactComponent as HelpIcon } from "assets/icons/help-mui.svg";
import { ReactComponent as KeyboardIcon } from "assets/icons/keyboard.svg";

import OptionWrapper from "./OptionElement";
import css from "./style.module.scss";
import clsx from "clsx";

interface Props {
  liveStatOnclick: () => void;
  hotKeysOnClick: () => void;
  soundOnClick?: () => void;

  bettingInformationOnClick?: () => void;
  turboDice?: () => void;

  withSound?: boolean;

  className?: string;
}

export const OptionsButton: FC<Props> = ({
  liveStatOnclick,
  hotKeysOnClick,
  soundOnClick,

  withSound = false,
  bettingInformationOnClick,
  turboDice,

  className,
}) => {
  return (
    <div className={clsx(css.iconButtonsWrapper, className)}>
      {turboDice && (
        <OptionWrapper
          callBack={turboDice}
          Icon={LightningIcon}
          color="green"
        />
      )}
      {withSound && (
        <OptionWrapper callBack={soundOnClick} Icon={SpeakerIcon} />
      )}

      <OptionWrapper callBack={liveStatOnclick} Icon={GraphIcon} />
      <OptionWrapper callBack={hotKeysOnClick} Icon={KeyboardIcon} />

      {bettingInformationOnClick ? (
        <OptionWrapper callBack={bettingInformationOnClick} Icon={HelpIcon} />
      ) : null}
    </div>
  );
};

export default OptionsButton;
