import React from "react";
import { useHistory, useLocation } from "react-router-dom";
import { useGetLocale } from "providers";

import { useLocalStorage } from "hooks";
import { Button } from "components";

import { HeaderButtonOption } from "../..";
import css from "../../style.module.scss";
import clsx from "clsx";

interface Props {
  options: HeaderButtonOption[];
  onChange: (value: number) => void;
  selectedIndex: number;
  withFairness?: boolean;
  smallButton?: boolean;
}
const permittedPaths = ["/crash", "/fortune-quest"];

export const Desktop = ({
  options,
  onChange,
  selectedIndex,
  withFairness,
  smallButton,
}: Props) => {
  const intl = useGetLocale();
  const [token] = useLocalStorage("token");
  const location = useLocation();
  const history = useHistory();

  const isPermittedPath = permittedPaths.includes(location.pathname);

  const handleFairness = () => {
    history.push("/page/fairness", { background: location });
  };

  return (
    <div className={clsx(css.wrapper, css.wrapper_small)}>
      {options?.map(({ key, label }, index) => {
        if (key === "history" && !isPermittedPath) {
          return null;
        }

        if (key === "yourBets" && !token) {
          return null;
        }

        return (
          <Button
            key={key}
            className={clsx(css.button, smallButton && css.button_small)}
            onClick={() => onChange(index)}
            transparent={selectedIndex !== index}>
            {label}
          </Button>
        );
      })}
      {withFairness && token && (
        <Button
          className={clsx(css.button, smallButton && css.button_small)}
          onClick={handleFairness}
          transparent={selectedIndex !== options?.length}>
          {intl.formatMessage({ id: "gameTemplate.statistics.fairness" })}
        </Button>
      )}
    </div>
  );
};
