import React, { FC, TouchEvent, useRef, useEffect } from "react";
import clsx from "clsx";

import { PopUpRoot } from "containers/PopUps/";
import { useTouchPreventDefault } from "components/LiveStats/components/Header/useTouchPreventDefault";

import styles from "./style.module.scss";

export interface DrawerProps {
  isOpen: boolean;
  transitionTimeSeconds?: number;
  onClose: () => void;
  onOpen: () => void;
  swipeTailWidth?: number;
}

interface Point {
  x: number;
  y: number;
}

const calculateAngleBetweenPoints = (firstPoint: Point, secondPoint: Point) => {
  const tangent = Math.abs(
    (firstPoint.y - secondPoint.y) / (firstPoint.x - secondPoint.x),
  );
  return Math.atan(tangent) * (180 / Math.PI);
};

const calculateDistanceBetweenTwoPoints = (
  firstPoint: Point,
  secondPoint: Point,
) => {
  return Math.sqrt(
    Math.pow(secondPoint.x - firstPoint.x, 2) +
      Math.pow(secondPoint.y - firstPoint.y, 2),
  );
};

const SwipeableDrawer: FC<DrawerProps> = ({
  children,
  isOpen,
  transitionTimeSeconds = 0.3,
  swipeTailWidth = 20,
  onClose,
  onOpen,
}) => {
  const startTouchCoordinate = useRef<Point | null>(null);
  const lastMoveTouchCoordinate = useRef<number | null>(null);
  const drawerRef = useRef<HTMLDivElement | null>(null);
  const tailRef = useRef<HTMLDivElement | null>(null);
  const blockInteraction = useRef(false);
  const isCloseDrawer = useRef(false);
  const isFirstAngleCheck = useRef(false);

  useEffect(() => {
    if (!isOpen) blockInteraction.current = false;
  }, [isOpen]);

  const moveDrawer = (
    translateValue: number | string,
    withTransition?: boolean,
  ) => {
    if (!drawerRef.current) return;

    drawerRef.current.style.transition = withTransition
      ? `transform ${transitionTimeSeconds}s`
      : "none";
    drawerRef.current.style.transform = `translateX(${translateValue}${
      typeof translateValue === "string" ? "" : "px"
    })`;
  };

  const touchMoveHandler = (
    event: TouchEvent<HTMLDivElement>,
    ltr?: boolean,
  ) => {
    if (startTouchCoordinate.current !== null && !blockInteraction.current) {
      const { clientX, clientY } = event.targetTouches[0];

      if (
        calculateAngleBetweenPoints(startTouchCoordinate.current, {
          x: clientX,
          y: clientY,
        }) > 30 &&
        !isFirstAngleCheck.current
      ) {
        isCloseDrawer.current = false;
        return;
      }
      isFirstAngleCheck.current = true;

      const translateValue = clientX - startTouchCoordinate.current.x;

      isCloseDrawer.current =
        (lastMoveTouchCoordinate.current as number) > clientX ||
        (Math.abs(translateValue) > event.currentTarget.clientWidth / 2 &&
          !ltr);

      lastMoveTouchCoordinate.current = clientX;

      if (translateValue < 0) {
        moveDrawer(translateValue);
      }
    }
  };

  const onTouchEndHandler = (event: TouchEvent<HTMLDivElement>) => {
    if (!(drawerRef.current && drawerRef.current.style.transform)) {
      return;
    }
    const currentOffset = Number(
      drawerRef.current.style.transform.replace(/[^0-9]+/g, ""),
    );

    let isOneClick = false;
    if (startTouchCoordinate.current) {
      const distance = calculateDistanceBetweenTwoPoints(
        startTouchCoordinate.current,
        {
          x: event.changedTouches[0].clientX,
          y: event.changedTouches[0].clientY,
        },
      );
      isOneClick = distance === 0;
    }

    if (isCloseDrawer.current && !blockInteraction.current && !isOneClick) {
      onClose();
    }
    blockInteraction.current = false;
    isFirstAngleCheck.current = false;
    if (currentOffset !== 0) {
      blockInteraction.current = true;
      moveDrawer(0, true);
    }
  };

  useTouchPreventDefault(tailRef.current, {
    passive: false,
  });

  return (
    <PopUpRoot
      className={clsx(styles.root, {
        [styles.rootClose]: !isOpen,
      })}>
      <div
        className={clsx(styles.drawer, {
          [styles.drawerClose]: !isOpen,
        })}
        ref={drawerRef}
        style={{ transition: `transform ${transitionTimeSeconds}s` }}
        onTouchMove={touchMoveHandler}
        onTouchStart={(event) => {
          startTouchCoordinate.current = {
            x: event.targetTouches[0].clientX,
            y: event.targetTouches[0].clientY,
          };
        }}
        onTouchEnd={onTouchEndHandler}
        onTouchCancel={onTouchEndHandler}
        onTransitionEnd={() => (blockInteraction.current = false)}>
        {children}
      </div>
      <div
        ref={tailRef}
        onTouchStart={(event) => {
          startTouchCoordinate.current = {
            x: window.innerWidth,
            y: event.targetTouches[0].clientY,
          };
          moveDrawer(`calc(-100% + ${swipeTailWidth}px)`, true);
          onOpen();
        }}
        onTouchMove={(event) => touchMoveHandler(event, true)}
        onTouchEnd={onTouchEndHandler}
        onTouchCancel={onTouchEndHandler}
        className={styles.tail}
        style={{
          width: swipeTailWidth,
          pointerEvents: isOpen ? "none" : "auto",
        }}
      />
    </PopUpRoot>
  );
};

export default SwipeableDrawer;
