import React, {
  ClipboardEvent,
  Dispatch,
  FC,
  FocusEventHandler,
  forwardRef,
  KeyboardEvent,
  MouseEvent,
  SetStateAction,
  useEffect,
  useImperativeHandle,
  useRef,
  useState,
} from "react";
import clsx from "clsx";

import { reduceSmileString, setCursor } from "utils";
import { useLocalStorage } from "hooks";
import { Icon } from "components";

import { copy } from "utils/copyToClipboard";
import { getStringFromDivChildren } from "../../utils/";
import css from "./style.module.scss";

interface Props {
  value: string;
  clearCursorPosition: () => void;
  sendMessage: (value?: string) => void;
  setOpenMedia: Dispatch<SetStateAction<boolean>>;
}

export interface ImperativeValues {
  renderTextDiv: {
    current: HTMLDivElement | null;
  };
}

export const Input = forwardRef<ImperativeValues, Props>(
  ({ value, sendMessage, setOpenMedia, clearCursorPosition }, ref) => {
    const [token] = useLocalStorage("token");
    const renderRef = useRef<HTMLDivElement>(null);
    const [focus, setFocus] = useState({
      isInFocus: false,
      isFull: false,
    });

    useImperativeHandle(ref, () => ({
      renderTextDiv: renderRef,
    }));

    const onBlur: FocusEventHandler<HTMLDivElement> = ({ target }) => {
      setFocus({
        isInFocus: false,
        isFull: !!target.childNodes.length,
      });
      clearCursorPosition();
    };

    const onKeyDown = (event: KeyboardEvent<HTMLDivElement>) => {
      setFocus((old) => {
        return {
          isInFocus: old.isInFocus,
          //@ts-ignore
          isFull: !!event.target?.childNodes?.length,
        };
      });
      if (event.key === "Enter") {
        event.preventDefault();
        if (!token) return;

        const { string } = getStringFromDivChildren(event.target, true);
        sendMessage(string);
      }
    };

    const onClick = ({ target }: MouseEvent<HTMLDivElement>) => {
      const editableDiv = renderRef.current;
      //@ts-ignore
      if (target?.tagName && target.tagName === "IMG" && editableDiv) {
        //@ts-ignore
        const img: HTMLImageElement = target;
        let position = 0;
        editableDiv.childNodes.forEach((node, i) => {
          if (node === img) {
            position = i + 1;
          }
        });

        setCursor(editableDiv, position);
      }
    };

    const onPaste = (event: ClipboardEvent<HTMLDivElement>) => {
      event.preventDefault();

      const paste = async () => {
        if (renderRef.current) {
          const { string, cursorPosition } = getStringFromDivChildren(
            renderRef.current,
          );

          const pasteText =
            event.nativeEvent.clipboardData?.getData("Text") || "";

          const startString = string.slice(0, cursorPosition);
          const endString = string.slice(cursorPosition, string.length);
          const returnString = `${startString}${pasteText}${endString}`;

          renderRef.current.innerHTML = reduceSmileString(
            returnString.replace(/\s/g, "\u00a0"),
          );

          setCursor(renderRef.current, renderRef.current.childNodes.length);
          renderRef.current.scrollTo({
            left: renderRef.current.scrollWidth,
            behavior: "smooth",
          });
        }
      };

      paste();
    };

    useEffect(() => {
      setFocus((old) => ({
        isInFocus: old.isInFocus,
        isFull: !!renderRef.current?.childNodes.length,
      }));
    }, [value]);

    return (
      <div className={css.root}>
        <div className={clsx(css.div, css.input, focus && css.divFocused)}>
          <div
            className={css.renderText}
            contentEditable
            ref={renderRef}
            dangerouslySetInnerHTML={{
              __html: reduceSmileString(value.replace(/\s/g, "\u00a0")),
            }}
            onFocus={({ target }) => {
              setFocus({
                isInFocus: true,
                isFull: !!target.childNodes.length,
              });
            }}
            onBlur={onBlur}
            onKeyDown={onKeyDown}
            onClick={onClick}
            onPaste={onPaste}
            onCopy={(event) => {
              //@ts-ignore
              if (event.target?.nodeName === "IMG") {
                const img = event.target as HTMLImageElement;
                setTimeout(() => {
                  copy(img.alt);
                }, 0);
              }
            }}
          />
        </div>
        {!focus.isInFocus && !focus.isFull ? (
          <span className={clsx(css.input, css.placeholder)}>
            {"Your message…"}
          </span>
        ) : null}
        <div className={css.formAdornment}>
          <Icon
            aria-label="emoticons"
            className={css.formInputAdornmentIcon}
            fontSize={20}
            variant={"smilingEmoticonFace"}
            onClick={() => setOpenMedia((prev) => !prev)}
          />
          <Icon
            aria-label="send message"
            className={css.formInputAdornmentIcon}
            fontSize={21}
            variant={"sendButton"}
            onClick={() => {
              if (!token) return;
              const { string } = getStringFromDivChildren(
                renderRef.current,
                true,
              );

              sendMessage(string);
            }}
          />
        </div>
      </div>
    );
  },
);
