import React from "react";
import { useLocale } from ".";
import {
  FormatMessageEtcValue,
  FormatMessageFN,
  FormatMessagesProps,
  GetLocaleShape,
} from "./types";
import locales, { LocalesList } from "config/locales";
import { track } from "utils";
type ReturnValue = (string | JSX.Element)[];

export const getFormatMessage = (messages: LocalesList | null) => {
  const formatMessage: FormatMessageFN = (
    { id }: FormatMessagesProps,
    values?: FormatMessageEtcValue,
  ) => {
    try {
      const getMessage = () => {
        if (messages) {
          if (messages[id]) return messages[id];
          if (locales?.en && locales.en[id]) return locales.en[id];
        }

        return id;
      };

      let isComponent = false;

      let msg = getMessage();
      let result: ReturnValue = [msg];

      if (values) {
        const valuesList: ReturnValue = [];
        const splitText = "~~{split}~~";

        Object.entries(values).forEach(([key, value]) => {
          const reg = new RegExp(`\{.*?${key}.*?\}`, "g");
          msg = msg.replace(reg, splitText);
          valuesList.push(value);

          if (typeof value !== "string") {
            isComponent = true;
          }
        });

        let splittedMsg = msg.split(splitText);

        let valuesIterator = 0;
        const fullList = splittedMsg.reduce<ReturnValue>((list, value, i) => {
          if (valuesList[valuesIterator]) {
            list.push(value, valuesList[valuesIterator]);
          } else {
            list.push(value);
          }

          valuesIterator++;
          return list;
        }, []);

        result = fullList;
      }

      return (isComponent ? <>{result}</> : result.join(" ")) as any;
    } catch (error) {
      track()?.errors(error);

      return id;
    }
  };

  return formatMessage;
};

export const useGetLocale = (): GetLocaleShape => {
  const { messages, locale } = useLocale();

  return {
    formatMessage: getFormatMessage(messages),
    locale,
  };
};
