import React, { DetailedHTMLProps, FC, OlHTMLAttributes } from "react";
import clsx from "clsx";

import css from "./style.module.scss";

interface BreadcrumbsProps
  extends DetailedHTMLProps<
    OlHTMLAttributes<HTMLOListElement>,
    HTMLOListElement
  > {
  elements: JSX.Element[];
  classes?: {
    nav?: string;
    root?: string;
    element?: string;
  };
}

export const Breadcrumbs: FC<BreadcrumbsProps> = ({
  elements,
  classes,
  className,
  ...props
}) => {
  return (
    <nav className={clsx(css.nav, classes?.nav)}>
      <ol className={clsx(className, css.root, classes?.root)} {...props}>
        {elements.map((element, i, src) => {
          const withoutSlash = i === src.length - 1;

          return (
            <React.Fragment key={`breadcrumbs_component_key_${i}`}>
              <li className={clsx(classes?.element)}>{element}</li>
              {!withoutSlash && <li className={clsx(classes?.element)}>/</li>}
            </React.Fragment>
          );
        })}
      </ol>
    </nav>
  );
};
