import React, { useRef } from "react";

import clsx from "clsx";
import { ReactComponent as CachedIcon } from "assets/icons/cached.svg";
import { useGetLocale } from "providers";

import {
  Button,
  FilledInput,
  FilledInputProps,
  Icon,
  InputAdornment,
  notifyBarManager,
} from "components";
import css from "./style.module.scss";
import { track } from "utils";

export interface CopyInputProps extends FilledInputProps {
  handleRotate?: () => void;
  withoutCopy?: boolean;
}

const CopyInput = React.forwardRef(
  (
    {
      id,
      label,
      className,
      handleRotate,
      withoutCopy,
      ...props
    }: CopyInputProps,
    ref,
  ) => {
    const inputRef = useRef<HTMLInputElement>(null);
    const intl = useGetLocale();

    ref = inputRef;
    const handleCopy: React.MouseEventHandler<HTMLButtonElement> = (e) => {
      try {
        if (inputRef.current) {
          inputRef.current.select();
          document.execCommand("copy");
          notifyBarManager.success({
            message: intl.formatMessage({ id: "copied" }),
            timeOut: 2000,
          });

          e.currentTarget.focus();
        }
      } catch (error) {
        track()?.errors(error);

        notifyBarManager.error({
          message: intl.formatMessage({ id: "unrecognizedError" }),
          timeOut: 2000,
        });
      }
    };

    return (
      <>
        <FilledInput
          {...props}
          label={label}
          classes={{
            root: css.root,
            label: css.label,
          }}
          inputClasses={{
            root: clsx(css.inputField),
            input: css.input,
          }}
          //@ts-ignore
          ref={inputRef}
          id={id}
          endAdornment={
            <InputAdornment position="end" className={css.formInputAdornment}>
              {handleRotate && (
                <Button
                  className={css.inputRotateButton}
                  onClick={handleRotate}>
                  <CachedIcon />
                </Button>
              )}
              {document.queryCommandSupported("copy") && !withoutCopy && (
                <Button className={css.inputCopyButton} onClick={handleCopy}>
                  <Icon variant="copy" withoutButton />
                </Button>
              )}
            </InputAdornment>
          }
        />
      </>
    );
  },
);

export default CopyInput;
