import React, { FC, useEffect, useState } from "react";
import clsx from "clsx";

import { PopUpRoot } from "..";
import { PopUpMenuProps } from "../types";
import css from "./style.module.scss";

export const PopUpAnchorMenu: FC<PopUpMenuProps> = ({
  children,
  anchor,
  isOpen,
  onClose,
  onMove,
  background = "transparent",
  customAdditionalTransform,

  paper,
}) => {
  const [rootElement, setRootElement] = useState<HTMLDivElement | null>(null);
  const [isClosed, setIsClosed] = useState(true);

  const getMenuPosition = () => {
    if (anchor && rootElement) {
      let { top, left } = anchor.getBoundingClientRect();

      if (top < 0) {
        top = 0;
      }

      if (left < 0) {
        left = 0;
      }

      if (left + rootElement.offsetWidth > window.innerWidth) {
        left = window.innerWidth - rootElement.offsetWidth;
      }

      if (top + rootElement.offsetHeight > window.innerHeight) {
        top = window.innerHeight - rootElement.offsetHeight;
      }
      if (customAdditionalTransform) {
        return {
          top: top + customAdditionalTransform.y,
          left: left + customAdditionalTransform.x,
        };
      }

      return {
        top,
        left,
      };
    }

    return {
      top: 0,
      left: 0,
    };
  };

  const endAnimation = () => {
    if (isClosed) {
      onClose();
    }
  };

  const setClose = () => {
    setIsClosed(true);
  };

  useEffect(() => {
    if (isOpen) {
      setIsClosed(false);
    }
  }, [isOpen]);

  if (!isOpen || !anchor) {
    return null;
  }

  const menuPosition = getMenuPosition();

  return (
    <PopUpRoot
      background={background}
      curtainOnClick={setClose}
      curtainOnMove={onMove}>
      <div
        onAnimationEnd={endAnimation}
        ref={(node) => setRootElement(node)}
        className={clsx(
          css.root,
          paper?.className,
          !isClosed ? css.open : css.close,
        )}
        style={{
          ...paper?.style,
          transform: `translate(${menuPosition.left}px, ${menuPosition.top}px)`,
        }}
        {...paper}>
        {children}
      </div>
    </PopUpRoot>
  );
};

export default PopUpAnchorMenu;
