import React, { FC } from "react";
import clsx from "clsx";

import { LinkWithSearch, Avatar } from "components";

import { Level } from "../Level";
import { getSearch } from "../../utils";
import css from "./style.module.scss";

interface Props {
  user: string;
  avatar: string;
  rating: number;
}

export const MessageAvatar: FC<Props> = ({ avatar, rating, user }) => {
  return (
    <div className={css.avatarWrapper}>
      <LinkWithSearch {...getSearch(user)} className={clsx(css.avatarLink)}>
        <Avatar userName={user} avatar={avatar} className={css.avatar} />
      </LinkWithSearch>
      <Level level={rating} />
    </div>
  );
};
