export function getSoundEffectNames<T extends keyof ISoundEffectNames>(
  gameName: T,
): Omit<ISoundEffectNames[T], "backgroundMusic"> {
  return soundEffectNames[gameName];
}

const soundEffectBasePath = "assets/audio/SoundEffects";

export const createSoundEffectsSrc = () => {
  let effectsSrc: IGameEffects = {};
  for (let gameName in soundEffectNames) {
    effectsSrc[gameName] = { background: [], effects: {} };
    let gameNameType = gameName as gameNames;

    for (let effName in soundEffectNames[gameNameType]) {
      if (effName === "backgroundMusic") {
        effectsSrc[gameName].background = soundEffectNames[
          gameNameType
        ].backgroundMusic.map((music) => {
          return (
            soundEffectBasePath +
              gameName +
              "/" +
              effName +
              "/" +
              music +
              ".mp3" || ""
          );
        });
      }
      effectsSrc[gameName].effects[effName] =
        soundEffectBasePath + gameName + "/" + effName + ".mp3" || "";
    }
  }

  return effectsSrc;
};

export const soundSystemConfig: ISoundSystemConfig = {
  games: createSoundEffectsSrc(), // create correct src name only for mp3 format
};
