import React, { FC, memo } from "react";
import clsx from "clsx";

import { Caption, TBody, TFoot, THead } from "./components";
import { TableProps } from "./types";
import css from "./style.module.scss";

export const Table: FC<TableProps> = ({
  caption,
  head,
  body,
  footer,
  root,
}) => {
  return (
    <div className={clsx(css.root, root?.className)} {...root}>
      <table className={css.table} style={{ color: "white" }}>
        {caption && <Caption {...caption} />}

        {head && <THead {...head} />}
        {body && <TBody {...body} />}
        {footer && <TFoot {...footer} />}
      </table>
    </div>
  );
};

export const MemoTable = memo(Table, (p, n) => {
  if (p.body?.rows && n.body?.rows) {
    return p.body.rows[0]?.key === n.body.rows[0]?.key;
  }

  return p.body?.rows?.length === n.body?.rows?.length;
});
