import React, { FC, useRef, useEffect, useMemo, useState } from "react";
import { LocationDescriptor, History } from "history";
import { Link } from "react-router-dom";
import clsx from "clsx";

import { Button, ButtonProps, Skeleton } from "components";
import { useSassImport } from "hooks";

import bannerVariants, { BannerVariantKeys } from "components/Banner/config";

export interface BannerProps extends ButtonProps {
  BannerText?: FC;
  withSkeleton?: boolean;
  skeleton?: boolean;
  skeletonClassName?: string;
  onLoading?: () => void;
  onLoad?: () => void;
  buttonVariant: BannerVariantKeys;
  to: LocationDescriptor<History.LocationState>;
}

const BannerComponent = ({
  BannerText,
  className,
  buttonVariant,
  skeletonClassName,
  withSkeleton,
  skeleton,
  onLoading,
  onLoad,
  to,
  ...rest
}: BannerProps) => {
  const [loaded, setLoaded] = useState(false);
  const css = useSassImport(() => import("./style.module.scss"));

  const { bannerURL, width, height } = bannerVariants[buttonVariant];

  const bgImg = useRef(new Image(width, height)).current;
  const imageRef = useRef<HTMLDivElement>(null);
  const styles = {
    image: {
      display: skeleton ? "none" : "block",
      backgroundImage:
        withSkeleton && (skeleton || !loaded) ? undefined : `url(${bannerURL})`,
    },
    skeleton: {
      opacity: skeleton ? 1 : 0,
    },
    dimensions: {
      width,
      height,
    },
  };

  useEffect(() => {
    bgImg.src = bannerURL;

    if (onLoading && !bgImg.complete) {
      onLoading();
    }
  }, []);

  useEffect(() => {
    if (!skeleton) {
      return;
    }

    const onLoading = () => {
      setLoaded(true);
      if (onLoad) {
        onLoad();
      }
    };

    if (bgImg.complete) {
      onLoading();
    } else {
      bgImg.onload = onLoading;
    }
  }, [skeleton]);

  const ForwardedLink = useMemo(
    () =>
      React.forwardRef<HTMLAnchorElement | null>((props, ref) => (
        <Link ref={ref} {...props} to={to} />
      )),
    [to],
  );

  if (!css.bannerTextWrapper) {
    return null;
  }

  return (
    <Button
      className={clsx(css.banner, className)}
      aria-label={buttonVariant}
      component={ForwardedLink}
      waveColor={"black"}
      {...rest}>
      {withSkeleton && skeleton && (
        <Skeleton
          className={clsx(skeletonClassName, css.skeleton)}
          style={styles.skeleton}
          width={width}
          height={height}
        />
      )}
      {BannerText && (
        <div className={css.bannerTextWrapper}>
          <BannerText />
        </div>
      )}
      <div
        ref={imageRef}
        className={clsx(skeletonClassName, css.image, css.dimensions)}
        style={{ ...styles.image, ...styles.dimensions }}
      />
    </Button>
  );
};

export default BannerComponent;
