import React, { CSSProperties, FC } from "react";
import clsx from "clsx";

import CircularProgress from "components/CircularProgress";

import css from "./style.module.css";

interface Props {
  className?: string;
  style?: CSSProperties;
  color?: string;
  rootClass?: string;
  size?: number | string;
  circularWidth?: number;
}

const ProgressFallback: FC<Props> = ({
  className,
  style,
  color,
  rootClass,
  circularWidth,
  size,
}) => {
  return (
    <div
      className={clsx(css.root, rootClass)}
      style={{
        width: size,
        height: size,
      }}>
      <CircularProgress
        {...{
          circularWidth,
          color,
          className,
          style,
          size,
        }}
      />
    </div>
  );
};

export default ProgressFallback;
