import React, { FC, Suspense, useRef } from "react";
import { createPortal } from "react-dom";
import clsx from "clsx";

import LazyComponentLoader from "config/LazyComponentLoader";
import ProgressFallback from "components/fallbacks/Progress";

import { modalsLoaderColor } from "sass/styleVariables";
import css from "./style.module.scss";

export * from "./eventEmitter";

const Body = React.lazy(() => LazyComponentLoader(import("./components/Body")));
const Header = React.lazy(() =>
  LazyComponentLoader(import("./components/Header")),
);
interface Props {
  isOpen: boolean;
  handleClose: () => void;
}

const rootDom = document.getElementById("pop-up");

const LiveStats: FC<Props> = ({ isOpen, handleClose }) => {
  const boxRef = useRef<HTMLDivElement | null>(null);

  if (rootDom) {
    return createPortal(
      <div
        className={clsx({ [css.disable]: !isOpen, [css.root]: isOpen })}
        ref={boxRef}>
        {boxRef.current && isOpen && (
          <>
            <Suspense
              fallback={
                <div className={css.fallback}>
                  <ProgressFallback
                    color={modalsLoaderColor}
                    size={150}
                    circularWidth={10}
                  />
                </div>
              }>
              <Header box={boxRef.current} handleClose={handleClose} />
              <Body />
            </Suspense>
          </>
        )}
      </div>,
      rootDom,
    );
  }

  return null;
};

export default React.memo(LiveStats, (p, n) => p.isOpen === n.isOpen);
