import React from "react";

import { Message } from "../types";
import { Channel, Device, SystemTypes, UserParams } from "./types";

export interface ChatContextType {
  selectedChannel: string;
  setSelectedChannel: (value: string) => void;
  channels: Channel[];
  userParams: UserParams | null;
  messages: Message[];
  device: Device;
  setDevice: (value: Device) => void;
  isAutoScrollMode: boolean;
  setIsAutoScrollMode: (value: boolean) => void;
  chatFunctions: {
    current: {
      setSystemMessage: (valuse: string, type: SystemTypes) => void;

      scrollToBottom: (options?: {
        whenScrolled?: (() => void) | undefined;
      }) => Promise<void> | void;
    };
  };
  createMessage: (value: string, type: "text" | "gif") => void;

  audio: HTMLAudioElement | null;
}

export const defaultValue: ChatContextType = {
  selectedChannel: "English",
  setSelectedChannel: () => {},
  createMessage: () => {},
  channels: [],
  messages: [],
  userParams: null,
  device: "desktop",
  setDevice: () => {},
  isAutoScrollMode: true,
  setIsAutoScrollMode: () => {},
  chatFunctions: {
    current: {
      setSystemMessage: () => {},

      scrollToBottom: () => {},
    },
  },

  audio: null,
};

export const Context = React.createContext<ChatContextType>(defaultValue);

export default Context;
