import React, { DetailedHTMLProps, FC, HTMLAttributes } from "react";
import clsx from "clsx";

import { ReactComponent as LeftToBeginIcon } from "assets/icons/pagination-left.svg";
import { ReactComponent as RightToBeginIcon } from "assets/icons/pagination-right.svg";

import { ReactComponent as LeftIcon } from "assets/icons/chevron-left.svg";
import { ReactComponent as RightIcon } from "assets/icons/chevron-right.svg";

import css from "./style.module.scss";
import { PageButton } from "./components";

export interface PaginationProps
  extends Omit<
    DetailedHTMLProps<HTMLAttributes<HTMLSpanElement>, HTMLSpanElement>,
    "onChange"
  > {
  showPrevNextButton?: boolean;
  showFirstLastButton?: boolean;
  showFirstLastPage?: boolean;

  maxButtonVisible?: number;
  page: number;
  pageAmount: number;
  onChange?: (page: number) => void;
  disabled?: boolean;

  classes?: {
    root?: string;
    button?: string;
    activePage?: string;
  };
}

const Skip = () => <span className={css.skip}>...</span>;

export const Pagination: FC<PaginationProps> = ({
  maxButtonVisible = 3,

  showFirstLastButton,
  showPrevNextButton,
  showFirstLastPage,

  page,
  pageAmount,

  classes,
  onChange,
  disabled,
}) => {
  const Pages: FC = () => {
    const components: JSX.Element[] = [];
    const firstPage = 1;
    const buttonsRange = Math.floor(maxButtonVisible / 2);

    const firstShowButtonNumber = Math.max(page - buttonsRange, firstPage);
    const lastShowButtonNumber = Math.min(page + buttonsRange, pageAmount);

    for (
      let pageNumber = firstShowButtonNumber;
      pageNumber <= lastShowButtonNumber;
      pageNumber++
    ) {
      const changePageToNumber = () => onChange && onChange(pageNumber);

      const isActive = pageNumber === page;

      const Component = (
        <PageButton
          onClick={changePageToNumber}
          disabled={isActive || disabled}
          className={clsx(
            classes?.button,
            css.button,
            isActive && [css.active, classes?.activePage],
          )}>
          {pageNumber}
        </PageButton>
      );

      const isLast = pageNumber === pageAmount;
      const isFirst = pageNumber === firstPage;

      if (!((isFirst || isLast) && showFirstLastPage)) {
        components.push(Component);

        if (
          pageNumber === lastShowButtonNumber &&
          lastShowButtonNumber + 1 < pageAmount
        ) {
          components.push(<Skip />);
        } else if (
          pageNumber === firstShowButtonNumber &&
          firstShowButtonNumber - 1 > firstPage
        ) {
          components.splice(components.length - 2, 0, <Skip />);
        }
      }
    }

    return <>{components}</>;
  };

  const setFirstPage = () => onChange && onChange(1);
  const setLastPage = () => onChange && onChange(pageAmount);
  const setPrevPage = () => onChange && onChange(Math.max(1, page - 1));
  const setNextPage = () =>
    onChange && onChange(Math.min(pageAmount, page + 1));

  const disabledBecauseFirstPage = page === 1 || disabled;
  const disabledBecauseLastPage = page === pageAmount || disabled;

  return (
    <span className={clsx(css.root, classes?.root)}>
      {showFirstLastButton && (
        <PageButton
          onClick={setFirstPage}
          disabled={disabledBecauseFirstPage}
          className={clsx(classes?.button, css.button)}>
          <LeftToBeginIcon />
        </PageButton>
      )}

      {showPrevNextButton && (
        <PageButton
          onClick={setPrevPage}
          disabled={disabledBecauseFirstPage}
          className={clsx(classes?.button, css.button)}>
          <LeftIcon />
        </PageButton>
      )}

      {showFirstLastPage && (
        <PageButton
          onClick={setFirstPage}
          disabled={disabledBecauseFirstPage}
          className={clsx(
            classes?.button,
            css.button,
            page === 1 && [css.active, classes?.activePage],
          )}>
          1
        </PageButton>
      )}

      {<Pages />}

      {showFirstLastPage && pageAmount !== 1 && (
        <PageButton
          onClick={setLastPage}
          disabled={disabledBecauseLastPage}
          className={clsx(
            classes?.button,
            css.button,
            page === pageAmount && [css.active, classes?.activePage],
          )}>
          {pageAmount}
        </PageButton>
      )}

      {showPrevNextButton && (
        <PageButton
          onClick={setNextPage}
          disabled={disabledBecauseLastPage}
          className={clsx(classes?.button, css.button)}>
          <RightIcon />
        </PageButton>
      )}

      {showFirstLastButton && (
        <PageButton
          onClick={setLastPage}
          disabled={disabledBecauseLastPage}
          className={clsx(classes?.button, css.button)}>
          <RightToBeginIcon />
        </PageButton>
      )}
    </span>
  );
};
