import React, { FC, ChangeEvent, useState } from "react";
import clsx from "clsx";

import styles from "./style.module.scss";

export interface RadioClasses {
  label: string;
  round: string;
  root: string;
  radio: string;
  rootChecked: string;
}

export interface Props {
  onChange?: (event: ChangeEvent<HTMLInputElement>) => void;
  value?: string | number;
  name?: string | number;
  checked?: boolean;
  label: string;
  disabled?: boolean;
  className?: string;
  classes?: Partial<RadioClasses>;
}

const Radio: FC<Props> = ({
  onChange,
  value,
  name,
  checked,
  label,
  disabled,
  className,
  classes,
}) => {
  const [isPressed, setIsPressed] = useState(false);

  return (
    <div
      className={clsx(styles.root, className, classes?.root, {
        [classes?.rootChecked || ""]: checked,
      })}>
      <label className={clsx(styles.label, classes?.label)}>
        <div
          className={clsx(styles.radio, classes?.radio, {
            [styles.radioPressed]: isPressed,
          })}>
          <input
            type="radio"
            onChange={onChange}
            value={value}
            name={String(name)}
            className={styles.input}
            checked={checked}
            disabled={disabled}
            onMouseDown={() => setIsPressed(true)}
            onMouseUp={() => setIsPressed(false)}
            onMouseLeave={() => setIsPressed(false)}
          />
          <div
            className={clsx(styles.round, classes?.round, {
              [styles.roundChecked]: checked,
            })}
          />
        </div>
        <span className={styles.labelText}>{label}</span>
      </label>
    </div>
  );
};

export default Radio;
