import { EventEmitter } from "events";
import { track } from "utils";
import { calcSmallNumber } from "utils/calcSmallNumber";
import { cutSmallNumber } from "utils/cutSmallNumber";
import {
  LiveData,
  LiveStatistics,
  LiveStatsDataResult,
  LiveStatsEvents,
  LiveStatsList,
} from "./types";

const FRACTIONAL_PART = 8;

class LiveStatsManager extends EventEmitter {
  graphDots: LiveStatsList = [0];
  prevCoin = "";
  generalLiveData: LiveData = {
    amount: 0,
    profit: 0,
    coin: "",
  };
  statistics: LiveStatistics = {
    wins: 0,
    loses: 0,
  };

  addBet(value: LiveData) {
    const amount = cutSmallNumber(value.amount, FRACTIONAL_PART);
    const profit = cutSmallNumber(value.profit, FRACTIONAL_PART);

    if (
      typeof amount !== "number" ||
      typeof profit !== "number" ||
      isNaN(amount) ||
      isNaN(profit)
    ) {
      track()?.errors("LiveStatsManager > addBet value wrong", {
        custom: {
          addBetValue: value,
          calculated: {
            amount,
            profit,
          },
        },
      });
    }

    if (value.coin !== this.prevCoin) {
      this.prevCoin = value.coin;
      this.restart();
    }

    const isLoss = profit < 0;

    this.generalLiveData.amount = calcSmallNumber(
      amount,
      this.generalLiveData.amount,
      "add",
    );
    this.generalLiveData.profit = calcSmallNumber(
      profit,
      this.generalLiveData.profit,
      "add",
    );

    this.generalLiveData.coin = value.coin;

    if (isLoss) {
      this.statistics.loses += 1;
    } else {
      this.statistics.wins += 1;
    }

    const { generalLiveData, statistics, graphDots } = this;
    this.graphDots.push(generalLiveData.profit);

    this.emit(LiveStatsEvents.GET_DATA, {
      generalLiveData,
      statistics,
    });

    this.emit(LiveStatsEvents.GET_DOTS, graphDots);
  }

  restart() {
    this.reset();
  }

  reset() {
    this.generalLiveData.amount = 0;
    this.generalLiveData.profit = 0;

    this.statistics.loses = 0;
    this.statistics.wins = 0;

    this.graphDots.length = 0;
    this.graphDots.push(0);

    const { generalLiveData, statistics, graphDots } = this;
    this.emit(LiveStatsEvents.GET_DATA, {
      generalLiveData,
      statistics,
    });

    this.emit(LiveStatsEvents.GET_DOTS, graphDots);
  }

  open() {
    const { generalLiveData, statistics, graphDots } = this;
    this.emit(LiveStatsEvents.GET_DATA, {
      generalLiveData,
      statistics,
    });

    this.emit(LiveStatsEvents.GET_DOTS, graphDots);
  }

  getData(callBack: (data: LiveStatsDataResult) => void) {
    this.addListener(LiveStatsEvents.GET_DATA, callBack);
    return () => {
      this.removeListener(LiveStatsEvents.GET_DATA, callBack);
    };
  }

  getDots(callBack: (data: LiveStatsList) => void) {
    this.addListener(LiveStatsEvents.GET_DOTS, callBack);
    return () => {
      this.removeListener(LiveStatsEvents.GET_DOTS, callBack);
    };
  }
}

export const liveStatsManager = new LiveStatsManager();
