import React, { Component, ErrorInfo } from "react";
import { track } from "utils";

import { LazyHiderComponent } from "./component";

interface Props {
  sizes:
    | {
        width: number | string;
        height?: number | string;
      }
    | {
        height: number | string;
        width?: number | string;
      };

  manualSet?: boolean;

  fallBack?: () => JSX.Element;
  debug?: boolean;
  isHide?: boolean;
  shouldHideIfOutOfFrame?: boolean;
}

export class LazyHider extends Component<Props> {
  state = {
    isError: false,
  };

  componentDidCatch(error: Error, info: ErrorInfo) {
    track()?.errors(error, {
      custom: info,
    });

    this.setState({
      isError: true,
    });
  }

  render() {
    const { isError } = this.state;

    if (isError) {
      return this.props.children;
    }

    return <LazyHiderComponent {...this.props} />;
  }
}
