import React, { useMemo } from "react";
import AutoSizer from "react-virtualized-auto-sizer";
import { VariableSizeGrid as Grid } from "react-window";

import { useWindowSize } from "providers";
import { repeat } from "utils";

import { emojiList } from "components/Chat/components/Write/data";
import Component from "./Component";
import css from "./style.module.scss";

interface Props {
  smileEnter: (value: string) => void;
}

export interface SmileCode {
  smile: string;
  base: string;
}

const renderEmoji = (columnBreakNumber: number) => {
  const all: SmileCode[] = [];
  let currentContainer: SmileCode[] = [];
  let activeCategory = "people";

  emojiList.forEach(({ base, category, smile }) => {
    if (category !== "people" && category !== activeCategory) {
      all.push(...currentContainer);

      let emptyAmount =
        columnBreakNumber +
        columnBreakNumber -
        (all.length % columnBreakNumber);
      if (emptyAmount === columnBreakNumber * 2) {
        emptyAmount = columnBreakNumber;
      }

      all.push(...repeat(emptyAmount, () => ({ base: "", smile: "" })));
      currentContainer = [];
      activeCategory = category;
    }

    currentContainer.push({
      base,
      smile,
    });
  });

  all.push(...currentContainer);

  return all;
};

const smileSizes = {
  w: 38.5,
  h: 43,
};

const Emoji = ({ smileEnter }: Props) => {
  const { width } = useWindowSize();
  const columnAmount = useMemo(() => {
    if (width > 600) {
      return 7;
    }

    return Math.floor((width - 40) / smileSizes.w);
  }, [width]);

  const smileList = useMemo(() => renderEmoji(columnAmount), []);

  return (
    <div className={css.root}>
      <AutoSizer>
        {({ height }) => (
          <Grid
            className={css.grid}
            height={height}
            width={columnAmount * smileSizes.w + 10}
            overscanCount={10}
            columnCount={columnAmount}
            columnWidth={() => smileSizes.w}
            rowCount={Math.ceil(smileList.length / columnAmount)}
            rowHeight={() => smileSizes.h}
            itemData={smileList}>
            {({ data, rowIndex, columnIndex, style }) => {
              const index = rowIndex * columnAmount + columnIndex;

              return (
                <Component
                  element={data[index]}
                  style={style}
                  smileEnter={smileEnter}
                />
              );
            }}
          </Grid>
        )}
      </AutoSizer>
    </div>
  );
};

export default React.memo(Emoji);
