import React, {
  FC,
  ImgHTMLAttributes,
  useEffect,
  useRef,
  useState,
} from "react";
import clsx from "clsx";

import css from "./style.module.scss";
import { createListeners } from "utils";
import debounce from "lodash.debounce";

interface Props extends ImgHTMLAttributes<HTMLImageElement> {
  forceLoading?: boolean | null;
  src?: string;

  transitionTime?: number;
  /*
   *transitionTime
   *only in seconds
   */
  debug?: boolean;
}

const listenerTypes = [
  "scroll",
  "visibilitychange",
  "resize",
  "orientationChange",
];

const { addListeners, removeListeners } = createListeners(
  listenerTypes as any[],
);

const Img: FC<Props> = ({
  forceLoading = null,
  src = "",
  transitionTime,
  className,
  alt = "img",
  style,
  onLoad: incomeOnLoad,
  debug,
  ...props
}) => {
  const [visibleSrc, setVisibleSrc] = useState("");
  const [isLoaded, setIsLoaded] = useState(false);
  const imgRef = useRef<HTMLImageElement | null>(null);

  const imgLoader = debounce(() => {
    if (debug) {
      debugger;
    }
    if (imgRef.current && !visibleSrc) {
      const imgScrollData = imgRef.current.getBoundingClientRect();

      if (
        imgScrollData.y <= window.scrollY + window.innerHeight &&
        imgScrollData.x + imgScrollData.width >= 0 &&
        imgScrollData.x <= window.innerWidth
      ) {
        setVisibleSrc(src);
      }
    }
  }, 300);

  const onLoad = (event: React.SyntheticEvent<HTMLImageElement, Event>) => {
    setIsLoaded(true);
    incomeOnLoad && incomeOnLoad(event);
  };

  useEffect(() => {
    if (visibleSrc) {
      removeListeners(imgLoader);
    }
  }, [visibleSrc]);

  if (visibleSrc !== src) {
    imgLoader();
    forceLoading && setVisibleSrc(src);
  }

  useEffect(() => {
    return () => {
      removeListeners(imgLoader);
    };
  }, []);

  return (
    <img
      ref={(node) => {
        imgRef.current = node;
        imgLoader();
        addListeners(imgLoader);
      }}
      src={visibleSrc}
      className={clsx(
        css.img,
        {
          [css.disable]: !isLoaded,
        },
        className,
      )}
      onLoad={onLoad}
      style={
        transitionTime
          ? {
              transition: `opacity ${transitionTime}s`,
              ...style,
            }
          : style
      }
      alt={alt}
      {...props}
    />
  );
};

export default Img;
