import React, { FC, useEffect, useMemo, useRef, useState } from "react";
import Context from "./context";

import { useLocation } from "react-router-dom";
import { gamePathNames, soundSystemConfig } from "./config";
import { useBackgroundSound } from "./hooks/useBackgroundSound";
import { between, track } from "utils";
import { useSoundEffects } from "./hooks/useSoundEffects";
import {
  audioKeys,
  controlFunctionsType,
  IAudiosRef,
  IAudioState,
  IGame,
} from "./types";

export const defaultState: IAudioState = {
  volume: 0.5,
  isInteractivityWas: false,
  isBackgroundPlay: false,
  isGameEffectsPlay: true,
};
export const defaultControlFunctionList: controlFunctionsType = {
  switchSoundEnableState: () => {},
  playEffect: () => {},
  stopEffect: () => {},
  changeVolume: () => {},
  preload: () => {},
};
export * from "./types";

export const permittedSoundGamePaths = Object.values(gamePathNames) as string[];
export const SoundSystemControlProvider: FC = ({ children }) => {
  const [soundSystemState, setSoundSystemState] =
    useState<IAudioState>(defaultState);

  const audiosRef = useRef<IAudiosRef>({
    background: {
      audio: null,
      src: "",
      index: 0,
    },
    game: {},
  });
  const isCanPlaySoundListener = () => {
    if (!soundSystemState.isInteractivityWas)
      setSoundSystemState((prev) => ({ ...prev, isInteractivityWas: true }));
    document.body.removeEventListener("click", isCanPlaySoundListener);
    if (audiosRef.current.background.audio) {
      const promiseAudio = audiosRef.current.background.audio.play();
      if (promiseAudio !== undefined) {
        promiseAudio.then().catch((err) => {
          console.log(err);
          track()?.errors(err, {
            custom: {
              place: "promiseAudio",
            },
          });
        });
      }
    }
  };

  const { pathname: locationPathanme, state } = useLocation<any>();
  const pathname =
    state && state.background ? state.background.pathname : locationPathanme;

  const stopAndClearAudio = () => {
    if (audiosRef.current.background.audio !== null) {
      audiosRef.current.background.audio.pause();
      audiosRef.current.background.audio.currentTime = 0;
      audiosRef.current.background.audio.src = "";
    }

    for (let effect in audiosRef.current.game) {
      if (
        audiosRef.current.game[effect] &&
        audiosRef.current.game[effect].audio !== null
      ) {
        audiosRef.current.game[effect].audio?.pause();
      }
    }
    audiosRef.current.background.src = "";
    audiosRef.current.game = {};
  };
  useEffect(() => {
    const soundSystemSavedState = localStorage.getItem("soundSystemState");
    if (soundSystemSavedState) {
      const newState = JSON.parse(soundSystemSavedState);
      setSoundSystemState({ ...newState, isInteractivityWas: false });
    }
  }, []);

  useEffect(() => {
    localStorage.setItem("soundSystemState", JSON.stringify(soundSystemState));
    localStorage.setItem("soundSystemStatee", JSON.stringify(soundSystemState));
  }, [soundSystemState]);

  useEffect(() => {
    if (pathname && permittedSoundGamePaths.includes(pathname)) {
      stopAndClearAudio();
      const backgroundMusicListLength =
        soundSystemConfig.games[pathname].background.length;
      const randomBackgroundMusicIndex =
        backgroundMusicListLength > 1
          ? Math.round(between(0, backgroundMusicListLength - 1))
          : 0;

      if (!audiosRef.current.background.src) {
        audiosRef.current.background.src =
          soundSystemConfig.games[pathname].background[
            randomBackgroundMusicIndex
          ];
        if (
          soundSystemState.isBackgroundPlay &&
          soundSystemState.isInteractivityWas
        ) {
        }

        audiosRef.current.background.index = randomBackgroundMusicIndex;
      }

      let game: IGame = {};
      for (let key in soundSystemConfig.games[pathname].effects) {
        game[key] = {
          src: soundSystemConfig.games[pathname].effects[key],
          audio: null,
        };
      }
      audiosRef.current.game = game;
    } else {
      stopAndClearAudio();
    }

    return () => {
      stopAndClearAudio();
    };
  }, [pathname]);

  useEffect(() => {
    document.body.addEventListener("click", isCanPlaySoundListener);

    return () => {
      document.body.removeEventListener("click", isCanPlaySoundListener);
    };
  }, []);

  useBackgroundSound({
    audiosRef,
    pathname,
    soundSystemState,
  });
  useSoundEffects({
    audiosRef,
    soundSystemState,
  });

  const controlFunctions: controlFunctionsType = useMemo(() => {
    return {
      switchSoundEnableState: (field, arg, isPermitted = true) => {
        if (!soundSystemState.isInteractivityWas || !isPermitted) return;
        const stateField =
          field === "background" ? "isBackgroundPlay" : "isGameEffectsPlay";
        setSoundSystemState((prev) => ({
          ...prev,
          [stateField]: arg,
        }));
      },
      playEffect: (field, options, isPermitted = true) => {
        if (
          !soundSystemState.isInteractivityWas ||
          !soundSystemState.isGameEffectsPlay ||
          !isPermitted ||
          !audiosRef.current.game[field]
        ) {
          return;
        }
        let isPlaying = false;

        if (audiosRef.current.game[field].audio === null) {
          audiosRef.current.game[field].audio = new Audio(
            audiosRef.current.game[field].src,
          );

          audiosRef.current.game[field].audio?.load();
        }
        isPlaying =
          !options?.currentTime &&
          audiosRef.current.game[field].audio!.currentTime > 0 &&
          !audiosRef.current.game[field].audio!.paused &&
          !audiosRef.current.game[field].audio!.ended &&
          audiosRef.current.game[field].audio!.readyState > 2;

        if (!audiosRef.current.game[field].audio!.paused && isPlaying) {
          audiosRef.current.game[field].audio!.pause();
          audiosRef.current.game[field].audio!.currentTime = 0;
        }
        for (let option in options) {
          if (
            audiosRef.current.game[field] &&
            audiosRef.current.game[field].audio
          ) {
            const optionIndex = option as "volume" | "currentTime";
            audiosRef.current.game[field].audio![optionIndex] =
              options[optionIndex];
          }
        }
        audiosRef.current.game[field].audio!.volume =
          options?.volume || soundSystemState.volume;
        audiosRef.current.game[field].audio!.currentTime =
          options?.currentTime || 0;
        const soundPromise = audiosRef.current.game[field]
          ? audiosRef.current.game[field].audio!.play()
          : null;
        if (soundPromise !== undefined) {
          soundPromise?.then().catch((err: Error) => {
            console.log(err);
            track()?.errors(err, {
              custom: {
                place: "soundPromise",
              },
            });
          });
        }

        return audiosRef.current.game[field].audio;
      },
      stopEffect: (field) => {
        if (
          audiosRef.current.game[field] &&
          audiosRef.current.game[field].audio !== null &&
          audiosRef.current.game[field].audio!.currentTime > 0
        ) {
          audiosRef.current.game[field].audio?.pause();
          audiosRef.current.game[field].audio!.currentTime = 0;
        }
      },
      changeVolume: (arg: number) => {
        setSoundSystemState((prev) => ({ ...prev, volume: arg }));
      },
      preload: (effectNames?: string[]) => {
        let loadItems = effectNames || Object.keys(audiosRef.current.game);
        loadItems.forEach((field) => {
          if (
            audiosRef.current.game[field] &&
            audiosRef.current.game[field].audio === null
          ) {
            audiosRef.current.game[field].audio = new Audio(
              audiosRef.current.game[field].src,
            );

            audiosRef.current.game[field].audio?.load();
          }
        });
      },
    };
  }, [soundSystemState]);

  return (
    <Context.Provider value={{ soundSystemState, controlFunctions }}>
      {children}
    </Context.Provider>
  );
};
