import React, { FC, useEffect, useState } from "react";
import clsx from "clsx";

import { Icon } from "components";
import { useWindowSize } from "providers";

import { DialogProps, OnClosePopUpCallBack, PopUpCloseReason } from "../types";
import { PopUpRoot } from "..";
import css from "./style.module.scss";

let reason: PopUpCloseReason = "backdropClick";

export const Dialog: FC<DialogProps> = ({
  isOpen,
  onClose,
  children,
  paper,
  withCloseIcon,
  closeIcon: CloseIcon,
  fullScreen,
  disableOnClose,
}) => {
  const [isClosed, setIsClosed] = useState(true);
  const { width } = useWindowSize();

  const endAnimation = () => {
    if (isClosed) {
      onClose({}, reason);
    } else if (!isOpen && !isClosed) {
      setIsClosed(true);
      onClose({}, reason);
    }
  };

  const handleCloseModal: OnClosePopUpCallBack = (ev, closeReason) => {
    if (disableOnClose) {
      return;
    }
    reason = closeReason;
    setIsClosed(true);
  };

  useEffect(() => {
    if (isOpen) {
      setIsClosed(false);
    }
  }, [isOpen]);

  const isSmall = width <= parseInt(css.mobileScreen);

  if (isClosed && !isOpen) {
    return null;
  }

  return (
    <PopUpRoot
      background="dark"
      curtainOnClick={handleCloseModal}
      showAnimation={isClosed ? "hide" : "show"}>
      <div
        onAnimationEnd={endAnimation}
        className={clsx(
          css.root,
          !isClosed ? css.open : css.close,
          !isOpen && !isClosed && css.close,
          paper?.className,
          (fullScreen || isSmall) && css.root__fullScreen,
        )}
        style={{
          ...paper?.style,
        }}>
        {children}

        {withCloseIcon &&
          (CloseIcon ? (
            <CloseIcon
              onClick={() => {
                handleCloseModal({}, "backdropClick");
              }}
            />
          ) : (
            <Icon
              variant="close"
              fontSize={12}
              className={css.closeIcon}
              onClick={() => {
                handleCloseModal({}, "backdropClick");
              }}
            />
          ))}
      </div>
    </PopUpRoot>
  );
};

export default Dialog;
