import React, { FC, useEffect, useState } from "react";
import clsx from "clsx";

import { GetCoinImg } from "../CoinImg";
import Item, { Props as ItemProps } from "./Item";
import styles from "./style.module.scss";
import BaseSelect, { SelectClasses, SelectProps } from "../BaseSelect";

interface Classes extends Partial<SelectClasses> {
  selectedAmount?: string;
}
export interface SelectorProps extends SelectProps {
  options: Array<Omit<ItemProps, "className" | "classes">>;
  className?: string;
  itemClass?: string;
  labelClass?: string;
  iconClass?: string;
  showLabel?: boolean;
  isCoinSelect?: boolean;
  classes?: Classes;
  adornmentComponent?: JSX.Element | null;
}

const Select: FC<SelectorProps> = ({
  options,
  onChange,
  value,
  className,
  itemClass,
  labelClass,
  iconClass,
  showLabel = true,
  isCoinSelect = true,
  classes,
  adornmentComponent,
  ...props
}) => {
  const [imgOpacity, setImgOpacity] = useState(0);

  const selectedOption = options?.find((option) => option.value === value);

  useEffect(() => {
    if (!isCoinSelect) {
      setImgOpacity(0);
    }
  }, [value]);

  let selectIcon: JSX.Element | null = null;

  if (isCoinSelect && !selectedOption?.icon) {
    selectIcon = (
      <GetCoinImg className={styles.coinImg} coin={value as string} />
    );
  } else if (selectedOption?.icon) {
    selectIcon =
      typeof selectedOption.icon === "string" ? (
        <img
          alt={selectedOption.value}
          src={selectedOption.icon}
          style={{ opacity: imgOpacity }}
          onLoad={(e) => {
            setImgOpacity(1);
          }}
          draggable={false}
          className={clsx(styles.coinImg, iconClass)}
        />
      ) : (
        selectedOption.icon
      );
  }

  return (
    <BaseSelect
      {...props}
      classes={{ ...classes }}
      value={value}
      onChange={onChange}
      className={clsx(styles.root, className)}
      component={
        <div className={styles.coinSelect}>
          {adornmentComponent}
          {selectIcon}
          <span className={clsx(styles.value, labelClass)}>
            {selectedOption?.name ? selectedOption.name : value}
          </span>
          {showLabel ? (
            <span className={clsx(styles.label, classes?.selectedAmount)}>
              {selectedOption?.amount
                ? selectedOption.amount
                : selectedOption?.label}
            </span>
          ) : null}
        </div>
      }>
      {options?.map((option) => {
        return <Item className={itemClass} key={option.value} {...option} />;
      })}
    </BaseSelect>
  );
};

export default Select;
