import React from "react";
import clsx from "clsx";

import { DialogActions, Typography } from "components";

import css from "./style.module.scss";

export const headerHeight = 81;

export interface DialogHeaderButtonOption {
  key: string;
  label: string;
}

export interface DialogHeaderProps {
  className?: string;
  title: string;
}

const DialogHeader = ({ title, className }: DialogHeaderProps) => {
  return (
    <DialogActions className={clsx(css.dialogActions, className)}>
      <Typography className={css.title}>{title}</Typography>

      <div className={css.grow} />
    </DialogActions>
  );
};

export default DialogHeader;
