import { createContext, Dispatch } from "react";
import { defaultControlFunctionList, defaultState } from "./provider";
import { controlFunctionsType, IAudioState } from "./types";

export interface ISoundSystemControlContext {
  soundSystemState: IAudioState;
  controlFunctions: controlFunctionsType;
}
const defaultValue: ISoundSystemControlContext = {
  soundSystemState: defaultState,
  controlFunctions: defaultControlFunctionList,
};

const Context = createContext(defaultValue);

export default Context;
