import React, { FunctionComponent } from "react";
import clsx from "clsx";

import { BaseSelect, SelectItem } from "components";
import { IconVariantKeys } from "components/Icon/config";
import css from "./style.module.scss";

export interface SelectOption {
  value: string;
  label: string;
  icon?: IconVariantKeys;
}

export interface SelectProps {
  className: string;
  value: string;
  handleChange: (value: string | number) => void;
  options: SelectOption[];
}

const FooterSelect: FunctionComponent<SelectProps> = ({
  className,
  value,
  handleChange,
  options,
}) => {
  return (
    <BaseSelect
      value={options?.find((option) => option.value === value)?.label || ""}
      classes={{
        root: clsx(css.select, className),
        label: css.selectLabel,
        expandIcon: css.selectIcon,
      }}
      onChange={handleChange}>
      {options?.map(({ value, label }) => (
        <SelectItem
          key={value}
          value={value}
          label={label}
          className={clsx(css.fontFamily, css.menuItem)}
        />
      ))}
    </BaseSelect>
  );
};

export default FooterSelect;
