import React from "react";

import { IconVariantKeys } from "components/Icon/config";
import { Desktop } from "./components/Desktop";

export interface HeaderButtonOption {
  key: string;
  icon?: IconVariantKeys;
  label: string;
}

export interface HeaderProps {
  options: HeaderButtonOption[];
  selectedIndex: number;
  onChange: (value: number) => void;
  withFairness?: boolean;
  smallButton?: boolean;
}

const Header = ({
  options,
  selectedIndex,
  onChange,
  withFairness,
  smallButton,
}: HeaderProps) => {
  return (
    <>
      <Desktop
        smallButton={smallButton}
        options={options}
        onChange={onChange}
        selectedIndex={selectedIndex}
        withFairness={withFairness}
      />
    </>
  );
};

export default Header;
