import React from "react";
import clsx from "clsx";

import { Button } from "components";

import { useMessages } from "components/Chat/chatContext";
import { ReactComponent as Arrow } from "./assets/arrow.svg";
import css from "./style.module.scss";

export const ScrollToBottom = () => {
  const { chatFunctions, isAutoScrollMode } = useMessages();

  return (
    <Button
      onClick={() => {
        chatFunctions.current.scrollToBottom();
        // whenScrolled: () => {
        //   setIsAutoScrollMode(true);
        // }
      }}
      className={clsx(css.button, isAutoScrollMode && css.disable)}>
      <Arrow className={css.arrow} />
    </Button>
  );
};
