import React, { FC, Suspense } from "react";

import LazyComponentLoader from "config/LazyComponentLoader";
import ProgressFallback from "components/fallbacks/Progress";
import { Dialog } from "containers/PopUps";

import { modalsLoaderColor } from "sass/styleVariables";
import { MoreHotKeys } from "./types";
import css from "./style.module.scss";

const Content = React.lazy(() =>
  LazyComponentLoader(import("./components/Content")),
);
const BlackjackContent = React.lazy(() =>
  LazyComponentLoader(import("./components/BlackjackContent")),
);

interface Props {
  onClose: () => void;
  isOpen: boolean;
  blackjack?: boolean;
  isMinesPage?: boolean;

  moreHotKeys?: MoreHotKeys[];
}

const HotKeysModal: FC<Props> = ({
  onClose,
  isOpen,
  blackjack,
  isMinesPage,

  moreHotKeys,
}) => {
  if (!isOpen) {
    return null;
  }

  return (
    <Dialog
      onClose={onClose}
      isOpen={isOpen}
      paper={{
        className: css.root,
      }}>
      <Suspense
        fallback={
          <ProgressFallback
            color={modalsLoaderColor}
            circularWidth={8}
            size={90}
          />
        }>
        {blackjack ? (
          <BlackjackContent onClose={onClose} />
        ) : (
          <Content
            isMinesPage={isMinesPage}
            onClose={onClose}
            moreSections={moreHotKeys}
          />
        )}
      </Suspense>
    </Dialog>
  );
};

export default HotKeysModal;
