import React, { FC, useEffect, useMemo, useState } from "react";
import clsx from "clsx";

import { repeat } from "utils";

import css from "./style.module.scss";

interface Props {
  actual: number;
  amount: number;
  onClick?: (index: number) => void;
  secondary?: boolean;
}

const intervals: NodeJS.Timeout[] = [];
const clearIntervals = () => {
  intervals.forEach((i) => clearTimeout(i));
  intervals.length = 0;
};

const Dots: FC<Props> = ({
  actual,
  amount,
  onClick = () => {},
  secondary = false,
}) => {
  const [animationPlay, setAnimationPlay] = useState(false);
  const dotSize = secondary ? 8 : 6,
    dotMargin = secondary ? 16 : 9;
  const calcWidth = useMemo(
    () => amount * (dotSize + dotMargin) - dotMargin,
    [amount],
  );

  useEffect(() => {
    clearIntervals();
    setAnimationPlay(true);
    const t = setTimeout(() => {
      setAnimationPlay(false);
    }, 200);

    intervals.push(t);
  }, [actual]);

  return (
    <div className={css.root}>
      <div style={{ width: calcWidth }} className={css.dotsWrapper}>
        {repeat(amount, (i) => (
          <div
            className={clsx(css.dot, secondary && css.secondary)}
            onClick={() => onClick(i)}
          />
        ))}

        <div
          className={css.moveDotWrapper}
          style={{
            transform: `translateX(${(dotSize + dotMargin) * (actual - 1)}px)`,
          }}>
          <div
            className={clsx(css.dot, css.moveDot, secondary && css.secondary)}
          />
        </div>
      </div>
    </div>
  );
};

export default Dots;
