import React, { FC, useContext } from "react";
import clsx from "clsx";

import { OptionsListContext } from "../..";
import styles from "./style.module.scss";

export interface Option {
  label: string;
  value: string | number;
}

export interface Props extends Omit<Option, "label"> {
  className?: string;
  selectedItemClass?: string;
}

const SelectItemBase: FC<Props> = ({
  value,
  className,
  selectedItemClass = styles.selected,
  children,
  ...props
}) => {
  const { setValue, value: selectedValue } = useContext(OptionsListContext);

  return (
    <div
      {...props}
      className={clsx(className, {
        [selectedItemClass]: selectedValue === value,
      })}
      onClick={() => {
        setValue(value);
      }}>
      {children}
    </div>
  );
};

export default SelectItemBase;
