import { HTMLFullScreenNode } from "../types";

const exitFullscreenFunctions = [
  "exitFullscreen",
  "mozCancelFullScreen",
  "mozExitFullscreen",
  "webkitExitFullscreen",
  "webkitCancelFullScreen",
  "msCancelFullScreen",
  "msExitFullscreen",
];

export const disableFullscreen = async (): Promise<void | never> => {
  const activeFullscreenFunctions = exitFullscreenFunctions.find((value) => {
    const request = value as keyof Document;
    if (document[request]) {
      return request;
    }
  });

  if (activeFullscreenFunctions) {
    //@ts-ignore bacause [typeof activeFullscreenFunctions === string] not [keyof Document]
    return await document[activeFullscreenFunctions]();
  }

  throw {
    message: "exitFullscreen function does not support in this device",
  };
};
