import { notifyBarManager } from "components";
import { ServerDataAnswer } from "hooks";
import { fetchBase } from "utils";

const callError = (error: string) => {
  notifyBarManager.error({
    timeOut: 5000,
    message: error,
  });
};

export const getData = async <T extends ServerDataAnswer>(
  link: string,
  showError?: boolean,
) => {
  const result = await fetchBase<T>(link);

  if (result?.error) {
    console.error(
      `useCoins request error.
      - Request link: ${link}
      - Request error:`,
      result?.error,
      `
      - Request result:`,
      result,
    );

    if (showError) {
      callError(result.error);
    }
  } else if (result?.data?.success) {
    return result.data;
  } else {
    callError("Something went wrong");
  }
};
