import React, { FC, createContext, useState, useRef } from "react";
import clsx from "clsx";

import { ReactComponent as ArrowDown } from "assets/icons/select-arrow.svg";
import { PopUpRoot } from "containers/PopUps/";
import { Option } from "./components/SelectItemBase";
import styles from "./style.module.scss";

export const OptionsListContext = createContext<{
  setValue: (value: Option["value"]) => void;
  value: Option["value"];
}>({
  setValue: () => {},
  value: "",
});

export interface SelectClasses {
  root: string;
  label: string;
  optionsContainer: string;
  expandIcon: string;

  open: string;
}

export interface SelectProps {
  value: Option["value"];
  onChange: (value: Option["value"]) => void;
  component?: JSX.Element;
  ArrowComponent?: React.FC;
  className?: string;
  openFromUpwards?: boolean;
  disabled?: boolean;
  classes?: Partial<SelectClasses>;
}

const defaultMenuItemParameters = {
  isOpen: false,
  offsetLeft: 0,
  offsetTop: 0,
  width: 0,
};

const Select: FC<SelectProps> = ({
  value,
  onChange,
  children,
  component,
  ArrowComponent,
  className,
  disabled,
  openFromUpwards = true,
  classes,
}) => {
  const [selectMenuParameters, setSelectMenuParameters] = useState(
    defaultMenuItemParameters,
  );
  const offsetCompleted = useRef(false);

  const closeMenu = () => {
    offsetCompleted.current = false;
    setSelectMenuParameters({ ...defaultMenuItemParameters });
  };

  return (
    <div
      className={clsx(styles.select, classes?.root, className, {
        [styles.disabled]: disabled,
      })}
      onClick={(event) => {
        if (disabled) return;
        const target = event.currentTarget;
        const { x: offsetLeft, y } = target.getBoundingClientRect();
        setSelectMenuParameters({
          isOpen: true,
          offsetLeft,
          offsetTop:
            y +
            target.offsetHeight -
            (openFromUpwards ? 0 : target.clientHeight),
          width: target.offsetWidth,
        });
      }}>
      {component ? (
        component
      ) : (
        <div className={clsx(styles.title, classes?.label)}>
          <span>{value}</span>
        </div>
      )}
      <div
        className={clsx(styles.arrow, classes?.expandIcon, {
          [classes?.open || styles.arrowOpen]: selectMenuParameters.isOpen,
        })}>
        {ArrowComponent ? <ArrowComponent /> : <ArrowDown />}
      </div>
      <OptionsListContext.Provider
        value={{
          setValue: (value) => {
            closeMenu();
            if (disabled) return;
            onChange(value);
          },
          value: value,
        }}>
        {selectMenuParameters.isOpen ? (
          <PopUpRoot curtainOnClick={closeMenu}>
            <div
              className={clsx(styles.options, classes?.optionsContainer, {
                [styles.optionsUpwards]: !openFromUpwards,
              })}
              ref={(ref) => {
                if (!ref || offsetCompleted.current) return;
                offsetCompleted.current = true;
                let optionsTopOffset = 0;
                if (openFromUpwards) {
                  optionsTopOffset =
                    ref.offsetTop < ref.offsetHeight
                      ? ref.offsetTop - 60 // indent from the header
                      : ref.offsetHeight;
                } else {
                  const occupiedHeight = ref.offsetHeight + ref.offsetTop;
                  optionsTopOffset =
                    occupiedHeight >= window.innerHeight
                      ? occupiedHeight - window.innerHeight + 10 // indent from the footer
                      : 0;
                }
                setSelectMenuParameters((state) => ({
                  ...state,
                  offsetTop: state.offsetTop - optionsTopOffset,
                }));
              }}
              style={{
                top: selectMenuParameters.offsetTop,
                left: selectMenuParameters.offsetLeft,
                width: selectMenuParameters.width,
              }}>
              {children}
            </div>
          </PopUpRoot>
        ) : null}
      </OptionsListContext.Provider>
    </div>
  );
};

export default Select;
