import React, { Dispatch } from "react";
import { BalanceAnimationReducerActions, BalanceElement } from "./reducer";

type BalanceAnimationContextType = {
  balanceUpdate: BalanceElement[];
  balanceDispatch: Dispatch<BalanceAnimationReducerActions>;
};

export const defaultValue: BalanceAnimationContextType = {
  balanceUpdate: [],
  balanceDispatch: () => {},
};

export const Context =
  React.createContext<BalanceAnimationContextType>(defaultValue);

export default Context;
