import { useEffect, useState } from "react";

const funcs = {
  changeVisibility: (value: boolean) => {},
};
const handleVisibility = () => {
  funcs.changeVisibility(document.hidden);
};

window.addEventListener("visibilitychange", handleVisibility);

export const useVisibility = () => {
  const [visibility, setVisibility] = useState({ isHidden: document.hidden });

  useEffect(() => {
    funcs.changeVisibility = (value: boolean) => {
      setVisibility({ isHidden: value });
    };

    return () => {
      funcs.changeVisibility = () => {};
    };
  }, []);

  return visibility;
};
