import { useEffect, useState } from "react";
import { whenDocumentLoaded } from "utils";

export const useDocumentLoaded = (state?: DocumentReadyState) => {
  const [isLoaded, setIsLoaded] = useState(false);

  useEffect(() => {
    const clear = whenDocumentLoaded(() => {
      setIsLoaded(true);
    }, state);

    return clear;
  }, []);

  return isLoaded;
};
