import React, { CSSProperties, FunctionComponent } from "react";

import { useWindowSize } from "providers";

import { Scrollbar } from "components/Scrollbar";
import { mobileScreen } from "sass/styleVariables";
import { spacing } from "sass/styleFunctions";

import { headerHeight } from "..";
import css from "./style.module.scss";
import clsx from "clsx";

export interface TemplateProps {
  scrollbarStyle?: CSSProperties;
  classes?: {
    wrapper?: string;
    root?: string;
    innerWrapper?: string;
  };
}

const scrollProps = {
  style: {
    display: "block",
  },
};

const Template: FunctionComponent<TemplateProps> = ({
  children,
  scrollbarStyle = {},
  classes,
}) => {
  const { width, height } = useWindowSize();

  const isSm = width <= mobileScreen;

  const scrollStyles = {
    minWidth: "100%",
    maxHeight: height - (headerHeight || 0) - (isSm ? 0 : spacing(8)),
    ...scrollbarStyle,
  };

  return (
    <div className={clsx(css.wrapper, classes?.wrapper)}>
      <Scrollbar
        translateContentSizeYToHolder
        noScrollX
        style={scrollStyles}
        contentProps={scrollProps}>
        <div className={clsx(css.root, classes?.root)}>
          <div className={clsx(css.innerWrapper, classes?.innerWrapper)}>
            {children}
          </div>
        </div>
      </Scrollbar>
    </div>
  );
};

export default Template;
