import React, { FC, useState, useEffect } from "react";

import { useEventListener } from "hooks";

export interface HiddenComponentProps {
  up?: boolean;
  limitWidth: number;
}

const Hidden: FC<HiddenComponentProps> = ({ children, limitWidth, up }) => {
  const [hide, setHide] = useState(false);

  const hideElement = () => {
    const windowWidth = window.innerWidth;
    setHide(
      (windowWidth >= limitWidth && up) || (windowWidth <= limitWidth && !up),
    );
  };

  useEffect(() => {
    hideElement();
  }, []);

  useEventListener("resize", hideElement);

  return <>{!hide ? children : null}</>;
};

export default Hidden;
