import React, { FC, useEffect, useState, useMemo } from "react";

import { ProgressiveModal, GetCoinImg } from "components";
import { DialogHeader } from "pages/ModalPages/componentsProgressive";
import Template from "pages/ModalPages/componentsProgressive/Template";

import styles from "./style.module.scss";

interface Props {
  game: string;
  isOpen: boolean;
  onClose: () => void;
}

interface BetInfo {
  coin: string;
  minimum_bet: string;
  maximum_bet: string;
  maximum_profit: string;
}

interface ConfigurationResponse {
  success: boolean;
  coins: Array<BetInfo>;
}

const BettingInformation: FC<Props> = ({ isOpen, onClose, game }) => {
  const [betsInfo, setBetsInfo] = useState<Array<BetInfo>>([]);

  const getBettingInformation = async () => {
    const bettingInformationResponse = await fetch(
      `${process.env.REACT_APP_URL}/${game}/get-configuration`,
    );
    const bettingInformation: ConfigurationResponse =
      await bettingInformationResponse.json();
    if (bettingInformation.success) setBetsInfo(bettingInformation.coins);
  };

  useEffect(() => {
    if (isOpen) {
      getBettingInformation();
    }
  }, [isOpen]);

  const betsConfiguration = useMemo(
    () =>
      betsInfo.map((bet) => (
        <tr key={`BettingInformation_${bet.coin}`}>
          <td>{bet.coin}</td>
          <td>
            {Number(bet.minimum_bet).toLocaleString("en-US", {
              maximumFractionDigits: 8,
            })}
            <GetCoinImg className={styles.coin} coin={bet.coin} />
          </td>
          <td>
            {Number(bet.maximum_bet)}
            <GetCoinImg className={styles.coin} coin={bet.coin} />
          </td>
          <td>
            {Number(bet.maximum_profit)}
            <GetCoinImg className={styles.coin} coin={bet.coin} />
          </td>
        </tr>
      )),
    [betsInfo],
  );

  return (
    <ProgressiveModal open={isOpen} width={600} onClose={onClose}>
      <DialogHeader title={"Betting Information"} />
      <Template>
        <div className={styles.root}>
          <table className={styles.table}>
            <thead>
              <tr>
                <th>Coin</th>
                <th>Min Bet</th>
                <th>Max Bet</th>
                <th>Max Profit</th>
              </tr>
            </thead>
            <tbody>{betsConfiguration}</tbody>
          </table>
        </div>
      </Template>
    </ProgressiveModal>
  );
};

export default BettingInformation;
