type Methods = "add" | "remove";

export const calcSmallNumber = (
  firstValue: number,
  secondValue: number,
  method: Methods,
) => {
  const firstNumberLength = String(firstValue).length;
  const secondNumberLength = String(secondValue).length;
  const numberLength = 8 + Math.max(firstNumberLength, secondNumberLength);
  const multiplier = Number("1".padEnd(numberLength, "0"));

  switch (method) {
    default:
    case "add":
      const addResult =
        Math.floor(firstValue * multiplier) +
        Math.floor(secondValue * multiplier);
      return addResult / multiplier;

    case "remove":
      const removeResult =
        Math.floor(firstValue * multiplier) -
        Math.floor(secondValue * multiplier);
      return removeResult / multiplier;
  }
};
