import React, { forwardRef, Suspense, useRef } from "react";
import type { Scrollbar as ScrollbarInstance } from "react-scrollbars-custom";
import LazyComponentLoader from "config/LazyComponentLoader";
import { ScrollbarProps } from "./type";

export * from "./type";

const CustomScrollBar = React.lazy(() =>
  LazyComponentLoader(import("./customScrollBar")),
);

export const Scrollbar = forwardRef<ScrollbarInstance, ScrollbarProps>(
  ({ ...rest }, ref) => {
    return (
      <Suspense fallback={null}>
        <CustomScrollBar {...rest} ref={ref} />
      </Suspense>
    );
  },
);
