import React from "react";

export interface ServerSeed {
  current_seed_hashed: string;
  next_seed_hashed: string;
  games_played: string;
}

export interface ClientSeed {
  seed: string;
  isSetManually: boolean;
}

export type SeedContextType = {
  serverSeed: ServerSeed | null;
  getServerSeed: () => void;
  rotateServerSeed: () => void;

  clientSeed?: ClientSeed | null;
  setClientSeed: (seed: ClientSeed) => void;
  rotateClientSeed: () => ClientSeed;
  updateNonce: () => void;
};

export const defaultValues: SeedContextType = {
  serverSeed: null,
  getServerSeed: () => {},
  rotateServerSeed: () => {},

  setClientSeed: () => {},
  rotateClientSeed: () => ({} as ClientSeed),
  updateNonce: () => {},
};

export const Context = React.createContext<SeedContextType>(defaultValues);

export default Context;
