import { useEffect } from "react";

export const useTouchPreventDefault = (
  box: HTMLDivElement | null,
  listenerOptions?: boolean | AddEventListenerOptions,
) => {
  useEffect(() => {
    const element = box || document;
    const defaultFunc = (event: Event) => {
      if (box) event.preventDefault();
    };

    element.addEventListener("touchmove", defaultFunc, listenerOptions);
    element.addEventListener("touchstart", defaultFunc, listenerOptions);

    return () => {
      element.removeEventListener("touchmove", defaultFunc, listenerOptions);
      element.removeEventListener("touchstart", defaultFunc, listenerOptions);
    };
  }, [box]);
};
