import React, { Dispatch, SetStateAction } from "react";

export type ThemeEvents = "christmas";

export type WhiteThemeContextType = {
  isWhiteThemeActive: boolean;
  activeEvent?: ThemeEvents;
  setIsWhiteThemeActive: Dispatch<SetStateAction<boolean>>;
};

export const defaultValue: WhiteThemeContextType = {
  isWhiteThemeActive: false,
  setIsWhiteThemeActive: () => {},
};

export const Context = React.createContext<WhiteThemeContextType>(defaultValue);

export default Context;
