import { createSuspenseComponent } from "utils";
import Cropper, {
  ReactCropperDefaultOptions,
  ReactCropperElement,
  ReactCropperProps,
} from "./types";

const applyDefaultOptions = (
  cropper: Cropper,
  options: ReactCropperDefaultOptions = {},
): void => {
  const {
    enable = true,
    scaleX = 1,
    scaleY = 1,
    zoomTo = 0,
    rotateTo,
  } = options;
  enable ? cropper.enable() : cropper.disable();
  cropper.scaleX(scaleX);
  cropper.scaleY(scaleY);
  rotateTo !== undefined && cropper.rotateTo(rotateTo);
  zoomTo > 0 && cropper.zoomTo(zoomTo);
};

const ReactCropper = createSuspenseComponent<
  ReactCropperProps,
  ReactCropperElement | HTMLImageElement
>(import("./component"), true);

export { ReactCropper, applyDefaultOptions };
