import React, { FC, useState } from "react";
import {
  OptionsButton,
  HotKeysModal,
  LiveStats,
  SoundModals,
} from "components";
import BettingInformation from "pages/ModalPages/pages/BettingInformation";
import { MoreHotKeys } from "components/HotKeys/Modal/types";
import css from "./style.module.scss";

interface Props {
  game?: string;

  moreHotKeys?: MoreHotKeys[];
}

export const Options: FC<Props> = ({ game, moreHotKeys }) => {
  const [modals, setModals] = useState({
    bettingInformation: false,
    liveStats: false,
    hotKeys: false,
    sound: false,
  });

  return (
    <>
      <HotKeysModal
        isOpen={modals.hotKeys}
        onClose={() => setModals((old) => ({ ...old, hotKeys: false }))}
        moreHotKeys={moreHotKeys}
      />
      <LiveStats
        isOpen={modals.liveStats}
        handleClose={() =>
          setModals((old) => ({
            ...old,
            liveStats: false,
          }))
        }
      />
      <SoundModals
        isOpen={modals.sound}
        onClose={() => setModals((old) => ({ ...old, sound: false }))}
      />
      {game && (
        <BettingInformation
          onClose={() =>
            setModals((old) => ({
              ...old,
              bettingInformation: false,
            }))
          }
          isOpen={modals.bettingInformation}
          game={game}
        />
      )}

      <OptionsButton
        className={css.buttons}
        withSound
        hotKeysOnClick={() =>
          setModals((old) => ({ ...old, hotKeys: !old.hotKeys }))
        }
        liveStatOnclick={() =>
          setModals((old) => ({ ...old, liveStats: !old.liveStats }))
        }
        soundOnClick={() => setModals((old) => ({ ...old, sound: !old.sound }))}
        bettingInformationOnClick={
          game
            ? () =>
                setModals((old) => ({
                  ...old,
                  bettingInformation: !old.bettingInformation,
                }))
            : undefined
        }
      />
    </>
  );
};
