import React, { DetailedHTMLProps, FC, HTMLAttributes } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { Button, LinkWithSearch } from "components";

import css from "./style.module.scss";

type Variant = "login" | "register";

interface Props
  extends DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement> {
  active?: Variant;
  withBorder?: boolean;
  aloneElement?: boolean;
}

const createForwardedLink = (value: Variant) => {
  return React.forwardRef<HTMLAnchorElement | null>((props, ref) => (
    <LinkWithSearch
      {...props}
      // @ts-ignore
      className={clsx(props?.className, css.link)}
      search={{ name: "page", value }}
    />
  ));
};

const LogIn = createForwardedLink("login");
const Register = createForwardedLink("register");

export const LoginButton: FC<Props> = ({
  active = "register",
  withBorder,
  aloneElement,

  className,
  ...props
}) => {
  const intl = useGetLocale();

  return (
    <div
      className={clsx(
        css.root,
        withBorder && css.border,
        className,
        aloneElement && css.mobileRoot,
      )}
      {...props}>
      {!aloneElement && (
        <Button
          base
          className={clsx(css.button, active === "login" && css.active)}
          component={LogIn}>
          {intl.formatMessage({ id: "login" })}
        </Button>
      )}
      <Button
        base
        className={clsx(css.button, active === "register" && css.active)}
        component={Register}>
        {intl.formatMessage({ id: "register" })}
      </Button>
    </div>
  );
};

export default LoginButton;
