export interface CoinData {
  symbol: string;
  name: string;
  price: string;
  image: string;
}

export type ServerDataAnswer = {
  success: boolean;
  message: string;
};

export interface CoinsPriceData extends ServerDataAnswer {
  coins: CoinData[];
}

export interface CoinsFetchData extends ServerDataAnswer {
  coins: Coin[];
}

export interface Coin {
  coin: CoinName;
  image: string;
  balance: string;
  price?: string;
}

export type CoinName =
  | "BTC"
  | "BTT"
  | "DOGE"
  | "ETH"
  | "FEY"
  | "GEM"
  | "LTC"
  | "NANO"
  | "TRX"
  | "USDT"
  | string;

export interface CoinPriceElement {
  name: string;
  price: string;
}

export type CoinPriceList = {
  [key in CoinName]: CoinPriceElement;
};

export type CoinsState = {
  [key in CoinName]: Coin;
};

export enum CoinEvents {
  UPDATE_SELECTED_COIN = "UPDATE_SELECTED_COIN",
  UPDATE_COINS_LIST = "UPDATE_COINS_LIST",
  UPDATE_SHOW_IN_USD = "UPDATE_SHOW_IN_USD",

  UPDATE = "UPDATE",
}

export type CoinsUpdateProps =
  | {
      type: "add";
      coin: Coin;
    }
  | {
      type: "update";
      coins: Coin[];
    };

export interface CoinsListenerProps {
  list: Coin[];
  state: CoinsState;
}

export interface CoinsHookProps {
  selectedCoin: Coin | undefined;
  coinsList: Coin[];
  coinsState: CoinsState;
  coinsPrices: CoinPriceList;
  showInUSD: boolean;

  // setCoins: (value: CoinsUpdateProps) => void;
  // setSelectedCoin: (value: string) => void;
  // setShowInUSD: (value: boolean) => void;
  // coinUpdate: (newCoinValue: string, coinName: string) => void;
}

export interface ManagerListenerValues {
  selectedCoin: Coin | undefined;
  walletCoinsList: Coin[];
  walletCoinsState: CoinsState;
  coinPriceState: CoinPriceList;
  showInUSD: boolean;
}
