import { Dispatch, SetStateAction, useEffect, useState } from "react";

import { CoinsHookProps, ManagerListenerValues } from "hooks";

import { coinsManager } from "./Emitter";

const createState = (values: ManagerListenerValues): CoinsHookProps => {
  return {
    selectedCoin: values.selectedCoin,
    coinsList: values.walletCoinsList,
    coinsState: values.walletCoinsState,
    coinsPrices: values.coinPriceState,
    showInUSD: values.showInUSD,
  };
};

const updateStateFactory = (
  setState: Dispatch<SetStateAction<CoinsHookProps>>,
  memoValue?: (keyof CoinsHookProps)[],
) => {
  let prevState: {
    [key: string]: any;
  } = {};

  return (values: ManagerListenerValues) => {
    const state = createState(values);
    if (memoValue) {
      memoValue.forEach((value) => {
        if (prevState?.hasOwnProperty(value)) {
          if (prevState[value] !== state[value]) {
            prevState[value] = state[value];
            setState(state);
          }
        } else {
          setState(state);
          prevState = state;
        }
      });
    } else {
      setState(state);
    }
  };
};

export const useCoins = (
  memoValue?: (keyof CoinsHookProps)[] | keyof CoinsHookProps,
) => {
  const [state, setState] = useState(
    createState(coinsManager.createReturnValues()),
  );

  useEffect(() => {
    const updateState = updateStateFactory(
      setState,
      typeof memoValue === "string" ? [memoValue] : memoValue,
    );

    const listener = (values: ManagerListenerValues) => {
      updateState(values);
    };

    const removeListener = coinsManager.listener(listener);

    return () => {
      coinsManager.closeListener(listener);
      removeListener();
    };
  }, []);

  return state;
};
