import React, { FC } from "react";
import clsx from "clsx";

import { PopperProps } from "components/Popper/types";

import css from "./style.module.scss";

interface Props {
  position?: PopperProps["position"];
  color?: string;
  className?: string;
}

export const Triangle: FC<Props> = ({ position = "top", color, className }) => {
  const style = {
    color,
  };
  return (
    <div className={clsx(css.root, css[position], className)} style={style} />
  );
};
