import React, { useEffect, useState } from "react";
import { createPortal } from "react-dom";
import clsx from "clsx";

import { useChat, useWindowSize } from "providers";
import { createTimeouts } from "utils";

import { Messages, Write } from "components/Chat/components";
import { useMessages } from "components/Chat/chatContext";
import { Header } from "./components";
import css from "./style.module.scss";

const rootDom = document.getElementById("pop-up");
const animationTime = parseInt(css.transitionTime);
const { clearTimeouts, pushTimeout } = createTimeouts();

export const Mobile = () => {
  const { isHide, setIsHide } = useChat();
  const { chatFunctions, setDevice } = useMessages();
  const [isReady, setIsReady] = useState(false);
  const [disabled, setDisabled] = useState(isHide);
  const { width } = useWindowSize();

  useEffect(() => {
    setDevice("mobile");

    setIsHide(true);
    pushTimeout(() => {
      setIsReady(true);
    }, animationTime + 100);

    return () => {
      clearTimeouts();
    };
  }, []);

  useEffect(() => {
    chatFunctions.current.scrollToBottom();
  }, [width]);

  useEffect(() => {
    if (isHide)
      pushTimeout(() => {
        setDisabled(true);
      }, animationTime);
    else {
      setDisabled(false);
      chatFunctions.current.scrollToBottom();
    }
  }, [isHide]);

  return createPortal(
    <div className={clsx(css.root, isHide && css.hidden, isReady && css.ready)}>
      <Header />
      <div className={clsx(css.content, disabled && css.disabled)}>
        <Messages />
        <Write />
      </div>
    </div>,
    rootDom as HTMLDivElement,
  );
};
