import React, { Component, ErrorInfo } from "react";
import { track } from "utils";

import MessagesProvider from "./chatContext/Provider";
import { Desktop, Mobile } from "./wrapper";

interface Props {
  type: "desktop" | "mobile";
}

const wrapper = {
  desktop: Desktop,
  mobile: Mobile,
};

export class Chat extends Component<Props> {
  state = {
    isError: false,
    errorAmount: 0,
  };

  componentDidCatch(error: Error, info: ErrorInfo) {
    console.error(
      " >>>> Chat error: ",
      error,
      "\n",
      "Component Stack: ",
      info.componentStack,
      "\n",
      "info: ",
      info,
    );

    track()?.errors(error, {
      custom: {
        "Component Stack": info.componentStack,
      },
    });

    this.setState({
      ...this.state,
      isError: true,
    });
  }

  render() {
    const { isError, errorAmount } = this.state;
    const { type } = this.props;
    const WrapperComponent = wrapper[type];

    if (isError) {
      if (errorAmount < 10) {
        setTimeout(() => {
          this.setState({ isError: false, errorAmount: errorAmount + 1 });
        }, 100);
      }

      return null;
    }

    return (
      <MessagesProvider>
        <WrapperComponent />
      </MessagesProvider>
    );
  }
}
