import React, { forwardRef, Suspense } from "react";
import clsx from "clsx";

import { useWhiteTheme } from "providers";

import Hidden from "components/Hidden";
import { mobileScreen } from "sass/styleVariables";
import LazyComponentLoader from "config/LazyComponentLoader";

import FooterRightBlock from "./FooterRightBlock";
import FooterLeftBlock from "./FooterLeftBlock";
import { SelectOption } from "./FooterSelect";
import MobileFooter from "./MobileFooter";
import css from "./style.module.scss";

export const footerId = "footer_element";
export const supportedLanguages: SelectOption[] = [
  { value: "en", label: "English" },
  { value: "es", label: "Español" },
  { value: "tr", label: "Türkçe" },
  { value: "ru", label: "Русский" },

  { value: "id", label: "Bahasa Indonesia" },
  { value: "de", label: "Deutsch" },
  { value: "jp", label: "日本語" },
  { value: "pl", label: "Polski" },
];

const EventDecoration = React.lazy(() =>
  LazyComponentLoader(import("./EventDecoration")),
);

const FooterComponent = forwardRef<HTMLElement>((props) => {
  const { activeEvent } = useWhiteTheme();
  return (
    <footer
      id={footerId}
      className={clsx(css.footer, activeEvent && css[activeEvent])}>
      <Hidden limitWidth={mobileScreen + 1} up>
        <MobileFooter />
      </Hidden>
      <Hidden limitWidth={mobileScreen}>
        <div className={css.container}>
          <FooterLeftBlock />
          <FooterRightBlock />

          {activeEvent && (
            <Suspense fallback={null}>
              <EventDecoration event={activeEvent} />
            </Suspense>
          )}
        </div>
      </Hidden>
    </footer>
  );
});

export default FooterComponent;
