import React, { FC } from "react";
import clsx from "clsx";

import { typographyVariant } from "./types";
import css from "./style.module.scss";

type VariantElementProps = React.DetailedHTMLProps<
  React.HTMLAttributes<
    HTMLParagraphElement & HTMLHeadingElement & HTMLSpanElement
  >,
  HTMLParagraphElement & HTMLHeadingElement & HTMLSpanElement
>;

interface Props extends VariantElementProps {
  variant?: typographyVariant;
}

export const Typography: FC<Props> = ({
  children,
  variant,
  className,
  ...props
}) => {
  const setPropsDependingOnTheVariant = (
    value?: string,
  ): VariantElementProps => {
    const variantClass = clsx(css.root, value, className);
    return {
      className: variantClass,
      ...props,
    };
  };

  switch (variant) {
    case "body2":
      return <p {...setPropsDependingOnTheVariant(css.body2)}>{children}</p>;
    case "button":
      return (
        <span {...setPropsDependingOnTheVariant(css.button)}>{children}</span>
      );
    case "caption":
      return (
        <span {...setPropsDependingOnTheVariant(css.caption)}>{children}</span>
      );

    case "inherit":
      return <span {...setPropsDependingOnTheVariant()}>{children}</span>;
    case "overline":
      return (
        <span {...setPropsDependingOnTheVariant(css.overline)}>{children}</span>
      );
    case "srOnly":
      return (
        <span {...setPropsDependingOnTheVariant(css.srOnly)}>{children}</span>
      );
    case "subtitle1":
      return (
        <h6 {...setPropsDependingOnTheVariant(css.subtitle1)}>{children}</h6>
      );
    case "subtitle2":
      return (
        <h6 {...setPropsDependingOnTheVariant(css.subtitle2)}>{children}</h6>
      );
    case "h1":
      return <h1 {...setPropsDependingOnTheVariant(css.h1)}>{children}</h1>;
    case "h2":
      return <h2 {...setPropsDependingOnTheVariant(css.h2)}>{children}</h2>;
    case "h3":
      return <h3 {...setPropsDependingOnTheVariant(css.h3)}>{children}</h3>;
    case "h4":
      return <h4 {...setPropsDependingOnTheVariant(css.h4)}>{children}</h4>;
    case "h5":
      return <h5 {...setPropsDependingOnTheVariant(css.h5)}>{children}</h5>;
    case "h6":
      return <h6 {...setPropsDependingOnTheVariant(css.h6)}>{children}</h6>;
    case "body1":
    default:
      return <p {...setPropsDependingOnTheVariant(css.body1)}>{children}</p>;
  }
};
