import React, { Dispatch, SetStateAction } from "react";

export type CoinsContextType = {
  setIsHotKeysPaused: Dispatch<SetStateAction<boolean>>;
  setIsHotkeyActive: Dispatch<SetStateAction<boolean>>;
  isHotkeyActive: boolean;
  isHotKeysPaused: boolean;
};

export const defaultValue: CoinsContextType = {
  setIsHotKeysPaused: () => {},
  setIsHotkeyActive: () => {},
  isHotKeysPaused: false,
  isHotkeyActive: false,
};

export const Context = React.createContext<CoinsContextType>(defaultValue);

export default Context;
