import { EventEmitter } from "events";

export enum NotificationType {
  INFO,
  SUCCESS,
  WARNING,
  ERROR,
}

export enum Events {
  CHANGE = "change",
  REMOVE = "remove",
}

export interface Notification {
  id: number;
  type: NotificationType;
  title?: string;
  message: string;
  timeOut: number;
}

export interface PartialNotification
  extends Omit<Notification, "id" | "type" | "timeOut"> {
  timeOut?: number;
}

const DEFAULT_TIMEOUT = 2500;

class NotifyBarManager extends EventEmitter {
  private readonly notificationMap: Map<number, Notification>;
  private readonly removingNotifications: Record<number, boolean>;

  constructor() {
    super();
    this.notificationMap = new Map();
    this.removingNotifications = {};
  }

  public isRemovingNotification(notificationId: number) {
    return this.removingNotifications[notificationId] || false;
  }

  public warning = this.createNotifyEvent(NotificationType.WARNING);
  public success = this.createNotifyEvent(NotificationType.SUCCESS);
  public error = this.createNotifyEvent(NotificationType.ERROR);
  public info = this.createNotifyEvent(NotificationType.INFO);

  private createNotifyEvent(notificationType: NotificationType) {
    return ({
      timeOut = DEFAULT_TIMEOUT,
      ...notification
    }: PartialNotification) => {
      this.createNotification({
        type: notificationType,
        timeOut,
        ...notification,
      });
    };
  }

  public deleteNotification(notificationId: number) {
    if (this.notificationMap.has(notificationId)) {
      delete this.removingNotifications[notificationId];
      this.notificationMap.delete(notificationId);
      this.emitChange();
    }
  }

  public deleteNotificationRequest(notificationId: number) {
    this.removingNotifications[notificationId] = true;
    this.emit(Events.REMOVE, notificationId);
  }

  private createNotification(notification: Omit<Notification, "id">) {
    const id = Date.now();
    this.notificationMap.set(id, {
      ...notification,
      id,
    });
    this.emitChange();
    setTimeout(() => {
      this.deleteNotificationRequest(id);
    }, notification.timeOut);
  }

  private emitChange() {
    this.emit(Events.CHANGE, Array.from(this.notificationMap.values()));
  }

  addChangeListener(callback: (notificationList: Notification[]) => void) {
    this.addListener(Events.CHANGE, callback);
  }

  addRemoveListener(callback: (notificationId: number) => void) {
    this.addListener(Events.REMOVE, callback);
  }

  removeNotifyListeners() {
    for (const event of Object.values(Events)) {
      this.removeAllListeners(event);
    }
  }
}

export default new NotifyBarManager();
