import React, { DetailedHTMLProps, FC, useEffect } from "react";
import clsx from "clsx";
import { useSassImport } from "hooks";

interface Props
  extends DetailedHTMLProps<
    React.HTMLAttributes<HTMLDivElement>,
    HTMLDivElement
  > {
  in?: boolean;
  animation?: "grow" | "fade";
}

let firstRender = true;

export const Transition: FC<Props> = ({
  children,
  in: isOpen,
  className,
  animation = "grow",
  ...props
}) => {
  const css = useSassImport(() => import("./style.module.scss"));

  useEffect(() => {
    if (isOpen) {
      firstRender = false;
    }
    return () => {
      firstRender = true;
    };
  }, [isOpen]);

  return (
    <div
      {...props}
      className={clsx(
        css.play,
        css[animation],
        isOpen ? css.playShow : !firstRender ? css.playHide : css.disable,
        className,
      )}>
      {children}
    </div>
  );
};
