import React, { FC, useEffect, useState } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { ReactComponent as AllInclusiveIcon } from "assets/icons/all-inclusive.svg";
import { Input, InputProps, InputAdornment } from "components";
import { useSassImport } from "hooks";

interface Props extends InputProps {
  className?: string;
  colorValue?: string;
}

const timeouts: NodeJS.Timeout[] = [];
const clearTimeouts = () =>
  timeouts.forEach((timeout) => clearTimeout(timeout));

export const NumberOfBetsInput: FC<Props> = ({
  className,
  colorValue,
  value,
  ...props
}) => {
  const intl = useGetLocale();
  const css = useSassImport(() => import("./style.module.scss"));

  const [isInfinity, setIsInfinity] = useState(!value);

  useEffect(() => {
    const checkValue = Number(value);

    clearTimeouts();
    if (!!checkValue) {
      const t = setTimeout(() => {
        setIsInfinity(false);
      }, 1000);
      timeouts.push(t);
    } else {
      setIsInfinity(true);
    }
  }, [value]);

  return (
    <div className={clsx(css.wrapper, className)}>
      {/* @ts-ignore */}
      <Input
        value={value}
        {...props}
        classes={{
          root: clsx(css.root, {
            [css.infinityInput]: !value,
          }),
          disabled: css.disabled,
        }}
        label={intl.formatMessage({
          id: "plinko.autoBet.numberOfBets",
        })}
        endAdornment={
          isInfinity ? (
            <InputAdornment position="end">
              <AllInclusiveIcon
                className={clsx(css.infinityImg, {
                  [css.showInfinityImg]: !Number(value),
                })}
              />
            </InputAdornment>
          ) : null
        }
      />
    </div>
  );
};
