import { useEffect } from "react";
import { useLocation } from "react-router";

import { useWhiteTheme } from "providers";

enum PathTheme {
  "/default" = "default",
  "/crash" = "crash",
  "/affiliate" = "affiliate",
  "/blackjack" = "blackjack",
  "/diamonds" = "diamonds",
  "/hash-dice" = "dice",
  "/contact-us" = "dice",
  "/dice-api" = "dice",
  "/keno" = "keno",
  "/limbo" = "limbo",
  "/mines" = "mines",
  "/roulette" = "roulette",
  "/fortune-quest" = "fortune",
  "/plinko" = "plinko",
  "/slot" = "slots",
  "/video-poker" = "video-poker",
  "/wager-mining" = "wager",
  "/white" = "white",
  "/slot-games" = "slots",
  "/towers" = "tower",
}

export const useTheme = () => {
  const { isWhiteThemeActive } = useWhiteTheme();
  const { pathname } = useLocation();

  useEffect(() => {
    const className: string = isWhiteThemeActive
      ? PathTheme["/white"]
      : //@ts-ignore
        PathTheme[pathname] || "";

    document.body.className = className;

    return () => {
      document.body.className = "";
    };
  }, [isWhiteThemeActive, pathname]);
};
