import React from "react";
import { useGetLocale } from "providers";

import { Logo } from "components";

import css from "./style.module.scss";

const FooterLeftBlock = () => {
  const intl = useGetLocale();

  return (
    <div className={css.leftColumn}>
      <div className={css.logoWrapper}>
        <Logo
          className={css.logo}
          logoVariant={"footerPasino"}
          to={{ pathname: "/" }}
        />
      </div>
      <div className={css.description}>
        <div className={css.copyright}>
          {intl.formatMessage({ id: "footer.copyright" })}
        </div>
        <div className={css.rights}>
          {intl.formatMessage({ id: "footer.rights" })}
        </div>
      </div>
    </div>
  );
};

const memoFroze = () => true;

export default React.memo(FooterLeftBlock, memoFroze);
