export interface BalanceElement {
  id: string;
  value: string;
}

export type BalanceAnimationReducerActions =
  | { type: "clean" }
  | { type: "replace"; value: BalanceElement }
  | { type: "update"; value: BalanceElement }
  | { type: "filter"; id: string }
  | { type: "get" };

export default function reducer(
  state: BalanceElement[],
  action: BalanceAnimationReducerActions,
): BalanceElement[] {
  if (!action) {
    return state;
  }

  switch (action.type) {
    case "clean":
      return [];

    case "replace":
      return [action.value];

    case "update":
      return [...state, action.value];

    case "filter":
      return state.filter((element) => element.id !== action.id);

    default:
      return state;
  }
}
