import React, { CSSProperties, useMemo, useRef } from "react";
import clsx from "clsx";

import { useWindowSize } from "providers";
import { emojiList } from "components/Chat/components/Write/data";
import { cdnEmojiUrl } from "pages/ModalPages/pages/Profile/utils";

import { repeat } from "utils";

import css from "./style.module.scss";

interface IProps {
  smileEnter: (value: string) => void;
  contentHeight?: number;
  contentWidth?: number;
  config?: {
    getCustomColumnAmount?: (
      windowWidth: number,
      smileWidth?: number,
    ) => number;
    smileSizes?: {
      w: number;
      h: number;
    };
  };
  defaultScroll?: boolean;
  classes?: {
    root?: string;
    grid?: string;
    item?: string;
  };
}

export interface ISmileCode {
  smile: string;
  base: string;
}
const getSmileList = (columnBreakNumber: number) => {
  const all: ISmileCode[] = [];
  let currentContainer: ISmileCode[] = [];
  let activeCategory = "people";

  emojiList.forEach(({ base, category, smile }) => {
    if (category !== "people" && category !== activeCategory) {
      all.push(...currentContainer);

      let emptyAmount =
        columnBreakNumber +
        columnBreakNumber -
        (all.length % columnBreakNumber);
      if (emptyAmount === columnBreakNumber * 2) {
        emptyAmount = columnBreakNumber;
      }

      all.push(...repeat(emptyAmount, () => ({ base: "", smile: "" })));
      currentContainer = [];
      activeCategory = category;
    }

    currentContainer.push({
      base,
      smile,
    });
  });

  all.push(...currentContainer);

  return all;
};
let smileSizesDefault = {
  w: 28,
  h: 28,
};
const gridGap = 10;
let contentHeightDefault = 135;
let contentWidthDefault = 528;

export const EmojiContent = ({
  smileEnter,
  defaultScroll = false,
  config,
  contentHeight = contentHeightDefault,
  contentWidth = contentWidthDefault,
}: IProps) => {
  const rootRef = useRef<HTMLDivElement | null>(null);
  if (config && config.smileSizes) {
    smileSizesDefault = config.smileSizes;
  }
  let styles = {
    root: {
      height: contentHeight,
    },
    item: {
      width: `${smileSizesDefault.w}px`,
      height: `${smileSizesDefault.h}px`,
    },
  };

  const { width } = useWindowSize();

  const columnAmount = useMemo(() => {
    if (config && config.getCustomColumnAmount) {
      return config.getCustomColumnAmount(width);
    }

    if (rootRef.current !== null) {
      const { width } = rootRef.current.getBoundingClientRect();
      return Math.floor((width - 24) / (smileSizesDefault.w + gridGap));
    }
    if (width > contentWidth) {
      return Math.floor((contentWidth - 24) / (smileSizesDefault.w + gridGap));
    }

    return Math.floor((width - 24) / (smileSizesDefault.w + gridGap));
  }, [width, rootRef.current]);

  const smileList = useMemo(() => getSmileList(columnAmount), []);

  return (
    <div
      className={css.root}
      style={{ ...styles.root }}
      ref={(node) => node && (rootRef.current = node)}>
      <div
        className={clsx(css.gridContainer, !defaultScroll && css.scrollBar)}
        style={
          {
            ["--gridColumnCount"]: columnAmount,
            ["--gridGap"]: `${gridGap}px`,
          } as CSSProperties
        }>
        {smileList.map((smileItem, i) => {
          const { base, smile } = smileItem;
          return (
            <div
              onClick={() => {
                smileEnter(smile);
              }}
              key={base || i}
              className={css.gridItem}
              style={{ ...styles.item }}>
              {base && <img src={`${cdnEmojiUrl}/${base}.png`} alt="" />}
            </div>
          );
        })}
      </div>
    </div>
  );
};
