import React, { FC } from "react";
import clsx from "clsx";

import { createFC, TableCaptionProps } from "components";

export const Caption: FC<TableCaptionProps> = ({
  value,
  className,
  component: Component,
}) => {
  const CaptionComponent = createFC(Component);

  return (
    <caption className={clsx(className)}>
      <CaptionComponent>{value}</CaptionComponent>
    </caption>
  );
};
