import React, { ComponentType, LazyExoticComponent, Suspense } from "react";
import LazyComponentLoader from "config/LazyComponentLoader";

const PasinoLogo = React.lazy(() =>
  LazyComponentLoader(import("assets/logos/pasino-logo")),
);

const FooterPasinoLogo = React.lazy(() =>
  LazyComponentLoader(import("assets/logos/footer-pasino-logo")),
);

type SVGProps = React.SVGProps<SVGSVGElement> & {
  title?: string | undefined;
};

type Logo = (props: SVGProps) => JSX.Element;

export type gamesLogo = "blackjack" | "crash" | "hash-dice";

export type LogoVariantKeys = "pasino" | "footerPasino" | "notFoundPasino";

export interface LogoVariantsStyle {
  top?: number;
  left?: number;
  right?: number;
  bottom?: number;
}

export type LogoType = React.FunctionComponent<SVGProps>;

export interface LogoVariant {
  logo: Logo;
  width: number;
  height: number;
}

const suspenseLazy = (
  Component: (() => JSX.Element) | LazyExoticComponent<ComponentType<any>>,
  props: SVGProps,
) => (
  <Suspense fallback={null}>
    <Component {...props} />
  </Suspense>
);

const logoVariants: Record<LogoVariantKeys, LogoVariant> = {
  pasino: {
    logo: (props) => suspenseLazy(PasinoLogo, props),
    width: 238,
    height: 81,
  },

  footerPasino: {
    logo: (props) => suspenseLazy(FooterPasinoLogo, props),
    width: 134,
    height: 47,
  },
  notFoundPasino: {
    logo: (props) => suspenseLazy(FooterPasinoLogo, props),
    width: 560,
    height: 196.4,
  },
};

export default logoVariants;
