import { EventEmitter } from "events";
import {
  Coin,
  CoinData,
  CoinEvents,
  CoinName,
  CoinPriceList,
  CoinsState,
  CoinsUpdateProps,
  ManagerListenerValues,
} from "hooks";
import { track } from "utils";

import {
  getAllCoins,
  getCoinPriceFromData,
  getCoinStateFromList,
  getUserBalance,
} from "./utils";

class CoinsManager extends EventEmitter {
  coinsDataList: CoinData[] = [];
  coinPriceState: CoinPriceList = {};

  walletCoinsList: Coin[] = [];
  walletCoinsState: CoinsState = {};
  selectedCoin?: Coin;
  showInUSD: boolean;

  constructor() {
    super();

    let selectedCoin: Coin | undefined;
    let showInUSD: boolean;
    try {
      selectedCoin = JSON.parse(localStorage.getItem("selectedCoin") || "");
      showInUSD =
        JSON.parse(localStorage.getItem("usdMode") || "false") || false;
    } catch (error: any) {
      track()?.errors(error);

      selectedCoin = undefined;
      showInUSD = false;
    }

    this.selectedCoin = selectedCoin;
    this.showInUSD = showInUSD;
  }

  init() {
    this.getActualBalanceFromServer();
  }

  listener(callBack: (manager: ManagerListenerValues) => void) {
    this.addListener(CoinEvents.UPDATE, callBack);

    return () => {
      this.removeListener(CoinEvents.UPDATE, callBack);
    };
  }

  closeListener(callBack: (manager: ManagerListenerValues) => void) {
    this.removeListener(CoinEvents.UPDATE, callBack);
  }

  async getActualBalanceFromServer() {
    const token = window.localStorage.getItem("token");

    await getAllCoins((coins) => {
      this.coinsDataList = coins;

      this.coinPriceState = getCoinPriceFromData(coins);
    });

    if (token) {
      getUserBalance((coins) => {
        this.walletCoinsList = coins;
        const coinState = getCoinStateFromList(coins);
        this.walletCoinsState = coinState;

        if (!this.selectedCoin) {
          this.selectedCoin = coinState["GEM"];
        } else {
          const { coin } = this.selectedCoin;
          this.selectedCoin = coinState[coin];

          const selectedPrice = this.coinPriceState[coin];
          this.selectedCoin.price = selectedPrice?.price;
        }
        try {
          localStorage.setItem(
            "selectedCoin",
            JSON.stringify(this.selectedCoin),
          );
        } catch (e) {
          track()?.errors(e);

          console.error(
            "Error when try save selected coin to Local Storage > ",
            e,
          );
        }
        this.emit(CoinEvents.UPDATE, this.createReturnValues());
      });
    }
  }

  changeSelectedCoin(value: CoinName) {
    if (this.walletCoinsState[value]) {
      this.selectedCoin = this.walletCoinsState[value];

      const selectedPrice = this.coinPriceState[value];
      this.selectedCoin.price = selectedPrice?.price;

      localStorage.setItem("selectedCoin", JSON.stringify(this.selectedCoin));
    }

    this.emit(CoinEvents.UPDATE, this.createReturnValues());
  }

  coinsUpdate(value: CoinsUpdateProps) {
    if (value.type === "add") {
      const { coin: newCoin } = value;
      const oldCoinIndex = this.walletCoinsList.findIndex(
        (value) => value.coin === newCoin.coin,
      );
      this.walletCoinsList[oldCoinIndex] = newCoin;
      this.walletCoinsState[newCoin.coin] = newCoin;

      if (this.selectedCoin?.coin === newCoin.coin) {
        this.selectedCoin = newCoin;
      }
    } else {
      const { coins } = value;
      this.walletCoinsList = coins;
      const coinState = getCoinStateFromList(coins);
      this.walletCoinsState = coinState;
      this.selectedCoin = coinState[this.selectedCoin?.coin || "gem"];
    }

    this.emit(CoinEvents.UPDATE, this.createReturnValues());
  }

  changeShowInUSD(value?: boolean) {
    this.showInUSD = !!value;
    this.emit(CoinEvents.UPDATE, this.createReturnValues());
  }

  coinValueUpdate(newCoinValue: string | number, coinName: CoinName) {
    const updatedCoin: Coin = {
      ...this.walletCoinsState[coinName],
      balance: Number(newCoinValue).toFixed(8),
    };

    this.coinsUpdate({
      type: "add",
      coin: updatedCoin,
    });
  }

  createReturnValues(): ManagerListenerValues {
    const {
      selectedCoin,
      walletCoinsList,
      walletCoinsState,
      coinPriceState,
      showInUSD,
    } = this;

    return {
      selectedCoin,
      walletCoinsList,
      walletCoinsState,
      coinPriceState,
      showInUSD,
    };
  }
}

export type CoinsManagerType = typeof CoinsManager;

export const coinsManager = new CoinsManager();
