import React, { FC } from "react";
import clsx from "clsx";

import SelectItemBase from "../../BaseSelect/components/SelectItemBase";
import { GetCoinImg } from "../../CoinImg";
import styles from "./style.module.scss";

export interface Option {
  value: string;
  icon?: string | JSX.Element | null;
  label?: string;
  name?: string;
  amount?: string;
  labelAdornment?: JSX.Element;
}

export interface Props extends Option {
  className?: string;
}

const Item: FC<Props> = ({ value, icon, label, className, labelAdornment }) => {
  const getIconComponent = () => {
    if (icon === null) {
      return null;
    }

    switch (typeof icon) {
      case "string":
        return (
          <img
            draggable={false}
            loading="lazy"
            className={styles.coin}
            alt={value}
            src={icon}
          />
        );

      case "object":
        return icon;

      default:
        return <GetCoinImg coin={value} className={styles.coin} />;
    }
  };

  return (
    <SelectItemBase value={value} className={clsx(styles.item, className)}>
      {getIconComponent()}
      <span>{label || value}</span>

      {!!labelAdornment && (
        <div className={styles.adornment}>{labelAdornment}</div>
      )}
    </SelectItemBase>
  );
};

export default Item;
