import React from "react";

import { Skeleton } from "components";
import "./index.css";

const FallbackPage = () => {
  return (
    <>
      <div className="FallbackPage_appBar">
        <div className="FallbackPage_logos">
          <Skeleton
            className="FallbackPage_menuButton"
            height={32}
            width={32}
          />
          <Skeleton
            className="FallbackPage_menuButton"
            height={47}
            width={134}
          />
        </div>

        <div className="FallbackPage_grow" />
        <div className="FallbackPage_block">
          <Skeleton className="FallbackPage_select" height={40} width={200} />
          <div className="FallbackPage_desktop">
            <Skeleton
              className="FallbackPage_icon"
              circle
              height={32}
              width={32}
            />
          </div>
          <div className="FallbackPage_desktop">
            <Skeleton
              className="FallbackPage_icon"
              circle
              height={32}
              width={32}
            />
          </div>
          <Skeleton
            className="FallbackPage_icon"
            circle
            height={32}
            width={32}
          />
        </div>
      </div>
      <div className="FallbackPage_wrapper">
        <div className="FallbackPage_innerWrapper">
          <div className="FallbackPage_content">
            <div className="FallbackPage_gameBlock">
              <Skeleton height="100%" width="100%" />
            </div>
            <div className="FallbackPage_rowTitle">
              <Skeleton height={20} width={200} />
            </div>
            <div className="FallbackPage_row">
              <div className="FallbackPage_banner">
                <Skeleton height="100%" width="100%" />
              </div>
              <div className="FallbackPage_banner">
                <Skeleton height="100%" width="100%" />
              </div>
              <div className="FallbackPage_banner">
                <Skeleton height="100%" width="100%" />
              </div>
            </div>
          </div>
          <div className="FallbackPage_chat">
            <div className="FallbackPage_chatMessages">
              <Skeleton height="100%" width="100%" />
            </div>
            <div className="FallbackPage_chatInput">
              <Skeleton height="100%" width="100%" />
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default FallbackPage;
