import React, { FC, forwardRef, useState } from "react";
import clsx from "clsx";

import { InputBase, BaseInputProps } from "components";
import css from "./style.module.scss";

export interface FilledInputProps extends Omit<BaseInputProps, "classes"> {
  label?: string;
  classes?: {
    root?: string;
    label?: string;
    focused?: string;
  };

  inputClasses?: BaseInputProps["classes"];
}

export const FilledInput = forwardRef<HTMLInputElement, FilledInputProps>(
  (
    {
      label,
      className,
      onFocus,
      onBlur,
      classes,
      inputClasses,
      value,
      ...props
    },
    ref,
  ) => {
    const [isFocused, setIsFocused] = useState(false);

    const handleOnFocus = (event: React.FocusEvent<HTMLInputElement>) => {
      setIsFocused(true);
      onFocus && onFocus(event);
    };

    const handleOnBlur = (event: React.FocusEvent<HTMLInputElement>) => {
      if (!event.currentTarget.value) {
        setIsFocused(false);
      }

      onBlur && onBlur(event);
    };

    return (
      <label className={clsx(css.root, className, classes?.root)}>
        {!!label && (
          <label
            className={clsx(
              css.label,
              (isFocused || value) && [css.focusedLabel, classes?.focused],
              classes?.label,
            )}>
            {label}
          </label>
        )}
        <InputBase
          ref={ref}
          onFocus={handleOnFocus}
          onBlur={handleOnBlur}
          {...props}
          classes={{
            ...inputClasses,
            root: clsx(css.inputWrapper, inputClasses?.root),
            input: clsx(css.input, inputClasses?.input),
          }}
          value={value}
        />
      </label>
    );
  },
);
