import React, {
  ButtonHTMLAttributes,
  DetailedHTMLProps,
  FunctionComponent,
  MouseEvent,
  useCallback,
  useState,
  forwardRef,
  useEffect,
} from "react";
import clsx from "clsx";

import { createTimeouts, throttle } from "utils";
import "./style.scss";

export interface ButtonProps
  extends Omit<
    DetailedHTMLProps<
      ButtonHTMLAttributes<HTMLButtonElement>,
      HTMLButtonElement
    >,
    "ref"
  > {
  transparent?: boolean;
  icon?: boolean;
  classes?: {
    root?: string;
    label?: string;
  };

  component?: (props?: any, ref?: any) => JSX.Element | null;
  waveColor?: string;
  base?: boolean;
}

interface ButtonAnimationData {
  x: number;
  y: number;
}
const { clearTimeouts, pushTimeout } = createTimeouts();
const wait = throttle();

const ButtonComponent = forwardRef<HTMLButtonElement, ButtonProps>(
  (
    {
      base,
      classes,
      children,
      className,
      waveColor,
      transparent,
      icon,
      component: Component,
      ...props
    },
    ref,
  ) => {
    const [playAnimation, setPlayAnimation] =
      useState<ButtonAnimationData | null>(null);

    const handleEndAnimation = () => {
      setPlayAnimation(null);
    };

    const setWait = useCallback(() => {
      wait(() => {
        handleEndAnimation();
        clearTimeouts();
      }, 300);
    }, []);

    const handleStartAnimation = (event: MouseEvent<HTMLButtonElement>) => {
      setWait();

      const { clientX, clientY, currentTarget } = event;

      pushTimeout(() => {
        const targetRect = currentTarget.getBoundingClientRect();
        setPlayAnimation({
          x: clientX - targetRect.left,
          y: clientY - targetRect.top,
        });
      });
    };

    const Root: FunctionComponent<any> = useCallback(
      forwardRef((props, ref) =>
        Component ? (
          <Component {...props} ref={ref} />
        ) : (
          <button {...props} ref={ref} />
        ),
      ),
      [],
    );

    useEffect(() => {
      return () => {
        clearTimeouts();
      };
    }, []);

    return (
      <Root
        {...props}
        ref={ref}
        onMouseDown={handleStartAnimation}
        onAnimationEnd={handleEndAnimation}
        className={clsx(
          className,
          classes?.root,
          "baseButton",
          transparent && "baseButton_transparent",
          icon && "baseButton_icon",
          base && "baseButton_base",
        )}>
        <span
          className={clsx(
            "baseButton__waveAnimation",
            playAnimation && "baseButton__waveAnimation_play",
          )}
          style={{
            left: playAnimation?.x,
            top: playAnimation?.y,
            background: waveColor,
          }}
        />
        {base ? (
          <>{children}</>
        ) : (
          <span className={clsx("baseButton__label", classes?.label)}>
            {children}
          </span>
        )}
      </Root>
    );
  },
);

export default ButtonComponent;
