import throttle from "lodash.throttle";
import { footerId } from "containers/Page/components/Footer";

export const chatContentId = "desktop_chat_content";
export const translateChatWhenScroll = throttle(() => {
  const chatContent = document.getElementById(chatContentId);
  const footer = document.getElementById(footerId);

  if (chatContent && footer) {
    const top = window.scrollY;
    const windowHeight = window.innerHeight;
    const footerDistance = footer.offsetTop - (top + windowHeight + 30);

    if (footerDistance < 0) {
      chatContent.style.transform = `translateY(${footerDistance}px)`;
    } else {
      chatContent.style.transform = `translateY(0px)`;
    }
  }
}, 200);
