import React from "react";
import clsx from "clsx";

import { useChat } from "providers";

import { LanguageSelector } from "components/Chat/components";

import { ReactComponent as ChevronRightIcon } from "assets/icons/chevron-right.svg";
import css from "./style.module.scss";

export const Header = () => {
  const { isHide, setIsHide } = useChat();
  const changeChat = () => setIsHide(!isHide);

  return (
    <div className={css.wrapper}>
      <div
        className={clsx(css.root, {
          [css.hide]: isHide,
        })}>
        <div className={css.arrBlock} onClick={changeChat}>
          <ChevronRightIcon className={css.arr} />
        </div>

        <div className={css.buttonsWrapper}>
          <LanguageSelector />
        </div>
      </div>
    </div>
  );
};
