import React, { FC, memo } from "react";
import css from "./style.module.scss";

interface Props {
  gif_url: string;
}

const Component: FC<Props> = ({ gif_url }) => {
  return <img src={gif_url} alt="chat gif" className={css.img} />;
};

export const Gif = memo(Component, (p, n) => p.gif_url === n.gif_url);
