import React, { useState, useImperativeHandle } from "react";
import { useScroll } from "./utils/useScroll";
import css from "./style.module.scss";
import { SliderValues, SliderProps } from "./utils/types";

const OptimizedSlider = React.forwardRef<SliderValues, SliderProps>(
  (
    {
      cardWidth = 0,
      rows = 1,
      children,
      dataLength,
      cardsAmount = null,
      currentIndex = null,
      setActualIndex = () => {},
      isGrow = true,
      onMouseOver = () => {},
      onMouseLeave = () => {},
    },
    ref,
  ) => {
    const [rootNode, setRootNode] = useState<HTMLDivElement | null>(null);
    const gridStyles = Math.ceil(dataLength / rows);
    const { increment, decrement } = useScroll(
      rootNode,
      dataLength,
      cardWidth,
      rows,
      cardsAmount,
      currentIndex,
      setActualIndex,
      isGrow,
    );
    useImperativeHandle(ref, () => ({
      increment,
      decrement,
    }));

    return (
      <div ref={setRootNode} className={css.observerWrapper}>
        <div
          className={css.list}
          onMouseOver={onMouseOver}
          onMouseLeave={onMouseLeave}
          style={{
            gridTemplateColumns: `repeat(${gridStyles}, 1fr)`,
          }}>
          {children}
        </div>
      </div>
    );
  },
);

export default OptimizedSlider;
