import React, { FC, memo, useMemo } from "react";
import clsx from "clsx";

import css from "./style.module.scss";

interface Props {
  level: number;
  className?: string;
}

const backgroundConfig: { [key: number]: string } = {
  0: "linear-gradient(150deg, #FAFAFA 54%, #C2C4CD 70%)",
  10: "linear-gradient(150deg, #c0b8b8 55%, #837d7d 70%)",
  20: "linear-gradient(150deg, #D8DCEF 55%, #BCC1D2 70%)",
  30: "linear-gradient(150deg, #F1DCC1 18%, #BD8A65 83%)",
  40: "linear-gradient(150deg, #C6D9FF 18%, #9CB7ED 83%)",
  50: "linear-gradient(150deg, #2d61a4 55%, #0e3686 70%)",
  60: "linear-gradient(150deg, #faebce 55%, #f3dba7 70%)",
};

export const Content: FC<Props> = ({ level, className }) => {
  const background = useMemo(() => {
    const key = Math.min(Math.floor(level / 10) * 10, 60);
    return backgroundConfig[key] || backgroundConfig[0];
  }, [level]);

  return (
    <div className={clsx(css.root, className)}>
      <div
        className={css.arrow}
        style={{
          background: background,
        }}>
        <p className={css.text}>{level}</p>
      </div>
    </div>
  );
};

export const Level = memo(Content, (prev, next) => prev.level === next.level);
