import { useCallback, useState } from "react";
import { useLocalStorage } from "hooks";
import { useGetLocale } from "providers";
import { isNumber, track } from "utils";

type OperationVariables = Record<string, any>;

export interface MutationResultPositive<TData extends any> {
  data: TData;
  error: undefined;
  loading: false;
  called: true;
}

export interface MutationResultNegative {
  data?: undefined;
  error?: any;
  loading: boolean;
  called: boolean;
}

export type MutationResult<TData extends any> =
  | MutationResultPositive<TData>
  | MutationResultNegative;

interface ExecutionResultPositive<TData extends any> {
  data: TData;
  error: undefined;
}

interface ExecutionResultNegative {
  data: undefined;
  error: any;
}

type ExecutionResult<TData extends any> =
  | ExecutionResultPositive<TData>
  | ExecutionResultNegative;

interface QueryOptions<TVariables extends OperationVariables> {
  headers?: HeadersInit;
  variables?: TVariables | {};
  withoutLoading?: boolean;
}

type UseFetchType<TData extends any, TVariables = any> = [
  (options?: QueryOptions<TVariables>) => Promise<ExecutionResult<TData>>,
  MutationResult<TData>,
];

export let isTokenRefreshing = false;

export const useFetch = <TData extends any, TVariables = any>(
  queryString: string,
  options: QueryOptions<TVariables> = {},
): UseFetchType<TData, TVariables> => {
  const { headers: initHeaders = {}, variables: initVariables = {} } = options;
  const intl = useGetLocale();
  const [, setToken] = useLocalStorage("token");
  const [, setExpirationTime] = useLocalStorage("expirationTime");

  const [, setTfa] = useLocalStorage("2fa");

  const [loading, setLoading] = useState(false);
  const [called, setCalled] = useState(false);
  const [error, setError] = useState<any>();
  const [data, setData] = useState<TData>();

  const fetchNewToken = async () => {
    const { default: refreshToken } = await import("./fetchToken");
    return await refreshToken({
      queryHeaders: {
        "Content-Type": "application/x-www-form-urlencoded",
      },
      queryBody: {
        language: intl.locale,
      },
      setToken,
      setExpirationTime,
      isTokenRefreshing,
      setIsTokenRefreshing: (value) => {
        isTokenRefreshing = value;
      },
    });
  };

  const query = useCallback<
    (options?: QueryOptions<TVariables>) => Promise<ExecutionResult<TData>>
  >(
    (options = {}) => {
      const { headers = {}, variables = {}, withoutLoading } = options;

      if (!withoutLoading) {
        setCalled(true);
      }

      const fetchData = async () => {
        if (!withoutLoading) {
          setLoading(true);
        }

        let data;
        let currentError;

        const expirationTime = window.localStorage.getItem("expirationTime");
        const expirationTimeNumber = expirationTime
          ? Number(expirationTime)
          : NaN;

        const needToWait =
          isNumber(expirationTimeNumber) && Date.now() >= expirationTimeNumber;

        try {
          let fetchedToken: string | undefined;

          if (
            window.localStorage.getItem("token") &&
            needToWait &&
            !isTokenRefreshing
          ) {
            isTokenRefreshing = true;
            fetchedToken = await fetchNewToken();
          }

          const { memoizedIsTokenRefreshing } = await import("./fetchToken");

          await memoizedIsTokenRefreshing(isTokenRefreshing);

          let token = fetchedToken
            ? fetchedToken
            : window.localStorage.getItem("token");

          if (token && needToWait && !isTokenRefreshing) {
            isTokenRefreshing = true;
            token = await fetchNewToken();
          }

          const queryHeaders = {
            "Content-Type": "application/x-www-form-urlencoded",
            ...initHeaders,
            ...headers,
          };

          const queryBody = JSON.stringify({
            ...initVariables,
            language: intl.locale,
            ...variables,
            token,
          });
          const response = await fetch(
            `${process.env.REACT_APP_URL}/${queryString}`,
            {
              method: "POST",
              headers: queryHeaders,
              body: queryBody,
            },
          );

          data = await response.json();
        } catch (err) {
          currentError = err;
          track()?.errors(err);
        }

        if (!withoutLoading) {
          const newToken = data?.new_token || data?.login_token;

          if (newToken) {
            setToken(newToken);

            const oneHour = 60 * 60 * 1000;
            setExpirationTime(`${Date.now() + oneHour}`);
          }

          if (data && data.success) {
            setData(data);
          } else if (data?.bad_token) {
            setToken("");
            setExpirationTime("");
          } else if (data && data["2fa_required"]) {
            setTfa("true");
          }
        }

        if (!data?.success) {
          currentError =
            data?.message || "Oops, unrecognized error has occurred.";
        }

        if (!withoutLoading) {
          setLoading(false);
          setError(currentError);
        }

        return {
          data,
          error: currentError,
        };
      };

      return fetchData();
    },
    [queryString],
  );

  let result: MutationResult<TData>;

  if (!called || loading || error) {
    result = {
      data: undefined,
      error,
      loading,
      called,
    };
  } else {
    result = {
      data: data as TData,
      error,
      loading,
      called,
    };
  }
  return [query, result];
};
