import { useDetectRobots } from "hooks";
import React, { FC, useEffect, useState } from "react";
import { detectRobots } from "utils";
import { Context } from "./Context";

const ChatProvider: FC = ({ children }) => {
  const [isHide, setIsHide] = useState(detectRobots());

  return (
    <Context.Provider
      value={{
        isHide,
        setIsHide,
      }}>
      {children}
    </Context.Provider>
  );
};

export default ChatProvider;
