import React, { FC } from "react";
import clsx from "clsx";

import { PopUpAnchorMenu, PopUpMenuProps } from "containers/PopUps";

import css from "./style.module.scss";

export interface MenuProps extends PopUpMenuProps {
  root?: React.DetailedHTMLProps<
    React.HTMLAttributes<HTMLDivElement>,
    HTMLDivElement
  >;
  isTriangle?: boolean;
  classes?: {
    triangle: string;
  };
  className?: string;
}

export const Menu: FC<MenuProps> = ({
  children,
  className,
  root,
  classes,
  isTriangle = false,
  ...props
}) => {
  return (
    <PopUpAnchorMenu {...props}>
      {isTriangle && (
        <div className={clsx(css.triangleDiv, classes?.triangle)}></div>
      )}
      <div {...root} className={clsx(css.root, root?.className, className)}>
        {children}
      </div>
    </PopUpAnchorMenu>
  );
};
