import React, { DetailedHTMLProps, InputHTMLAttributes } from "react";
import clsx from "clsx";

import styles from "./style.module.scss";

export interface InputClasses {
  root: string;
  input: string;
  disabled: string;
  focused: string;
}

type DefaultInputProps = DetailedHTMLProps<
  InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement
>;

export interface BaseInputProps
  extends Omit<DefaultInputProps, "ref" | "value"> {
  classes?: Partial<InputClasses>;
  endAdornment?: JSX.Element | null;
  startAdornment?: JSX.Element | null;
  error?: boolean;
  value?: DefaultInputProps["value"] | null;
}

const Input = React.forwardRef<HTMLInputElement, BaseInputProps>(
  (
    {
      className,
      value,
      onChange,
      classes,
      endAdornment,
      startAdornment,
      onBlur,
      disabled,
      type,
      error,
      ...props
    },
    ref,
  ) => {
    return (
      <div
        className={clsx(
          styles.root,
          classes?.root,
          className,
          error && styles.error,
        )}>
        {startAdornment}
        <input
          {...props}
          className={clsx(styles.input, classes?.input)}
          ref={ref}
          value={value as string}
          onChange={onChange}
          onBlur={onBlur}
          disabled={disabled}
          type={type}
        />
        {endAdornment}
      </div>
    );
  },
);

export default Input;
