import React, { FC, cloneElement, useCallback, useState } from "react";
import { LocationDescriptor, History } from "history";
import { Link, LinkProps } from "react-router-dom";
import clsx from "clsx";

import logoVariants, { LogoVariantKeys } from "components/Logo/config";
import css from "./style.module.scss";
import { useDocumentLoaded } from "hooks";

export interface LogoProps extends Omit<LinkProps, "to"> {
  skeleton?: boolean;
  skeletonClassName?: string;
  onLoad?: () => void;
  logoVariant: LogoVariantKeys;
  to?: LocationDescriptor<History.LocationState>;
}

const LogoComponent = ({
  logoVariant,
  skeletonClassName,
  skeleton,
  onLoad,
  to,
  className,
  style,
  ...rest
}: LogoProps) => {
  const { logo: Component, width, height } = logoVariants[logoVariant];

  const styles = {
    ...style,
    width,
    height,
  };

  const Wrapper: FC<any> = useCallback(
    (props) => {
      if (to) {
        return <Link {...props} className={clsx(css.link)} to={to} />;
      } else {
        const { children, ...rest } = props;
        return <>{cloneElement(children, rest)}</>;
      }
    },
    [to],
  );
  return (
    <Wrapper {...rest}>
      <Component
        className={clsx(css.dimensions, className)}
        style={styles}
        onLoad={onLoad}
      />
    </Wrapper>
  );
};

export default LogoComponent;
