import { useEffect, useMemo, useRef } from "react";
import { createIntervals } from "utils";
import { ScrollFunctions } from "./types";

/**
  callBack: autoScroll function

  time: delay

  dependencies?: dependencies for usememo
 */
export const useAutoScroll = (
  callBack: () => void,
  time: number,
  dependencies: any[] = [],
): ScrollFunctions => {
  const status = useRef({
    isStarted: false,
  });

  const { clearIntervals, pushInterval } = useMemo(createIntervals, []);

  const start = () => {
    status.current.isStarted = true;
    pushInterval(() => {
      callBack();
    }, time);
  };
  const stop = () => {
    status.current.isStarted = false;
    clearIntervals();
  };

  useEffect(() => {
    start();

    return () => {
      stop();
      clearIntervals();
    };
  }, [...dependencies]);

  return { start, stop, status: status.current };
};
