import React, { DetailedHTMLProps, FC } from "react";
import clsx from "clsx";

import { medalUrlPNG, opacityMedalUrlPNG } from "assets/levels";
import { repeat } from "utils";

import css from "./style.module.scss";

interface Props {
  value?: number;
  size?: "small" | "medium";
}

interface MedalProps {
  value: number;
  size?: "small" | "medium";
  opacity?: "50%" | "100%";
  props?: DetailedHTMLProps<
    React.ImgHTMLAttributes<HTMLImageElement>,
    HTMLImageElement
  >;
}

const Medal: FC<MedalProps> = ({ value, opacity, size, ...props }) => {
  const medalKeyName = Math.ceil(value / 10) * 10 || 10;
  const key = Math.min(medalKeyName, 70);

  if (!opacity) {
    const index = size === "small" ? 0 : 1;

    return (
      <img
        onClick={() => {
          console.log(value, key);
        }}
        src={medalUrlPNG[`${key}`][index]}
        alt={String(value)}
        {...props}
      />
    );
  } else {
    const unitSize = size === "small" ? 16 : 32,
      unitOpacity = opacity === "50%" ? "half" : "empty";

    return (
      <img
        onClick={() => {
          console.log(value, key);
        }}
        src={opacityMedalUrlPNG[`${key}`][unitSize][unitOpacity]}
        alt={String(value)}
        {...props}
      />
    );
  }
};

const MemoMedal = React.memo(Medal, (p, n) => p.value === n.value);

export const Rating: FC<Props> = ({ value = 70, size = "small" }) => {
  if (!value) {
    value = 0;
  }

  const starAmount =
    !!value && value !== 0 ? (value % 10 === 0 ? 10 : value % 10) : 0;

  return (
    <div className={css.root}>
      {repeat(5, (i) => {
        const iteratedRating = i + 1;
        if (iteratedRating <= starAmount / 2) {
          return (
            <span
              key={`_${i}`}
              className={clsx(css.star, {
                [css.smallStar]: size === "small",
                [css.mediumStar]: size === "medium",
              })}>
              <MemoMedal
                value={value}
                size={size}
                {...{
                  className: clsx(css.img),
                }}
              />
            </span>
          );
        } else {
          if (
            iteratedRating - 1 < starAmount / 2 &&
            iteratedRating + 1 > starAmount / 2
          ) {
            return (
              <span
                key={`_${i}`}
                className={clsx(css.star, {
                  [css.smallStar]: size === "small",
                  [css.mediumStar]: size === "medium",
                })}>
                <MemoMedal
                  value={value}
                  size={size}
                  opacity={"50%"}
                  {...{
                    className: clsx(css.img),
                  }}
                />
              </span>
            );
          }
          return (
            <span
              key={`_${i}`}
              className={clsx(css.star, {
                [css.smallStar]: size === "small",
                [css.mediumStar]: size === "medium",
              })}>
              <MemoMedal
                value={value}
                size={size}
                opacity={"100%"}
                {...{
                  className: clsx(css.img),
                }}
              />
            </span>
          );
        }
      })}
    </div>
  );
};

export default React.memo(Rating, (prev, next) => prev.value === next.value);
