import React, { FC } from "react";
import clsx from "clsx";

import { createFC, TableTBodyProps } from "components";
import css from "./style.module.scss";

export const TBody: FC<TableTBodyProps> = ({ rows, components, classes }) => {
  const Cell = createFC(components?.cell);
  const Row = createFC(components?.row);
  const Body = createFC(components?.body);

  return (
    <tbody className={clsx(css.body, classes?.tbody)}>
      <Body>
        {rows?.map(({ values, callBack, key }) => {
          return (
            <tr
              className={clsx(css.tr, callBack && css.pointer, classes?.tr)}
              key={key}
              onClick={callBack}>
              <Row>
                {values.map((value, i) => {
                  return (
                    <td
                      className={clsx(css.td, classes?.td)}
                      key={`${key}__${i}`}>
                      <Cell>{value}</Cell>
                    </td>
                  );
                })}
              </Row>
            </tr>
          );
        })}
      </Body>
    </tbody>
  );
};
