import React, { CSSProperties } from "react";
import clsx from "clsx";

import { Button, ButtonProps } from "components";

import iconVariants, { IconVariantKeys } from "./config";
import { spacing } from "sass/styleFunctions";
import css from "./style.module.scss";

export let iconHeight = parseInt(css.iconHeight);

export interface IconProps extends ButtonProps {
  variant: IconVariantKeys;
  fontSize?: number;
  withoutButton?: boolean;
}

const IconComponent = ({
  variant,
  className,
  fontSize = 24,
  withoutButton,
  ...rest
}: IconProps) => {
  const { icon: IconElement, viewBox, style = {} } = iconVariants[variant];

  const wrapperStyles: CSSProperties = {
    paddingTop: spacing(1.5) + (style.top ?? 0) - (style.bottom ?? 0),
    paddingBottom: spacing(1.5) + (style.bottom ?? 0) - (style.top ?? 0),
    paddingLeft: spacing(1.5) + (style.left ?? 0) - (style.right ?? 0),
    paddingRight: spacing(1.5) + (style.right ?? 0) - (style.left ?? 0),
  };
  const sizeStyle = {
    fontSize,
  };

  if (withoutButton) {
    return (
      <>
        <IconElement
          className={clsx(css.icon, className)}
          viewBox={viewBox}
          style={sizeStyle}
          {...(rest as any)}
        />
      </>
    );
  }

  return (
    <Button icon className={className} style={{ ...wrapperStyles }} {...rest}>
      <IconElement className={css.icon} viewBox={viewBox} style={sizeStyle} />
    </Button>
  );
};

export default IconComponent;
