import React, { DetailedHTMLProps, FC } from "react";
import clsx from "clsx";

import styles from "./style.module.scss";
export interface CircularProgressProps
  extends DetailedHTMLProps<
    React.HTMLAttributes<HTMLDivElement>,
    HTMLDivElement
  > {
  size?: number | string;
  color?: string;
  circularWidth?: number;
}

export const CircularProgress: FC<CircularProgressProps> = ({
  size,
  color,
  circularWidth,
  className,
  style,
}) => {
  const loaderColor = color || "white";
  const loaderWidth = circularWidth || 4;

  const components = [] as Array<JSX.Element>;

  for (let i = 0; i < 5; i++) {
    components.push(
      <div
        key={`progress_${i}`}
        style={{
          border: `${loaderWidth}px solid ${loaderColor}`,
          borderColor: `${loaderColor} transparent transparent transparent`,
        }}
      />,
    );
  }

  const sizeFix = (node: HTMLDivElement | null) => {
    if (node) {
      const { height, width } = node.getBoundingClientRect();
      let fixedSize = size || Math.min(height, width);

      if (!fixedSize) fixedSize = 40;

      node.style.width = `${fixedSize}px`;
      node.style.height = `${fixedSize}px`;
    }
  };

  return (
    <div
      style={{
        ...style,
      }}
      className={clsx(styles.ldsRing, className)}
      ref={sizeFix}>
      {components}
    </div>
  );
};

export default CircularProgress;
