import React, { FunctionComponent } from "react";
import clsx from "clsx";

import { useWindowSize } from "providers";

import { PopUpPaperProps } from "containers/PopUps/types";
import { Dialog, OnClosePopUpCallBack, DialogProps } from "containers/PopUps";

import css from "./style.module.scss";
import { useModalLocation } from "hooks";

const defaultWidth = 500;

export interface ModalProps extends DialogProps {
  className?: string;
  open: boolean;
  width?: number | string;
  paper?: PopUpPaperProps;
}

const Modal: FunctionComponent<ModalProps> = ({
  children,
  className,
  width,
  open,
  onClose,
  paper,
  ...props
}) => {
  const { close } = useModalLocation();
  const { width: screenWidth } = useWindowSize();
  const isSm = screenWidth < parseInt(css.mobileScreen);

  const handleClose: OnClosePopUpCallBack = (...args) => {
    if (onClose) {
      return onClose(...args);
    }

    close();
  };

  return (
    <Dialog
      {...props}
      isOpen={open}
      paper={{
        className: clsx(css.paper, paper?.className, className),
        style: {
          width: isSm ? "100%" : width || defaultWidth,
          minWidth: isSm ? "100%" : width || defaultWidth,
          ...paper?.style,
        },
      }}
      onClose={handleClose}
      fullScreen={isSm}>
      {children}
    </Dialog>
  );
};

export default Modal;
