import React, {
  DetailedHTMLProps,
  FC,
  ImgHTMLAttributes,
  useMemo,
} from "react";

interface Props
  extends Omit<
    DetailedHTMLProps<ImgHTMLAttributes<HTMLImageElement>, HTMLImageElement>,
    "srcSet"
  > {
  srcSet: {
    src: string;
    size: number;
  }[];
}

export const ImgSet: FC<Props> = ({ srcSet, src, alt, ...props }) => {
  const set = useMemo(
    () =>
      srcSet
        .sort((first, second) => second.size - first.size)
        .reduce(
          (acm, img) => {
            if (!src) {
              src = img.src;
            }

            if (!acm.sizes) {
              acm.sizes = `(max-width: ${img.size}px) 100vw, ${img.size}px`;
            }

            acm.srcSet += `${img.src} ${img.size}w, `;
            return acm;
          },
          {
            srcSet: "",
            sizes: "",
          },
        ),
    [srcSet],
  );

  return (
    <img
      {...props}
      src={src}
      alt={alt || "imgSet"}
      srcSet={set.srcSet}
      sizes={set.sizes}
    />
  );
};
