import { useEffect } from "react";
import { track } from "utils";
import { useLocation } from "react-router-dom";
import { useLocalStorage } from "hooks";

const checkedSource = (source: string) => {
  if (source.includes(`${process.env.REACT_APP_FRONT_URL}/blog`)) return source;
  if (source.includes(process.env.REACT_APP_FRONT_URL as string)) return "";
  return source;
};

export const useUtmSource = () => {
  const [token] = useLocalStorage("token");
  const location = useLocation();
  useEffect(() => {
    if (!token) {
      try {
        const source =
          new URLSearchParams(location.search).get("utm_source") ??
          document.referrer;
        localStorage.setItem(
          "utm_source",
          checkedSource(source).slice(0, 255) ?? "",
        );
      } catch (error) {
        track()?.errors(error, {
          custom: {
            locationSearch: location.search,
            documentReferrer: document.referrer,
          },
        });
      }
    }
    /*eslint-disable-next-line */
  }, []);
};
