import { emojiList } from "components/Chat/components/Write/data";

export const getColumnAmount = (smileWidth: number, windowWidth: number) => {
  if (windowWidth > 578) {
    return 13;
  }

  return Math.floor((windowWidth - 40) / smileWidth);
};

export const cdnEmojiUrl = "https://cdn.pasino.com/emoji";

export const addSmile = (currentSmile: string) => {
  let src = "";
  let statusCode = "";
  emojiList.forEach((emoji) => {
    const { smile, base } = emoji;
    if (smile === currentSmile) {
      statusCode = base;
      src = `${cdnEmojiUrl}/${base}.png`;
    }
  });
  return { src, status: statusCode };
};
