import React, { FC, ChangeEvent, CSSProperties } from "react";
import clsx from "clsx";

import styles from "./style.module.scss";

interface SwitchClasses {
  root: string;
  track: string;
  toggle: string;
  toggleEnabled: string;
}

export interface SwitchProps {
  disabled?: boolean;
  onChange?: (event: ChangeEvent<HTMLInputElement>) => void;
  checked?: boolean;
  classes?: Partial<SwitchClasses>;
  className?: string;
  style?: Record<string, string>;
  togglePosition?: string;
}

const Switch: FC<SwitchProps> = ({
  disabled,
  onChange,
  checked,
  classes,
  className,
  style: inlineStyles,
  togglePosition,
}) => {
  let toggleStyle: CSSProperties | undefined;
  if (togglePosition) {
    toggleStyle = { "--switch-position": togglePosition } as CSSProperties;
  }

  return (
    <div
      className={clsx(styles.switch, classes?.root, className)}
      style={inlineStyles}>
      <div className={clsx(styles.track, classes?.track)} />
      <input
        checked={checked}
        type="checkbox"
        className={styles.checkbox}
        onChange={onChange}
        disabled={disabled}
      />
      <span
        style={toggleStyle}
        className={clsx(styles.toggle, classes?.toggle, {
          [clsx(styles.toggleOn, classes?.toggleEnabled)]: checked,
        })}
      />
    </div>
  );
};

export default Switch;
