import React, { ReactElement, useEffect, useMemo, useRef } from "react";

import ReactDOM from "react-dom";

import { useWindowSize } from "providers";
import { EmojiContent } from "./EmojiContent";
import { useClickOutside } from "hooks/useClickOutside";

import css from "./style.module.scss";
interface IProps {
  smileEnter: (value: string) => void;
  isOpen: boolean;
  onClose: () => void;
  releteElement: ReactElement<any, any>;
  contentProps?: {
    contentWidth: number;
    correctPositionXValue?: number;
    correctPositionYValue?: number;
    contentHeight?: number;
    position?: "end" | "center" | "start" | string;
  };
  triangle?: boolean;

  config?: {
    getCustomColumnAmount?: (
      windowWidth: number,
      smileWidth?: number,
    ) => number;
    smileSizes?: {
      w: number;
      h: number;
    };
  };
  defaultScroll?: boolean;
  classes?: {
    root?: string;
    grid?: string;
    item?: string;
  };
}

export interface ISmileCode {
  smile: string;
  base: string;
}

const appRoot = document.getElementById("pop-up")!;
let triangleSizesDefault = {
  w: 15,
  h: 9,
};
let smileSizesDefault = {
  w: 28,
  h: 28,
};
let contentPropsDefault = {
  contentWidth: 528,
  correctPositionXValue: 0,
  correctPositionYValue: 0,
  contentHeight: 135,
  position: "end",
};
const defaultMargin = 10;
export const EmojiWindow = ({
  isOpen,
  onClose,
  releteElement,
  contentProps = contentPropsDefault,
  triangle = false,

  config,
  ...props
}: IProps) => {
  const releteElementRef = useRef<HTMLDivElement | null>(null);
  const modalRef = useRef<HTMLDivElement | null>(null);

  const { width, height } = useWindowSize();
  if (config && config.smileSizes) {
    smileSizesDefault = config.smileSizes;
  }
  if (contentProps) {
    contentPropsDefault = { ...contentPropsDefault, ...contentProps };
  }
  const {
    contentHeight,
    contentWidth,
    correctPositionXValue,
    correctPositionYValue,
    position: positionName,
  } = contentPropsDefault;

  const position = useMemo(() => {
    if (releteElementRef?.current && isOpen) {
      const {
        top: releteElementTop,
        left,
        height: releteElementHeight,
        width: releteElementWidth,
      } = releteElementRef.current.getBoundingClientRect();

      const isNeedContentWidth = width >= contentWidth + 20;
      let currentContentWidth = isNeedContentWidth ? contentWidth : width;
      const getLeftPosition = (align: string) => {
        if (!isNeedContentWidth) {
          return defaultMargin / 2;
        }
        let position = left + (correctPositionXValue || 0);
        if (align === "center") {
          position += -currentContentWidth / 2;
        }
        if (align === "end") {
          position +=
            -currentContentWidth + releteElementWidth + releteElementWidth / 2;
        }
        if (align === "start") {
          position -= releteElementWidth / 2;
        }
        if (position + currentContentWidth > width) {
          position = width - currentContentWidth - defaultMargin * 2;
        }

        return position <= 0 ? 0 : position;
      };

      let topValue =
        releteElementTop +
        releteElementHeight +
        (correctPositionYValue || 0) +
        triangleSizesDefault.h +
        5;

      const isEnoughSpaceBottom = topValue + contentHeight < height;
      if (!isEnoughSpaceBottom) {
        topValue -= contentHeight;
      }
      let top = `${topValue}px`;

      return {
        root: {
          width: isNeedContentWidth
            ? contentWidth
            : `calc(100% - ${defaultMargin}px)`,
          top,
          left: `${getLeftPosition(positionName || "end")}px`,
          height: `${contentHeight || "100%"}px`,
        },
        triangle: {
          top,
          left: left + releteElementWidth / 2,
        },
      };
    }
  }, [width, isOpen]);
  const callback = () => {
    setTimeout(() => {
      onClose();
    }, 0);
  };
  useEffect(() => {
    const eventHandler = (event: WheelEvent) => {
      if (
        modalRef.current &&
        !modalRef.current?.contains(event.target as Node)
      ) {
        onClose();
      }
    };
    window.addEventListener("wheel", eventHandler);
    return () => {
      window.removeEventListener("wheel", eventHandler);
    };
  }, []);

  useClickOutside(releteElementRef, callback);

  return (
    <>
      <div
        ref={(node) => {
          releteElementRef.current = node;
        }}>
        {releteElement}
      </div>

      {isOpen
        ? ReactDOM.createPortal(
            <>
              {triangle && (
                <div
                  className={css.triangle}
                  style={
                    {
                      ["--triangleWidth"]: triangleSizesDefault.w,
                      ["--triangleHeight"]: triangleSizesDefault.h,
                      ...position?.triangle,
                    } as any
                  }></div>
              )}
              <div
                ref={modalRef}
                className={css.root}
                style={{ ...position?.root }}>
                <EmojiContent {...props} contentWidth={contentWidth} />
              </div>
            </>,
            appRoot,
          )
        : null}
    </>
  );
};
