import { isDivElement } from "utils";

export const getStringFromDivChildren = (
  div: HTMLDivElement | EventTarget | null,
  isCleanAfter?: boolean,
) => {
  if (!isDivElement(div) || !div) {
    return {
      cursorPosition: 0,
      string: "",
    };
  }

  let resultString = "";
  let cursorPosition = 0;
  const cursorSelection = window.getSelection();
  let isCursorFound = false;
  div.childNodes.forEach((node, i) => {
    switch (node.nodeName) {
      case "#text":
        resultString += node.textContent;

        if (!isCursorFound) {
          if (cursorSelection?.anchorNode === node) {
            cursorPosition += cursorSelection.anchorOffset;
            isCursorFound = true;
          } else {
            //@ts-ignore
            cursorPosition += node.length || 0;
          }
        }
        break;
      case "IMG":
        //@ts-ignore
        const smileEmoji: string = node.alt;
        resultString += smileEmoji || "";

        if (!isCursorFound) {
          if (cursorSelection?.anchorNode === div) {
            if (cursorSelection.anchorOffset > 0) {
              if (i + 1 === cursorSelection.anchorOffset) {
                isCursorFound = true;
              }
              cursorPosition += smileEmoji.length;
            } else {
              isCursorFound = true;
            }
          } else {
            cursorPosition += smileEmoji.length;
          }
        }

        break;

      default:
        break;
    }
  });

  if (isCleanAfter) {
    div.innerHTML = "";
  }

  return {
    cursorPosition,
    string: resultString.replace(/\s+/g, " "),
  };
};
