import React, { FC, forwardRef, useEffect, useRef, useState } from "react";
import clsx from "clsx";

import { useEventListener } from "hooks";
import { createTimeouts, rangeBetween } from "utils";

import { Tooltip } from "containers/PopUps";

import { PopperProps } from "./types";
import { Triangle } from "./components";
import triangleCss from "./components/Triangle/style.module.scss";
import css from "./style.module.scss";

const arrowSize = parseInt(triangleCss.arrowSize) || 0;

const { clearTimeouts, pushTimeout } = createTimeouts();

export const Popper = forwardRef<HTMLDivElement, Omit<PopperProps, "ref">>(
  (
    {
      children,
      anchor,
      isOpen,
      position = "top",
      margin = 0,
      backgroundColor = "white",
      withTriangle,
      style,
      fixed,
      recalculate /*less productive, but required for a dynamic change of position*/,
      withShowAnimation,
      scrollOverWindow,
      className,
      classes,
      ...props
    },
    outcomeRef,
  ) => {
    const popperRef = useRef<HTMLDivElement | null>();
    const isCalculated = useRef(false);
    const [isVisible, setIsVisible] = useState(false);

    const recalculatePopperPosition = (
      ref?: HTMLDivElement | null,
      event?: "resize" | "scroll",
    ) => {
      if (isCalculated.current && !event && event !== "resize") {
        return;
      }

      popperRef.current = ref;
      if (ref) {
        if (typeof outcomeRef === "function") {
          outcomeRef(ref);
        } else if (outcomeRef) {
          outcomeRef.current = ref;
        } else {
          outcomeRef = {
            current: ref,
          };
        }
      }

      if (ref && anchor) {
        const anchorRect = anchor.getBoundingClientRect();
        const refRect = ref.getBoundingClientRect();

        const widthDifference = refRect.width - anchorRect.width,
          heightDifference = refRect.height - anchorRect.height,
          triangleSize = withTriangle ? arrowSize : 0,
          {
            offsetWidth: documentWidth,
            offsetHeight: documentHeight,
            scrollWidth,
            scrollHeight,
          } = document.body;

        let newXPosition = 0,
          { scrollX, scrollY } = window,
          newYPosition = 0;

        if (fixed) {
          scrollX = 0;
          scrollY = 0;
        }

        switch (position) {
          default:
          case "top":
            newXPosition = anchorRect.x - widthDifference / 2 + scrollX;
            newYPosition =
              anchorRect.y - refRect.height - margin - triangleSize + scrollY;
            break;
          case "bottom":
            newXPosition = anchorRect.x - widthDifference / 2 + scrollX;
            newYPosition =
              anchorRect.y +
              anchorRect.height +
              margin +
              triangleSize +
              scrollY;
            break;
          case "left":
            newXPosition =
              anchorRect.x - refRect.width - margin - triangleSize + scrollX;
            newYPosition = anchorRect.y - heightDifference / 2 + scrollY;

            break;
          case "right":
            newXPosition =
              anchorRect.x + anchorRect.width + margin + triangleSize + scrollX;
            newYPosition = anchorRect.y - heightDifference / 2 + scrollY;
            break;
        }

        const maxX = (fixed ? documentWidth : scrollWidth) - refRect.width,
          maxY = (fixed ? documentHeight : scrollHeight) - refRect.height;

        newXPosition = scrollOverWindow
          ? newXPosition
          : rangeBetween(newXPosition, 0, Math.max(maxX, 0));
        newYPosition = scrollOverWindow
          ? newYPosition
          : rangeBetween(newYPosition, 0, Math.max(maxY, 0));

        ref.style.transform = `translate(${newXPosition}px, ${newYPosition}px)`;

        if (!recalculate) {
          isCalculated.current = true;
        }
      }
    };

    useEventListener("resize", () => {
      setTimeout(() => {
        recalculatePopperPosition(popperRef.current, "resize");
      }, 1000);
    });

    useEventListener("scroll", () =>
      recalculatePopperPosition(popperRef.current),
    );

    useEffect(() => {
      if (isOpen && !isVisible) {
        setIsVisible(true);

        setTimeout(() => {
          recalculatePopperPosition(popperRef.current, "resize");
        }, 100);
      } else if (!isOpen && isVisible) {
        pushTimeout(() => {
          setIsVisible(false);
        }, 400);
      }
      return () => {
        clearTimeouts();
      };
    }, [isOpen]);

    if (!isVisible && !isOpen) {
      isCalculated.current = false;
      return null;
    }

    return (
      <Tooltip
        ref={recalculatePopperPosition}
        {...props}
        style={{
          ...style,
          position: fixed ? "fixed" : undefined,
          backgroundColor,
          borderColor: backgroundColor,
        }}
        className={clsx(
          className,
          css.root,
          isVisible && isOpen && css.visible,
        )}
        classes={{
          childWrapper: clsx(
            css.wrapper,
            classes?.childWrapper,
            isVisible && isOpen
              ? clsx(css.visibleWrapper, classes?.childWrapperOptions?.open)
              : classes?.childWrapperOptions?.close,
          ),
        }}>
        <>
          {withTriangle && (
            <Triangle
              position={position}
              color={backgroundColor}
              className={classes?.triangle}
            />
          )}
          {children}
        </>
      </Tooltip>
    );
  },
);
